import axios from 'axios/dist/axios';
import qs from 'qs';
let instance = null;
// * 上传人脸视频的时，将请求超时的时间设置为一分半
axios.defaults.timeout = 90000; // 1min30s
axios.interceptors.request.use(
    function(config) {
        config.url = instance.configApi + config.url;
        if (!config.headers['cb']) {
            config.url = config.url + (config.url.indexOf('?') > 0 ? '&' : '?') + `cb=${new Date().getTime()}`;
        }
        Object.keys(window.qysQuery).forEach(key => {
            config.url = config.url + (config.url.indexOf('?') > 0 ? '&' : '?') + `${key}=${window.qysQuery[key]}`;
        });
        config.headers['X-Requested-With'] = 'XMLHttpRequest';

        return config;
    },
    err => {
        return Promise.reject(err);
    }
);
axios.interceptors.response.use(
    function(response) {
        if (response.status === 200) {
            if (response.$get('config', 'headers', 'noMes') != 'true') {
                if ([2002, 401, 402, 403].includes(response.data.code)) {
                    instance.dealStep('INVALID');
                }
                if (response.data.code !== 0) {
                    instance.$message(response.data.message);
                    throw new Error(response.data.message);
                }
            }
        }
        return response;
    },
    function(error) {
        return Promise.reject(error);
    }
);
const install = function(Vue, opts = {}) {
    Vue.prototype.get = async function(url, data = null, config = {}) {
        let requestData = JSON.parse(data.requestData);
        requestData = Object.assign({}, requestData, {
            version: VERSION,
        });
        data.requestData = JSON.stringify(requestData);
        instance = this;
        let res = await axios({
            method: 'GET',
            url,
            params: data,
            headers: Object.assign(config.headers || {}),
        });
        return res.data;
    };
    Vue.prototype.postForm = async function(url, data, config = {}) {
        instance = this;
        return post.bind(this)(url, data, config, 'form');
    };
    Vue.prototype.postMulti = async function(url, data, config = {}, cancelToken) {
        instance = this;
        return post.bind(this)(url, data, config, 'multi', cancelToken);
    };
    Vue.prototype.postJson = async function(url, data, config = {}) {
        instance = this;
        return post.bind(this)(url, data, config, 'json');
    };
};

async function post(url, data, config = {}, type, cancelToken) {
    let requestData = JSON.parse(data.requestData);
    requestData = Object.assign({}, requestData, {
        version: VERSION,
    });
    data.requestData = JSON.stringify(requestData);
    let contentType, body;

    switch (type) {
        case 'form':
            contentType = 'application/x-www-form-urlencoded';
            body = qs.stringify(data);
            break;
        case 'multi':
            contentType = 'multipart/form-data';
            body = new FormData();
            Object.keys(data).map(key => {
                if (Array.isArray(data[key])) {
                    data[key].map(value => {
                        body.append(key, value);
                    });
                } else {
                    body.append(key, data[key]);
                }
            });
            break;
        case 'json':
            contentType = 'application/json';
            body = data;
            break;
    }
    let res = await axios({
        method: 'POST',
        url,
        headers: Object.assign(config.headers || {}, {
            'Content-Type': contentType,
        }),
        data: body,
        cancelToken: cancelToken || null,
    });
    return res.data;
}
export default {
    install,
};
