<template>
    <div class="auth-ways" :class="{ 'is-transparent': isModalDisplay }">
        <template v-if="!noWays">
            <template v-if="!isModalDisplay">
                <div class="header">
                    <q-icon class="safetycertificate-icon" icon="safetycertificate"></q-icon
                    >所有信息仅用于身份验证，我们不会以任何形式收集
                </div>
                <div class="tip-large ways-title">选择认证方式</div>
            </template>
            <div v-if="isModalDisplay" class="mask" @click="close"></div>
            <div class="content" :class="{ 'modal-content': isModalDisplay }">
                <template v-if="!loading">
                    <transition name="real-name-ways">
                        <div v-show="toggleDisplay" class="ways" :class="{ 'modal-style': isModalDisplay }">
                            <slot></slot>
                        </div>
                    </transition>
                </template>
                <div v-if="loading" class="loading-con">
                    <img src="@mobile-common/loading.svg" class="loading" />
                    正在查找可用认证方式...
                </div>
            </div>
        </template>
        <template v-else-if="!loading">
            <q-icon class="exit-auth" icon="export" @click="showExitAuthModal"></q-icon>
            <div class="no-ways-tip middle-center main-word regular-color">
                JSSDK不支持该认证流程，请核实后台配置
            </div>
        </template>
    </div>
</template>

<script>
import AuthGraphicCode from '@mobile-common/auth-graphic-code.vue';
import certifyCode from '../auth-default/default-certify-code.vue';
import bankEntrance from './bank-entrance.vue';
import faceEntrance from './face-entrance.vue';
import ivsEntrance from './ivs-entrance.vue';
import manualEntrance from './manual-entrance.vue';

export default {
    name: 'authWays',
    components: {},
    props: {
        isModalDisplay: {
            type: Boolean,
            default: true,
        },
        root: {
            type: Object,
        },
        user: {
            type: Object,
            default: () => {
                return {};
            },
        },
        channel: {
            type: String,
            default: '',
        },
        reverifyCert: {
            type: String,
            default: '',
        },
        otherModes: {
            type: String,
            default: '',
        },
        accountPrecheck: {
            type: Boolean,
            default: false,
        },
        useOtherFrom: {
            type: String,
            default: '',
        },
        faceFailedType: {
            type: Array,
            default: () => [],
        },
    },
    data() {
        return {
            showAlipayWays: false,
            authWays: [],
            hasCardPic: false, // 是否有证件照
            hasHoldCardPic: false, // 是否有手持证件照
            hasManualed: false,
            isUseMail: false,
            step: '',
            faildLogs: [],
            loading: true,
            isAndroid: false,
            isShotVideo: false,
            toggleDisplay: false,
        };
    },
    computed: {
        faceAuthDesc() {
            if (!this.isShotVideo) {
                return '需现场拍摄正脸照片';
            }
            return '需录制一段朗读数字的认证视频';
        },
        authAccount() {
            let contactContent = this.getContactCodeAndContent(this.user.mobile || '').contactContent || '';
            let contactCode = this.getAreaCodeObj(this.user.mobile || '').value;
            if (this.isUseMail) {
                return contactContent;
            }
            return this.getContactWithCode(contactCode, contactContent);
        },
        degradeToManual() {
            return this.otherModes.includes('MANUAL');
        },
        baiduAiHasFailed() {
            return this.faildLogs.some(item => item.type === 'BAIDUAI');
        },
        hasOtherWaysExcludeManual() {
            if (this.$appointAuthWay === 'FACE') {
                return true;
            }
            return this.authWays.some(item => item !== 'MANUAL');
        },
        // 数据库中是否存有认证图片（如果有，那么意味着人脸验证通过/人工审核通过）
        hasPic() {
            return this.hasCardPic || this.hasHoldCardPic;
        },
        noWays() {
            return this.authWays.every(way => way === 'MANUAL') && !this.showAuthWay('MANUAL');
        },
    },
    watch: {
        loading(val) {
            if (!val) {
                this.sortEntrance();
                this.toggleDisplay = true;
            }
        },
    },
    async created() {
        try {
            this.isUseMail = !this.$qUtils.validata({
                value: this.user.mobile,
                noValNotValid: false,
                rules: [{ rule: this.$qUtils.validator.isEmail }],
            }).isInvalid;

            const deviceType = this.getDeviceType();
            this.isAndroid = deviceType === 'android';

            // 特殊浏览器由于不支持跳转支付宝，隐藏支付宝入口和弹窗
            this.authWays = await this.$getAuthWays();

            this.excludeCurrentAuthWay();

            // 判断证件照类型
            await this.judgeCardPictureType();
            await this.judgeManualed();

            await this.$getAppointAuthWay();
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_DETAIL',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            const { result } = this.decryptoData(res.result);
            this.step = result.step;
            const data = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_LOG',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            this.faildLogs = data.result;

            // * Android或无证件照的iOS => true (录视频)
            // * 有证件照的iOS => false (拍照片)
            this.isShotVideo = this.isAndroid || (!this.isAndroid && !this.hasPic);
        } catch (error) {
            console.log(error);
        } finally {
            this.loading = false;
        }
    },
    mounted() {
        this.$nextTick(() => {
            this.showAlipayWays = true;
        });
    },
    methods: {
        sortEntrance() {
            const vnodes = {
                BANK: this.insertBankEntrance(),
                FACE: this.insertFaceEntrance(),
                IVS: this.insertIvsEntrance(),
                MANUAL: this.insertManualEntrance(),
            };
            let vnodeArr = [];

            let authWays = this.$qUtils.deepClone(this.authWays);
            authWays = this.addAuthWays(authWays);

            authWays.forEach(way => {
                if (Object.keys(vnodes).includes(way)) {
                    vnodeArr.push(vnodes[way]);
                }
            });
            let vnode = this.$createElement(
                'div',
                {
                    class: {
                        container: true,
                    },
                },
                vnodeArr
            );
            this.$slots.default = vnode;
            console.log('this.$slots**', this.$slots);
        },
        insertBankEntrance() {
            let vnode = this.$createElement(bankEntrance, {
                props: {
                    isModalDisplay: this.isModalDisplay,
                    isShow: this.showAuthWay('BANK'),
                    auth: this.bankAuth,
                },
            });
            return vnode;
        },
        insertFaceEntrance() {
            let vnode = this.$createElement(faceEntrance, {
                props: {
                    isModalDisplay: this.isModalDisplay,
                    isShow: this.showAuthWay('FACE'),
                    auth: this.faceAuth,
                    faceAuthDesc: this.faceAuthDesc,
                },
            });
            return vnode;
        },
        insertIvsEntrance() {
            let vnode = this.$createElement(ivsEntrance, {
                props: {
                    isModalDisplay: this.isModalDisplay,
                    isShow: this.showAuthWay('IVS'),
                    auth: this.phoneAuth,
                },
            });
            return vnode;
        },
        insertManualEntrance() {
            let vnode = this.$createElement(manualEntrance, {
                props: {
                    isModalDisplay: this.isModalDisplay,
                    isShow: this.showAuthWay('MANUAL'),
                    auth: this.manualAuth,
                },
            });
            return vnode;
        },
        manualAuth() {
            this.dealStep('MANUALINFO');
        },
        excludeCurrentAuthWay() {
            let index = (this.authWays || []).findIndex(way => way === this.useOtherFrom);
            if (index > -1) {
                this.authWays.splice(index, 1);
            }
        },
        async judgeCardPictureType() {
            try {
                const res = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_AUTH_IMAGE_EXIST',
                        data: {
                            userId: this.$_qyssdk_userId,
                            name: this.user.username,
                            cardNo: this.user.cardNo,
                        },
                    }),
                });
                this.hasCardPic = res.faceImage;
                this.hasHoldCardPic = res.handfrontImage;
            } catch (error) {
                console.log(error);
            }
        },
        async judgeManualed() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_PASS_BEFORE',
                    data: {
                        userId: this.$_qyssdk_userId,
                        name: this.user.username,
                        cardNo: this.user.cardNo,
                    },
                }),
            });
            this.hasManualed = res.result;
        },
        addAuthWays(authWays) {
            // 指定人脸，authWays里没有当前指定的方式，故需要手动添加
            // 指定IVS || BANK不会导致改问题，因为不会调至认证方式选择页
            if (this.$appointAuthWay === 'FACE' && !this.baiduAiHasFailed && !authWays.includes('FACE')) {
                authWays.push('FACE');
            }

            // 去除当前操作认证方式
            let index = authWays.findIndex(item => item === this.useOtherFrom);
            if (index > -1) {
                authWays.splice(index, 1);
            }
            console.log('authWays--', authWays);
            return authWays;
        },
        showAuthWay(authWay) {
            let authWays = this.$qUtils.deepClone(this.authWays);
            authWays = this.addAuthWays(authWays);

            // 当前指定认证方式为人脸时，只有失败后才展示降级方式
            if (
                this.$appointAuthWay === 'FACE' &&
                this.faildLogs.length === 0 &&
                (authWay === 'IVS' || authWay === 'BANK' || authWay === 'MANUAL')
            ) {
                return false;
            }

            if (authWay === 'MANUAL') {
                // 人工只在失败后展示
                if (!this.step.includes('FAILED') && this.faildLogs.length === 0) {
                    return false;
                }
                return !this.hasManualed && this.degradeToManual && !this.hasHoldCardPic;
            }

            return (authWays || []).includes(authWay);
        },
        async auth(authWay) {
            const operateBeforeAuth = {
                PHONEINFO: this.phoneAuth,
                BANKINFO: this.bankAuth,
            };
            await operateBeforeAuth[authWay]();
        },
        // 三要素认证
        async phoneAuth() {
            if (this.isUseMail || !this.authAccount) {
                this.dealStep('threeElementsAuth');
                return;
            }

            if (this.reverifyCert || this.accountPrecheck) {
                this.submitInfoWithoutCode();
                return;
            }

            const { result } = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_CAPTCHAREQUIRED',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            if (!result) {
                this.getCode(null, result);
                return;
            }
            const instance = new Vue({
                components: { AuthGraphicCode },
                render: h =>
                    h(AuthGraphicCode, {
                        props: {
                            title: '输入图形验证码',
                        },
                        on: {
                            getCode: (captcha, captchaToken) => {
                                this.getCode(captcha, result, captchaToken);
                            },
                        },
                    }),
            }).$mount();
            document.body.appendChild(instance.$el);
        },
        // 银行卡认证
        bankAuth() {
            this.dealStep('BANKINFO');
        },
        async getCode(captcha, validCaptcha, captchaToken = null) {
            await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_PIN',
                    data: {
                        userId: this.$_qyssdk_userId,
                        mobile: this.authAccount,
                        validCaptcha,
                        captcha,
                        captchaToken,
                    },
                }),
            });
            const vueOptions = {
                components: { certifyCode },
                render: h =>
                    h(certifyCode, {
                        props: {
                            root: this.root,
                            user: this.user,
                            mobile: this.authAccount,
                            isUseMail: this.isUseMail,
                            accountPrecheck: this.accountPrecheck,
                            otherModes: this.otherModes,
                            channel: this.channel,
                        },
                    }),
            };
            let elm = new Vue(vueOptions).$mount();
            document.body.appendChild(elm.$el);
        },
        async submitInfoWithoutCode() {
            let { username, cardNo, mobile, paperType } = this.user;
            let loading = this.$loading('提交验证中...');
            let params = {
                username,
                cardNo,
                mobile,
                paperType,
            };
            try {
                await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'UPLOAD_IVS_MATERIAL',
                            data: {
                                userId: this.$_qyssdk_userId,
                                material: params,
                            },
                        }),
                    },
                    {
                        headers: {
                            'reverify-certificate': this.reverifyCert,
                        },
                    }
                );
                await this.verify();
            } catch (error) {
                console.log(error);
            } finally {
                loading.close();
            }
        },
        async verify() {
            try {
                const res = await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'PHONE_AUTH_VERIFY',
                            data: {
                                userId: this.$_qyssdk_userId,
                            },
                        }),
                    },
                    {
                        headers: {
                            noMes: 'true',
                        },
                    }
                );
                if (res.code === 0 && res.nextStep === 'END') {
                    this.dealStep('SUCCESS');
                    return;
                }
                this.dealStep(res.nextStep);
            } catch (error) {
                console.log(error);
            }
        },
        // 百度AI
        async faceAuth() {
            try {
                const res = await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'UPLOAD_FACE_MATERIAL',
                            data: {
                                userId: this.$_qyssdk_userId,
                                material: {
                                    username: this.user.username,
                                    cardNo: this.user.cardNo,
                                },
                            },
                        }),
                    },
                    {
                        headers: {
                            noMes: 'true',
                        },
                    }
                );
                if (res.code === 0) {
                    this.dealStep('FACEID');
                } else {
                    this.$message(res.message);
                }
            } catch (error) {
                console.log(error);
            }
        },
        close() {
            this.$destroy(true);
            this.$el.parentNode.removeChild(this.$el);
        },
        dealStep(step) {
            this.root.dealStep(step);
        },
        async showExitAuthModal() {
            try {
                await this.$mt.MessageBox.confirm('', {
                    message: '确定退出认证流程？',
                    confirmButtonClass: 'main-word link-color',
                });
                this.$giveUpAuth();
            } catch (e) {
                console.log(e);
            }
        },
    },
};
</script>
<style lang="less" scoped>
.auth-ways {
    position: fixed !important;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: white;
    z-index: 100;
    &.is-transparent {
        background: transparent;
    }
    .mask {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.4);
    }
    .header {
        width: 100%;
        height: 36px;
        display: flex;
        align-items: center;
        padding: 0 16px;
        background: #e9f7ed;
        color: @success-color;
        font-size: 12px;
        margin-bottom: 24px;
        .safetycertificate-icon {
            margin-right: 4px;
        }
    }
    .ways-title {
        padding: 0 20px 8px;
    }
    .content {
        width: 100%;
        padding: 0 20px;
        background: white;
        &.modal-content {
            position: fixed;
            bottom: 0;
            left: 0;
            padding: 0 0 0 20px;
        }
        .title {
            width: 100%;
            height: 28px;
            background: @body-background;
            color: #4c596e;
            line-height: 28px;
            padding: 0 15px;
        }
        .ways {
            width: 100%;
            border-radius: 6px;
            margin-bottom: 32px;
            transition: all 0.3s ease-out;
            &.modal-style {
                box-shadow: none;
                margin-bottom: 0;
                padding: 0;
                transition: none;
            }

            &.real-name-ways-enter,
            &.real-name-ways-leave-to {
                transform: translate3d(100%, 0, 0);
            }
            &.real-name-ways-leave,
            &.real-name-ways-enter-to {
                transform: translate3d(0, 0, 0);
            }
            .way {
                position: relative;
                width: 100%;
                display: flex;
                align-items: center;
                border-radius: 6px;
                background: @body-background;
                padding: 0 16px;
                margin-bottom: 4px;
                .bg-img {
                    width: 80px;
                    position: absolute;
                    right: 0;
                    bottom: 0;
                    display: none;
                }
                .icon-con {
                    width: 42px;
                    height: 42px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background: linear-gradient(315deg, rgba(85, 99, 126, 1) 0%, rgba(125, 139, 170, 1) 100%);
                    color: white;
                    border-radius: 21px;
                    font-size: 22px;
                    margin-right: 18px;
                    &.modal-style {
                        &.zhifubao {
                            background: @primary-color;
                            color: white;
                        }
                    }
                    &.zhifubao {
                        font-size: 36px;
                    }
                }
                .desc {
                    height: 100%;
                    flex: 1;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    padding: 17px 0;
                    .tip {
                        font-size: 12px;
                        color: #7f8997;
                        position: relative;
                        top: -2px;
                    }
                    &.modal-style {
                        padding: 17px 20px 17px 0;
                        border-bottom: 1px solid #f2f3f4;
                        .text-title {
                            color: #001330;
                        }
                        .text-tip {
                            color: #7f8997;
                        }
                    }
                }
                &:first-child {
                    background: linear-gradient(313deg, rgba(36, 101, 242, 1) 0%, rgba(36, 155, 242, 1) 100%);
                    margin-bottom: 24px;
                    .bg-img {
                        display: block;
                    }
                    .icon-con {
                        background: white;
                        color: @primary-color;
                        &.modal-style {
                            background: linear-gradient(315deg, rgba(85, 99, 126, 1) 0%, rgba(125, 139, 170, 1) 100%);
                            color: white;
                            &.zhifubao {
                                background: @primary-color;
                                color: white;
                            }
                        }
                    }
                    .desc {
                        span {
                            color: white;
                        }
                    }
                }
                &.way-modal-style {
                    background: white;
                    margin-bottom: 0;
                    padding: 0;
                    .bg-img {
                        display: none;
                    }
                }
                .right-icon {
                    position: relative;
                    top: 0;
                    right: -6px;
                    font-size: 16px;
                    color: #c1c6cd;
                }
            }
        }
        .loading-con {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            width: 100%;
            height: 100px;
            letter-spacing: 1px;
            font-size: 16px;
            color: #7f8997;
            .loading {
                width: 30px;
                height: 30px;
                margin-bottom: 10px;
                margin-right: 10px;
            }
        }
    }
    .exit-auth {
        position: absolute;
        top: 16px;
        right: 16px;
        font-size: 18px;
        color: #7f8997;
    }
    .no-ways-tip {
        width: 100%;
        padding: 20px 20px;
        text-align: center;
    }
}
</style>
