<template>
    <div class="auth-checkbox" @click="$emit('update:checked', !checked)">
        <q-icon :icon="checked ? 'check-square-selected' : 'check-square-normal'"></q-icon>
        <slot></slot>
    </div>
</template>

<script>
export default {
    name: 'AuthCheckbox',
    props: {
        checked: Boolean,
    },
    data() {
        return {};
    },
    methods: {},
};
</script>

<style lang="less" scoped>
.auth-checkbox {
    position: relative;
    display: inline-block;

    // 扩大点击区域
    &::after {
        content: '\200b';
        position: absolute;
        right: -5%;
        top: -10%;
        display: block;
        width: 110%;
        height: 120%;
    }
}
</style>
