<template>
    <div class="bank-fail">
        <div class="main-content">
            <div class="result-top">
                <q-icon @click="showExitAuthModal" class="exit-auth" icon="export"></q-icon>
                <img class="res-img" src="../assets/img/default-result-fail.png" />
                <span class="h2-title title">认证失败</span>
                <span class="tip-large">身份信息与银行卡登记信息不匹配</span>
            </div>

            <div class="result-bottom">
                <div class="info-module">
                    <span class="main-word title-color title">核对身份信息</span>
                    <div class="info-row">
                        <span class="main-word regular-color">姓&nbsp;&nbsp;&nbsp;&nbsp;名</span
                        ><span class="main-word">{{ user.username }}</span>
                    </div>
                    <div class="info-row">
                        <span class="main-word regular-color">证件类型</span
                        ><span class="main-word">{{ paperType }}</span>
                    </div>
                    <div class="info-row">
                        <span class="main-word regular-color">证件号</span
                        ><span class="main-word">{{ user.cardNo }}</span>
                    </div>
                    <div v-if="user.mobile" class="info-row">
                        <span class="main-word regular-color">{{ isUseMail ? '邮箱' : '手机号' }}</span
                        ><span class="main-word">{{ user.mobile }}</span>
                    </div>
                </div>

                <div class="info-module">
                    <div class="info-row">
                        <span class="main-word regular-color">银行卡号</span
                        ><span class="main-word">{{ user.bankNo }}</span>
                    </div>
                    <div class="info-row">
                        <span class="main-word regular-color">银行预留手机号</span
                        ><span class="main-word">{{ user.contact }}</span>
                    </div>
                </div>
            </div>
            <div class="modify-info main-word body-color-dark" type="primary" @click="authRepeat">
                修改信息重新认证
            </div>
            <div class="change-other main-word" type="primary" v-if="isShowOtherWays" @click="openAuthWaysModal">
                尝试其他认证方式
            </div>
        </div>
    </div>
</template>

<script>
import authWaysModal from '../auth-common/auth-ways.vue';

export default {
    name: 'BankFail',
    props: {
        otherModes: {
            type: String,
            default: '',
        },
        user: {
            type: Object,
            default: () => {
                return {};
            },
        },
        channel: {
            type: String,
            default: '',
        },
        successPage: {
            type: String,
            default: '',
        },
        step: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            hasCardPic: false,
            hasHoldCardPic: false,
            hasManualed: false,
            isNextStepDisabled: false,
            otherAuthWays: [],
        };
    },
    computed: {
        isUseMail() {
            let result = this.$qUtils.validata({
                value: this.user.mobile,
                noValNotValid: false,
                rules: [{ rule: this.$qUtils.validator.isEmail }],
            });
            return !result.isInvalid;
        },
        paperType() {
            let result = '';
            let obj = {
                IDCARD: '身份证',
                PASSPORT: '护照',
                HKMP: '港澳通行证',
                MTPS: '台胞证',
            };
            return obj[this.user.paperType];
        },
        degradeToManual() {
            return this.otherAuthWays.includes('MANUAL');
        },
        isShowOtherWays() {
            // 除了当前正在操作的认证，还有其他认证则显示其他认证入口
            let authWays = this.$qUtils.deepClone(this.otherAuthWays);
            let canManual = !this.hasManualed && this.degradeToManual && !this.hasHoldCardPic;
            if (canManual) {
                return true;
            }
            if (!canManual && authWays.every(item => item === 'MANUAL')) {
                return false;
            }
            return !!authWays.length;
        },
    },
    async created() {
        await this.getDetail();
        await this.judgeCardPictureType();
        await this.judgeManualed();
        this.otherAuthWays = await this.$getAuthWays();
    },
    methods: {
        openAuthWaysModal() {
            const instance = new Vue({
                components: { authWaysModal },
                render: h =>
                    h(authWaysModal, {
                        props: {
                            root: this,
                            user: this.user,
                            channel: this.channel,
                            otherModes: this.otherModes,
                            step: this.step,
                        },
                    }),
            }).$mount();
            document.body.appendChild(instance.$el);
        },
        async judgeCardPictureType() {
            try {
                const res = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_AUTH_IMAGE_EXIST',
                        data: {
                            userId: this.$_qyssdk_userId,
                            name: this.user.username,
                            cardNo: this.user.cardNo,
                        },
                    }),
                });
                this.hasCardPic = res.faceImage;
                this.hasHoldCardPic = res.handfrontImage;
            } catch (error) {
                console.log(error);
            }
        },
        async authRepeat() {
            this.$emit('reVerify');
            const { result } = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_CURRENT_STEP',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            if (result === 'END') {
                this.dealStep('INVALID');
                return;
            }
            this.dealStep('INFO');
        },
        async getDetail() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_DETAIL',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            const { result } = this.decryptoData(res.result);
            // 若可以降级处理，则需先更新user
            if (this.otherModes) {
                this.user = Object.assign(this.user, result.authInfo);
            }
        },
        async judgeManualed() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_PASS_BEFORE',
                    data: {
                        userId: this.$_qyssdk_userId,
                        name: this.user.username,
                        cardNo: this.user.cardNo,
                    },
                }),
            });
            this.hasManualed = res.result;
        },
        async showExitAuthModal() {
            try {
                await this.$mt.MessageBox.confirm('', {
                    message: '确定退出认证流程？',
                    confirmButtonClass: 'main-word link-color',
                });
                this.$giveUpAuth();
            } catch (e) {
                console.log(e);
            }
        },
    },
};
</script>
<style lang="less" scoped>
.bank-fail {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    .main-content {
        flex: 1;
        background: @body-background;
    }
    .result-top {
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 19px 20px 28px;
        background: white;
        margin-bottom: 10px;
        position: relative;
        .exit-auth {
            position: absolute;
            top: 16px;
            right: 16px;
            font-size: 18px;
            color: #c1c6cd;
        }
        .res-img {
            width: 138px;
        }
        .title {
            margin-top: -10px;
        }
    }
    .result-bottom {
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        padding-left: 20px;
        background: white;
        .info-module {
            padding: 16px 20px 6px 0;
            border-bottom: 1px solid @body-background;
            &:last-child {
                border-bottom: none;
            }
        }
        .title {
            flex-shrink: 0;
            margin-bottom: 16px;
        }
        .info-row {
            flex-shrink: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 12px;
            :first-child {
                min-width: 80px;
            }
            > :last-child {
                text-align: right;
            }
        }
        .upload-title {
            margin-bottom: 8px;
        }
        .img-container {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            margin-bottom: 16px;
            .img-item {
                flex: 1;
                display: flex;
                flex-direction: column;
                align-items: center;
                &:first-child {
                    margin-right: 14px;
                }
                img {
                    width: 100%;
                    border-radius: 6px;
                    margin-bottom: 4px;
                }
            }
        }
    }

    .modify-info {
        padding: 10px 0px;
        background: @primary-color;
        border: 1px solid #2489f2;
        border-radius: 4px;
        text-align: center;
        margin: 16px;
    }
    .change-other {
        margin: 0 16px 16px 16px;
        padding: 10px 0px;
        border-radius: 4px;
        text-align: center;
        border: 1px solid #2489f2;
        color: #2489f2;
        margin-right: 11px;
        background: #fff;
    }
}
</style>
