const PHONE_RE_LIST = [
    /^(?:\+?(86)\s?)?(1[0-9]{10})$/, // 大陆号码：区号86，1开头的十一位数字
    /^\+?(852)\s?([569][0-9]{7})$/, // 香港号码：区号852，5,6,9开头的八位数字
    /^\+?(886)\s?(0?9[0-9]{8})$/, // 台湾号码：区号886，09开头的十位数字（0可能不存在）
];
Vue.filter('plusPrefix', v => {
    let result = v;

    const re = PHONE_RE_LIST.find(re => re.test(v));
    if (re) {
        result = v.replace(re, function(matched, code, phone, index, full) {
            return `${code ? '+' : ''}${full}`;
        });
    }

    return result;
});

