Vue.prototype.$mosaic = function(v, returnOriginal) {
    function mosaicMail(value) {
        let index = value.indexOf('@');
        if (index > 3) {
            return `${value.substr(0, 3)}******${value.substr(index)}`;
        } else {
            return value;
        }
    }
    function mosaicMobile(value) {
        const re = /^(?:\+?(86)\s?)?(1\d{2})(\d{4})(\d{4})$/; // 大陆号码：区号86，1开头的十一位数字
        const hkRE = /^\+?(852)\s?([569]\d{1})(\d{4})(\d{2})$/; // 香港号码：区号852，5,6,9开头的八位数字
        const twRE = /^\+?(886)\s?(0?9\d{1})(\d{4})(\d{3})$/; // 台湾号码：区号886，09开头的十位数字（0可能不存在）

        let reUsed = re;
        if (hkRE.test(value)) {
            reUsed = hkRE;
        } else if (twRE.test(value)) {
            reUsed = twRE;
        }
        return value.replace(reUsed, function($0, $1, $2, $3, $4) {
            const code = !$1 || $1 === '86' ? '' : `+${$1} `;
            return code + $2 + $3.replace(/./g, '*') + $4;
        });
    }

    if (!v) {
        return '';
    }
    if (returnOriginal) {
        // 如果显示地传入了 returnOriginal ，那么不打马赛克
        return v;
    }

    const emailRE = this.$qUtils.validator.isEmail;
    if (PHONE_RE_LIST.find(re => re.test(v))) {
        return mosaicMobile(v);
    } else if (emailRE.test(v)) {
        return mosaicMail(v);
    }
    return v;
};

/* personalAuth 自用的 */
let pageKey = Vue.observable({ $pageRandom: uuid() });
Vue.prototype.dealStep = async function(step) {
    try {
        const self = this;
        // const flag = this.$props == undefined;
        switch (step) {
            case 'FACEINFO':
            case 'PHONEINFO':
            //!测试状态后端没有合并
            case 'INFO':
                changeStatus('authInformation');
                break;

            case 'PHONEFAILED':
                changeStatus('defaultFail');
                break;

            case 'FACEID':
                changeStatus('faceAuth');
                break;

            case 'FACEFAILED':
                changeStatus('faceFail');
                break;

            case 'BANKFAILED':
                changeStatus('bankFail');
                break;
            case 'BANKINFO':
                changeStatus('bankAuth');
                break;

            case 'MANUALINFO':
            case 'MANUAL':
                changeStatus('manualAuth');
                break;
            case 'MANUALING':
                changeStatus('manualInAudit');
                break;
            case 'MANUALFAILED':
                changeStatus('manualFail');
                break;

            case 'SUCCESS':
                changeStatus('authSuccess');
                break;

            case 'INVALID':
                changeStatus('authInvalid');
                break;

            default:
                changeStatus(step); // 手机号三要素【threeElementsAuth】，验证码【certifyCode】,验证码用户【defaultMailAuth】
                break;
        }
        function changeStatus(status) {
            console.log('[test] changeStatus, status :', status); // TODO: test to del
            self.status = status;
            self.$emit('update:status', status);

            // 为每个页面提供随机key，用于解决同一页面切换时页面不重新加载的问题
            pageKey.$pageRandom = uuid();
            Vue.prototype.pageKey = pageKey;

            let authWaysModal = document.getElementsByClassName('auth-ways');
            Array.from(authWaysModal).forEach(modal => {
                modal.parentNode.removeChild(modal);
            });
        }
    } catch (e) {
        console.log(e);
    }
};

// 退出认证
Vue.prototype.$giveUpAuth = async function() {
    try {
        await this.postForm(
            '',
            {
                requestData: JSON.stringify({
                    type: 'END_AUTH_PROCESS',
                    data: { userId: this.$_qyssdk_userId },
                }),
            },
            { headers: { noMes: 'true' } }
        );
        await this.postForm(
            '',
            {
                requestData: JSON.stringify({
                    type: 'CLEAR_AUTH_CERTIFICATION',
                    data: { userId: this.$_qyssdk_userId },
                }),
            },
            { headers: { noMes: 'true' } }
        );
        if (this.$_qyssdk_options && this.$_qyssdk_options.authFailCb) {
            this.$_qyssdk_options.authFailCb();
        }
    } catch (error) {
        console.log(error);
    }
};

