if (!Object.$get) {
    Object.defineProperty(Object.prototype, '$get', {
        value() {
            return Array.from(arguments).reduce((xs, x) => (xs && xs[x] ? xs[x] : null), this);
        },
        enumerable: false,
        writable: false,
        configurable: false,
    });
}

window.VERSION = '1.0.9';
class qysSdk {
    constructor(url, { query }) {
        console.log(`契约锁 SDK v${VERSION}`);
        window.qysQuery = query;
        Vue.prototype.configApi = url;
        this.ajax = new Vue();
        this.element = null;
    }

    async initUserId(data) {
        let { result: userId } = await this.ajax.postForm('', {
            requestData: JSON.stringify({
                type: 'GET_USER',
                data,
            }),
        });
        this.userId = userId;
        Vue.prototype.$_qyssdk_userId = userId;
    }

    async initContract() {
        let { result: contract } = await this.ajax.postForm('', {
            requestData: JSON.stringify({
                type: 'GET_CONTRACT',
                data: {
                    contractId: this.contractId,
                    userId: this.userId,
                    simple: false,
                },
            }),
        });
        this.contract = contract;
    }

    isRealNameNeeded() {
        let currentSignatory =
            this.contract.signatories.find(signatory => signatory.id === this.contract.signatoryId) || {};
        let needRealName = currentSignatory.preAction == 'REALNAME';
        return needRealName;
    }

    appendRequestDataWithType(data, dataType) {
        const excludeReqTypes = [
            'GET_CONTRACT',
            'GET_SIGN_PIN',
            'GET_SIGN_VOICE',
            'GET_FACESIGN_CHECK',
            'GET_FACESIGN_KEY',
            'UPLOAD_PERSONAL_SEAL',
            'SIGN_CONTRACT',
            'REJECT_CONTRACT',
            'DOWNLOAD_DOCUMENT_IMAGE',
            'DOWNLOAD_SEAL',
            'DOWNLOAD_CONTRACT',
        ];
        let result = data;
        if (typeof data === 'string') {
            // case String
            const [full, prefix, origin, suffix] = data.match(/(requestData=)([^&]*)(.*)/);
            const json = JSON.parse(decodeURIComponent(origin));
            if (excludeReqTypes.includes(json.type)) {
                return data;
            }
            json.data.type = dataType;
            if (dataType === 'SIGN_AUTH') {
                json.data.contractId = this.contractId;
            }
            const update = encodeURIComponent(JSON.stringify(json));
            result = [prefix, update, suffix].join('');
        } else if (typeof data === 'object' && data.append) {
            // case FormData
            const oldDataStr = data.get('requestData');
            const json = JSON.parse(oldDataStr);
            if (excludeReqTypes.includes(json.type)) {
                return data;
            }
            json.data.type = dataType;
            if (dataType === 'SIGN_AUTH') {
                json.data.contractId = this.contractId;
            }
            const newDataStr = JSON.stringify(json);
            result.set('requestData', newDataStr);
        } else {
            throw new Error(`In function appendRequestDataWithType, get UNKNOWN type of data parameter :(`, data);
        }
        return result;
    }

    async personalSign(contractId, contact, options) {
        const self = this;
        axios.interceptors.request.use(
            function(config) {
                config.data = self.appendRequestDataWithType(config.data, 'SIGN_AUTH');
                return config;
            },
            err => Promise.reject(err)
        );

        this.contractId = contractId;
        this.contact = contact;
        options.callType = 'SIGN_AUTH';
        Vue.prototype.$_qyssdk_options = options;
        Vue.prototype.$_qyssdk_contractId = contractId;

        await this.initUserId({
            contractId: this.contractId,
            contact: this.contact,
        });
        await this.initContract();

        if (this.isRealNameNeeded()) {
            this.element = await this.preparePersonalAuth({
                contractId: this.contractId,
                signatoryId: this.contract.signatoryId,
            });
        } else {
            this.element = this.createPersonalSign();
        }

        return this.element.$el;
    }

    async preparePersonalAuth(extraData) {
        let data = await this.ajax.postForm(
            '',
            {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_DETAIL',
                    data: { userId: this.userId },
                }),
            },
            { headers: { noMes: 'true' } }
        );
        if ([2002, 401, 402, 403].includes(data.code)) {
            await this.ajax.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_CERTIFICATION',
                    data: Object.assign({ userId: this.userId }, extraData),
                }),
            });
        }
        Vue.prototype.$_qyssdk_options.certificationExtraData = extraData;
        return this.createPersonalAuth(extraData);
    }

    async personalAuth(userInfo, options) {
        const self = this;
        axios.interceptors.request.use(
            function(config) {
                config.data = self.appendRequestDataWithType(config.data, 'PRE_AUTH');
                return config;
            },
            err => Promise.reject(err)
        );

        /**
         * userInfo: Object
         *   mode        // DEFLAUT、FACE、BANK
         *   userName    // 用户姓名
         *   contact     // 用户手机号
         *   idCardNo    // 用户身份证号
         *   bankNo      // 用户银行卡账号
         *   bankMobile  // 用户银行卡预留手机号
         */
        if (typeof userInfo.manual === 'boolean') {
            if (userInfo.manual && ['BANK', 'FACE'].includes(userInfo.mode)) {
                userInfo.mode += 'ORMANUAL';
            }
            delete userInfo.manual;
        }
        this.userInfo = userInfo;
        if (options.authSuccessCb) {
            options.authSuccessCbWrapper = function(mode, authInfo) {
                const userInfo = {
                    mode,
                    username: authInfo.username,
                    mobile: authInfo.mobile,
                    cardNo: authInfo.cardNo,
                };
                if (mode === 'BANK') {
                    Object.assign(userInfo, {
                        bankNo: authInfo.bankNo,
                        contact: authInfo.contact,
                    });
                }
                this.authSuccessCb(userInfo);
            };
        }
        options.callType = 'PRE_AUTH';
        Vue.prototype.$_qyssdk_options = options;

        await this.initUserId({
            contact: this.userInfo.contact,
        });

        this.element = await this.preparePersonalAuth({
            userInfo: this.userInfo,
        });

        return this.element.$el;
    }

    createPersonalAuth(certificationExtraData) {
        const self = this;
        let personalAuthInstance = new Vue({
            render: h =>
                h(personalAuth, {
                    props: {
                        contractId: this.contractId,
                        signatoryId: this.contract ? this.contract.signatoryId : '',
                        certificationExtraData,
                    },
                    on: {
                        goPersonalSign() {
                            self.replaceElement('personalSign');
                        },
                    },
                }),
            components: {
                personalAuth,
            },
        }).$mount();

        return personalAuthInstance;
    }
    createPersonalSign() {
        let personalSignInstance = new Vue({
            render: h =>
                h(personalSign, {
                    props: {
                        contractId: this.contractId,
                        contact: this.contact,
                    },
                }),
            components: {
                personalSign,
            },
        }).$mount();

        return personalSignInstance;
    }
    replaceElement(elementType) {
        let instance = null;
        if (elementType == 'personalSign') {
            instance = this.createPersonalSign();
        }
        instance.$el.style.cssText = this.element.$el.style.cssText;
        this.element.$el.parentNode.replaceChild(instance.$el, this.element.$el);
        this.element.$destroy(true);
        this.element = instance;
    }
    destroy() {
        if (this.element) {
            this.element.$destroy(true);
            this.element.$el.parentNode.removeChild(this.element.$el);
        }
    }
}
window.qysSdk = qysSdk;
window.Vue = Vue;
