<template>
    <div>
        <img class="middle-center" :src="url"
            alt="" @load="successLoadImg">
    </div>
</template>

<script>
export default {
    name: 'showImage',
    data() {
        return {};
    },
    props: {
        url: {
            default: ''
        }
    },
    methods: {
        successLoadImg(event) {
            let target = event.target || event.srcElement;
            let scale = target.parentElement.offsetWidth / target.parentElement.offsetHeight;
            let image = target;
            if (image.complete == true) {
                if (image.width > image.height * scale) {
                    image.style.cssText = 'width: 100%;';
                } else {
                    image.style.cssText = 'height: 100%;';
                }
            }
            this.$emit('load', event);
        }
    },
    created() {}
};
</script>


<style lang="less" scoped>
div {
    width: 100%;
    height: 100%;
    position: relative;
}
</style>
