<template>
    <div @click="close" class="manual-photo-modal">
        <div class="modal-content">
            <img :src="img" />
            <div class="tip-item main-word" v-for="(item, index) in tips" :key="index">
                <span class="main-word disable-color">•</span> {{ item }}
            </div>
            <auth-button @click.stop="goUploadImg" class="btn" type="primary">我知道了</auth-button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ManualPhotoModal',
    props: {
        img: {
            type: String,
            default: '',
            required: true,
        },
        tips: {
            type: Array,
            default() {
                return [];
            },
        },
    },
    data() {
        return {};
    },
    methods: {
        goUploadImg() {
            this.$emit('goUploadImg');
            this.close();
        },
        close() {
            this.$destroy(true);
            const el = this.$el;
            el.parentNode.removeChild(el);
        },
    },
};
</script>
<style lang="less" scoped>
.manual-photo-modal {
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.4);
    width: 100%;
    height: 100%;
    z-index: 1000;
    .modal-content {
        position: absolute;
        left: 0;
        bottom: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 24px 20px;
        background: white;
        img {
            width: 100%;
            margin-bottom: 16px;
        }
        .tip-item {
            margin-bottom: 8px;
        }
        .btn {
            margin-top: 24px;
        }
    }
}
</style>
