<template>
    <div class="sub-header">
        <div class="tip-large">{{ account && !isNumberUser ? `认证账号：${$mosaic(account)}` : '' }}</div>
        <div @click="showSecurity" class="tip-large success-color sub-header-right">
            <q-icon class="safetycertificate-icon" icon="safetycertificate"></q-icon>
            <span>安全声明</span>
        </div>
    </div>
</template>

<script>
import authSecurityModal from '@mobile-common/auth-security-modal';
export default {
    name: 'AuthSubheader',
    props: {
        account: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            isNumberUser: false, // 是否为编号用户
        };
    },
    async created() {
        await this.getUserType();
    },
    methods: {
        async getUserType() {
            try {
                const { result } = await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'GET_AUTH_ONLYID',
                            data: {
                                userId: this.$_qyssdk_userId,
                            },
                        }),
                    },
                    {
                        headers: {
                            noMes: 'true',
                        },
                    }
                );
                this.isNumberUser = result;
            } catch (error) {
                console.log(error);
            }
        },
        showSecurity() {
            const intance = new Vue({
                components: { authSecurityModal },
                render: h => h(authSecurityModal, {}),
            }).$mount();
            document.body.appendChild(intance.$el);
        },
    },
};
</script>
<style lang="less" scoped>
.sub-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    padding: 0 12px;
    background: @body-background;
    height: 40px;
    .sub-header-right {
        display: flex;
        align-items: center;
        .safetycertificate-icon {
            margin-right: 3px;
        }
    }
}
</style>
