<template>
    <div
        @click.stop="codeToggle"
        class="area-code-wrapper"
        :class="{
            [className]: !!className,
            'is-disabled': disabled,
        }"
    >
        {{ selected.code }}
        <q-icon
            class="icon-arrow"
            :class="{ 'is-open': isShowSelect, 'is-close': !isShowSelect, 'is-disabled-arrow': disabled }"
            icon="rignt"
        ></q-icon>
        <div class="right-line"></div>
        <div v-if="isShowSelect" class="code-display">
            <div class="triangle_border_up">
                <span></span>
            </div>
            <div class="code-list">
                <div @click="selectCode(item)" class="code-item" v-for="(item, index) in codeList" :key="index">
                    <span>{{ item.label }}：</span><span>{{ item.code }}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'AreaCode',
    props: {
        disabled: {
            type: Boolean,
            default: false,
        },
        type: {
            type: String,
            default: '',
        },
        className: {
            type: String,
            default: '',
        },
        codeList: {
            type: Array,
            default() {
                return [
                    { label: '大陆', areaAbbr: 'ZH', code: '+86', value: '' },
                    { label: '香港', areaAbbr: 'HK', code: '+852', value: '852' },
                    { label: '台湾', areaAbbr: 'TW', code: '+886', value: '886' },
                ];
            },
        },
        code: {
            type: Object,
            default() {
                return { label: '大陆', areaAbbr: 'ZH', code: '+86', value: '' };
            },
        },
    },
    data() {
        return {
            isShowSelect: false,
            selected: {},
        };
    },
    watch: {
        code: {
            handler(data) {
                this.selected = data;
            },
            deep: true,
        },
    },
    mounted() {
        this.selected = this.code;
        document.getElementsByTagName('body')[0].addEventListener('click', e => {
            this.isShowSelect = false;
        });
    },
    methods: {
        codeToggle(e) {
            if (this.disabled) {
                return;
            }
            e.stopPropagation();
            this.isShowSelect = !this.isShowSelect;
            this.$emit('toggleSelectCode');
        },
        selectCode(item) {
            this.selected = item;
            this.$emit('getCode', item, this.type);
        },
    },
};
</script>

<style lang="less" scoped>
.area-code-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    color: #001330;
    &.is-disabled {
        color: #c1c6cd;
    }
    .icon-arrow {
        font-size: 11px;
        color: #7f8997;
        margin-right: 16px;
        &.is-open {
            transform: rotate(270deg);
            transition: transform 0.3s;
        }
        &.is-close {
            transform: rotate(90deg);
            transition: transform 0.3s;
        }
        &.is-disabled-arrow {
            color: #c1c6cd;
        }
    }
    .right-line {
        width: 1px;
        height: 40%;
        position: absolute;
        top: 30%;
        right: 0;
        background: #e5e7ea;
        z-index: -1;
    }
    .code-display {
        position: absolute;
        left: 0;
        top: calc(~'100% + 5px');
        width: 100px;
        background: #ffffff;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
        border-radius: 4px;
        font-weight: normal;
        z-index: 999;
        .code-list {
            width: 100%;
            max-height: 250px;
            overflow: auto;
            padding: 10px;
            box-sizing: border-box;
        }
        .code-item {
            width: 100%;
            height: 40px;
            background: white;
            display: flex;
            align-items: center;
            border-bottom: 1px solid #f2f3f4;
            font-size: 14px;
            &:last-child {
                border-bottom: none;
            }
            :first-child {
                font-size: 13px;
                color: #001330;
            }
        }
        .triangle_border_up {
            position: absolute;
            top: -6px;
            left: 20px;
            width: 0;
            height: 0;
            border-width: 0 6px 6px;
            border-style: solid;
            border-color: transparent transparent #eee;
            margin: 0 auto;
            z-index: 999;
        }
        .triangle_border_up span {
            display: block;
            width: 0;
            height: 0;
            border-width: 0 5px 5px;
            border-style: solid;
            border-color: transparent transparent #fff;
            position: absolute;
            top: 1px;
            left: -5px;
        }
    }
}
</style>
