/**
 * 获取带区号的联系方式
 * @param areaCode 区号
 * @param content 联系方式
 * @returns {string}
 */
export function getContactWithCode(areaCode, content) {
    let result;
    if (!areaCode || areaCode === '86') {
        result = content;
    } else {
        result = `${areaCode} ${content}`;
    }
    return result;
}

/**
 * 根据带区号的联系方式获取区号和联系方式
 * @param contact 带区号的联系方式
 * @returns {{contactContent: *, contactCode: (*|string)}}
 */
export function getContactCodeAndContent(contact = '') {
    if (!contact) {
        return {
            contactCode: '',
            contactContent: '',
        };
    }
    const phoneRegLoose = /^(?:\+?(86|852|886)\s?)?(.*)$/;
    const contactCode = contact.match(phoneRegLoose)[1] || '86';
    const contactContent = contact.match(phoneRegLoose)[2];
    return {
        contactCode,
        contactContent,
    };
}

/**
 * 获取手机区号数据
 * @param {*} contact
 */
export function getAreaCodeObj(contact = '') {
    const codeList = [
        { label: '大陆', areaAbbr: 'ZH', code: '+86', value: '' },
        { label: '香港', areaAbbr: 'HK', code: '+852', value: '852' },
        { label: '台湾', areaAbbr: 'TW', code: '+886', value: '886' },
    ];
    const phoneRegLoose = /^(?:\+?(86|852|886)\s?)?(.*)$/;
    const contactCode = contact.match(phoneRegLoose)[1] || '86';
    if (contactCode === '' || contactCode === '86') {
        return { label: '大陆', areaAbbr: 'ZH', code: '+86', value: '' };
    }
    return codeList.find(item => item.value === contactCode);
}

/**
 * 获取移动设备类型
 */
export function getDeviceType() {
    let type = ''; // 未知终端

    const UA = navigator.userAgent;
    let isAndroid = UA.indexOf('Android') > -1 || UA.indexOf('Adr') > -1; // android终端
    let isiOS = !!UA.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); // ios终端

    if (isAndroid) {
        type = 'android';
    } else if (isiOS) {
        type = 'ios';
    }

    return type;
}
