<template>
    <div ref="outerWrapper" class="bank-wrapper">
        <auth-subheader :account="user.mobile"></auth-subheader>
        <div class="bank-content">
            <div class="form-item">
                <div class="input-wrap" :class="inputStatus(info.bankNo.status)">
                    <ui-textbox
                        floating-label
                        id="bankNo"
                        ref="bankNo"
                        label="银行卡号"
                        v-model="info.bankNo.value"
                        iconPosition="right"
                        @blur="checkData('bankNo')"
                        :disabled="bankNoDisabled"
                    >
                        <q-icon
                            slot="icon"
                            v-if="info.bankNo.value && $refs.$get('bankNo', 'isActive')"
                            @touchstart="info.bankNo.value = ''"
                            class="clear-icon"
                            icon="close-circle-fill"
                        ></q-icon>
                    </ui-textbox>
                </div>
                <span v-if="info.bankNo.status" class="ui-textbox-tips" :class="tipStaus(info.bankNo.status)">
                    {{ info.bankNo.message }}
                </span>
            </div>

            <!-- 银行预留手机号 -->
            <div class="form-item" @click="activeForm('contact')">
                <div
                    class="input-wrap"
                    :class="[inputStatus(info.contact.status), !bankContactIsUseEmail && 'area-code-con']"
                >
                    <area-code
                        v-if="!bankContactIsUseEmail"
                        type="contact"
                        className="area-code-selecter"
                        v-show="isContactActive || !!info.contact.value"
                        :code="contactCode"
                        :disabled="bankContactDisabled"
                        @toggleSelectCode="toggleSelectCode('contact')"
                        @getCode="getAreaCode"
                    />
                    <ui-textbox
                        floating-label
                        id="contact"
                        ref="contact"
                        label="银行预留手机号"
                        v-model="info.contact.value"
                        @blur="
                            checkData('contact');
                            activeForm('contact');
                        "
                        @input="contactInput"
                        iconPosition="right"
                        :disabled="bankContactDisabled"
                    >
                        <q-icon
                            slot="icon"
                            v-if="!!info.contact.value && $refs.$get('contact', 'isActive')"
                            @touchstart="info.contact.value = ''"
                            class="clear-icon"
                            icon="close-circle-fill"
                        ></q-icon>
                    </ui-textbox>
                </div>
                <span v-if="info.contact.status" class="ui-textbox-tips" :class="tipStaus(info.contact.status)">
                    {{ info.contact.message }}
                </span>
            </div>

            <div v-if="showPin" class="code-wrap">
                <div class="form-item form-item-code">
                    <div class="input-wrap" :class="inputStatus(info.pin.status)">
                        <ui-textbox
                            floating-label
                            id="pin"
                            ref="pin"
                            label="验证码"
                            :maxlength="6"
                            :enforceMaxlength="true"
                            v-model="info.pin.value"
                        >
                        </ui-textbox>
                    </div>
                    <span v-if="info.pin.status" class="ui-textbox-tips" :class="tipStaus(info.pin.status)">
                        {{ info.pin.message }}
                    </span>
                </div>
                <div @click="sendCode" class="get-code-btn main-word link-color" :class="{ disabled: !countEnable }">
                    {{ countDown }}
                </div>
            </div>

            <auth-button
                class="submit-btn"
                type="primary"
                :disabled="submitDisable"
                :debounce="300"
                @click="submit"
                style="margin-top:40px;"
            >
                下一步
            </auth-button>

            <div v-if="showOtherAuthWays" class="tips">
                <span class="tip-large">不记得卡号/手机号？</span>
                <span class="tip-large link-color" @click="openAuthWaysModal">尝试其他认证方式</span>
            </div>
        </div>
    </div>
</template>

<script>
import authWaysModal from '../auth-common/auth-ways.vue';

export default {
    name: 'Bank',
    props: {
        user: {
            type: Object,
            default: () => {
                return {};
            },
        },
        staticInfo: {
            type: Object,
            default: () => {
                return {};
            },
        },
        canEditBankPhone: {
            type: Boolean,
            default: true,
        },
        modifyFieldList: {
            type: Array,
            default: () => [],
        },
        otherModes: {
            type: String,
            default: '',
        },
        channel: {
            type: String,
            default: '',
        },
        step: {
            type: String,
            default: '',
        },
        accountPrecheck: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            interval: null,
            countNumber: null,
            info: {
                bankNo: {
                    value: this.user.bankNo || '',
                    status: '',
                    message: '',
                },
                contact: {
                    value: this.bankContactIsUseEmail
                        ? ''
                        : this.getContactCodeAndContent(this.user.contact || '').contactContent,
                    status: '',
                    message: '',
                },
                pin: {
                    value: '',
                    status: '',
                    message: '',
                },
            },
            contactCode: this.getAreaCodeObj(this.user.contact || ''), // 银行预留手机号区号
            activeForm: null,
            isContactActive: false,
            isSubmitSuccess: false,
            showPin: true,
            authWays: [],
            faildLogs: [],
            hasHoldCardPic: false,
            hasManualed: false,
        };
    },
    computed: {
        degradeToManual() {
            return this.authWays.includes('MANUAL');
        },
        showOtherAuthWays() {
            // 除了当前正在操作的认证，还有其他认证则显示其他认证入口
            let authWays = this.$qUtils.deepClone(this.authWays);
            let index = authWays.findIndex(way => way === 'BANK');
            if (index > -1) {
                authWays.splice(index, 1);
            }
            if (this.$appointAuthWay === 'BANK' && this.faildLogs.length === 0) {
                return false;
            }

            let canManual = !this.hasManualed && this.degradeToManual && !this.hasHoldCardPic;
            if (canManual) {
                return true;
            }
            if (!canManual && authWays.every(item => item === 'MANUAL')) {
                return false;
            }
            return authWays.length !== 0;
        },
        bankNoDisabled() {
            //有默认值且没有可修改字段
            return this.hasDefaultBankNo && !this.modifyFieldList.includes('BANKNO');
        },
        // 是否禁用银行预留手机号,优先根据modifyFields判断
        bankContactDisabled() {
            //用邮箱就不禁用
            if (this.bankContactIsUseEmail) {
                return false;
            }
            return !this.modifyFieldList.includes('BANKMOBILE') && !this.canEditBankPhone && this.hasDefaultBankMobile;
        },
        submitDisable() {
            //* 提交成功后
            if (this.isSubmitSuccess) {
                return true;
            }

            return Object.keys(this.info).some(key => {
                if (key !== 'pin' && !this.info[key].value) {
                    return true;
                }
                if (key === 'pin' && this.showPin && !this.info[key].value) {
                    return true;
                }
                if (this.info[key].status === 'error') {
                    return true;
                }
                return false;
            });
        },
        bankContactIsUseEmail() {
            let result = this.$qUtils.validata({
                value: this.user.contact,
                noValNotValid: false,
                rules: [{ rule: this.$qUtils.validator.isEmail }],
            });
            return !result.isInvalid;
        },
        countEnable() {
            if (this.countNumber === 0) {
                return true;
            }
            if (this.countNumber === null) {
                return true;
            }
            return false;
        },
        countDown() {
            if (this.countNumber === null) {
                return '点击获取';
            }
            if (this.countNumber === 0) {
                return '重新获取';
            }
            return `${this.countNumber}s`;
        },

        hasDefaultBankNo() {
            return !!this.staticInfo.bankNo;
        },
        hasDefaultBankMobile() {
            return !!this.staticInfo.contact;
        },
    },
    async created() {
        this.activeForm = this.$qUtils.debounce(id => {
            if (id === 'contact') {
                this.isContactActive = this.$refs[id] && this.$refs[id].isActive;
            }
        }, 100);

        await this.judgeManualed();
        await this.judgeCardPictureType();

        await this.$getAppointAuthWay();
        this.authWays = await this.$getAuthWays();

        const { result } = await this.postForm('', {
            requestData: JSON.stringify({
                type: 'GET_AUTH_LOG',
                data: {
                    userId: this.$_qyssdk_userId,
                },
            }),
        });
        this.faildLogs = result;
    },
    mounted() {
        document.addEventListener('touchstart', this.bindEvent, false);
    },
    beforeDestroy() {
        document.removeEventListener('touchstart', this.bindEvent, false);
        this.cleanInterval();
    },
    methods: {
        async judgeManualed() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_PASS_BEFORE',
                    data: {
                        userId: this.$_qyssdk_userId,
                        name: this.user.username,
                        cardNo: this.user.cardNo,
                    },
                }),
            });
            this.hasManualed = res.result;
        },
        async judgeCardPictureType() {
            try {
                const res = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_AUTH_IMAGE_EXIST',
                        data: {
                            userId: this.$_qyssdk_userId,
                            name: this.user.username,
                            cardNo: this.user.cardNo,
                        },
                    }),
                });
                this.hasHoldCardPic = res.handfrontImage;
            } catch (error) {
                console.log(error);
            }
        },
        contactInput(contact) {
            const contactLen = {
                8: 'HK',
                9: 'TW',
                10: 'TW',
                11: 'ZH',
            };
            let key = `${contact.length}`;
            if (!contactLen.hasOwnProperty(key) || contactLen[key] !== this.contactCode.areaAbbr) {
                this.showPin = true;
                return;
            }
            let bankContact = this.getContactCodeAndContent(this.user.mobile || '').contactContent;
            if (contact === bankContact) {
                this.showPin = false;
            } else {
                this.showPin = true;
            }
        },
        openAuthWaysModal() {
            const instance = new Vue({
                components: { authWaysModal },
                render: h =>
                    h(authWaysModal, {
                        props: {
                            root: this,
                            user: this.user,
                            otherModes: this.otherModes,
                            alipayInstalled: this.alipayInstalled,
                            livinessAccessed: this.livinessAccessed,
                            channel: this.channel,
                            useOtherFrom: 'BANK',
                            step: this.step,
                        },
                    }),
            }).$mount();
            document.body.appendChild(instance.$el);
        },
        inputStatus(status) {
            if (status === 'error') {
                return 'ui-input-error';
            }
            if (status === 'warning') {
                return 'ui-input-warning';
            }
        },
        tipStaus(status) {
            if (status === 'error') {
                return 'error-tips';
            }
            if (status === 'warning') {
                return 'warning-tips';
            }
        },
        bindEvent() {
            this.activeForm('contact');
        },

        // 获取验证码
        async sendCode() {
            if (!this.countEnable) {
                return;
            }
            if (!this.info.contact.value) {
                this.$message('请先输入手机号');
                return false;
            }

            this.countNumber = 60;
            this.interval = setInterval(() => {
                if (this.countNumber === 0) {
                    this.cleanInterval();
                } else {
                    this.countNumber--;
                }
            }, 1000);

            try {
                await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_AUTH_PIN',
                        data: {
                            userId: this.$_qyssdk_userId,
                            mobile: this.getContactWithCode(this.contactCode.value, this.info.contact.value),
                        },
                    }),
                });
            } catch (e) {
                this.countNumber = 0;
                console.log(e);
            }
        },
        cleanInterval() {
            this.interval && clearInterval(this.interval);
        },
        async submit() {
            ['bankNo', 'contact'].forEach(item => {
                this.checkData(item);
            });
            if (this.submitDisable) {
                return;
            }
            let loading = this.$loading('正在提交');
            try {
                let params = this.formateInfo();
                await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'UPLOAD_BANK_MATERIAL',
                        data: {
                            userId: this.$_qyssdk_userId,
                            material: params,
                            pin: params.pin,
                        },
                    }),
                });
                this.isSubmitSuccess = true;
                loading.close();
                const { nextStep } = await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'BANK_AUTH_VERIFY',
                            data: {
                                userId: this.$_qyssdk_userId,
                            },
                        }),
                    },
                    {
                        headers: {
                            noMes: 'true',
                        },
                    }
                );
                delete params.pin;
                this.user = Object.assign(this.user, params);
                if (nextStep === 'END') {
                    this.dealStep('SUCCESS');
                    return;
                }
                this.dealStep(nextStep);
            } catch (error) {
                loading.close();
                console.log(error);
            }
        },
        formateInfo() {
            let params = {};
            Object.keys(this.info).forEach(key => {
                if (key === 'contact') {
                    params[key] = this.getContactWithCode(this.contactCode.value, this.info[key].value);
                    return;
                }
                params[key] = this.info[key].value;
            });
            ['username', 'paperType', 'cardNo', 'mobile'].forEach(key => {
                params[key] = this.user[key];
            });
            return params;
        },
        checkData(id) {
            let target = this.info[id].value;
            let result = {};

            switch (id) {
                case 'bankNo':
                    target = this.trimData(id);
                    result = this.$qUtils.validata({
                        value: target,
                        rules: [
                            {
                                rule: this.$qUtils.validator.isCreditCard,
                                level: 'error',
                                message: '银行卡号格式不正确',
                            },
                        ],
                    });
                    break;
                case 'contact':
                    target = this.trimData(id);
                    let regKey = `is${this.contactCode.areaAbbr}Phone`;
                    result = this.$qUtils.validata({
                        value: target,
                        rules: [
                            { rule: this.$qUtils.validator[regKey], level: 'error', message: '手机号码格式不正确' },
                        ],
                    });
                    break;
            }

            if (result.isInvalid) {
                this.info[id].status = result.status;
                this.info[id].message = result.message;
            } else {
                this.info[id].status = '';
                this.info[id].message = '';
            }
        },

        // 获取手机号码区号
        getAreaCode(codeItem, type) {
            if (type === 'contact') {
                this.contactCode = codeItem;
            }
        },
        toggleSelectCode(id) {
            // 解决ios不可聚焦的问题--会导致页面闪动
            this.$refs[id].focus();
        },
        trimData(id) {
            let val = this.info[id].value;
            if (val) {
                val = val.replace(/\s/g, '');
                this.info[id].value = val;
            }
            return val;
        },
    },
};
</script>
<style lang="less" scoped>
.bank-wrapper {
    width: 100%;
    .bank-content {
        padding: 0 20px;
        .tips {
            width: calc(~'100% - 40px');
            position: fixed;
            bottom: 16px;
            text-align: center;
        }
    }
    .clear-icon {
        font-size: 14px;
        color: #e5e7ea;
        bottom: 8px;
    }

    .code-wrap {
        position: relative;
        .form-item-code {
            width: 100%;
            height: 68px;
            .certify-code-con {
                .ui-textbox__input {
                    padding-right: 100px !important;
                }
            }
            .tips-tel {
                .icon-succ {
                    font-size: 14px;
                    color: @success-color;
                    margin-right: 5px;
                }
            }
        }
        .get-code-btn {
            position: absolute;
            top: 24px;
            right: 0;
            padding-left: 12px;
            line-height: 16px;
            border-left: 1px solid #e5e7ea;
            min-width: 83px;
            text-align: center;
            &.disabled {
                color: #c1c6cd;
            }
        }
    }

    .submit-btn {
        width: 100%;
        margin-bottom: 20px;
    }
}
</style>
