<template>
    <button
        type="button"
        class="auth-button"
        :class="{
            [type]: !!type,
            disabled: disabled,
        }"
        v-on="listeners"
    >
        <span class="middle-center">
            <slot></slot>
        </span>
    </button>
</template>

<script>
export default {
    name: 'AuthButton',
    props: {
        type: {
            type: String,
            default: '',
        },
        disabled: {
            type: Boolean,
            default: false,
        },
        debounce: {
            type: Number,
            default: 0,
        },
    },
    data() {
        return {
            debounceFn: null,
        };
    },
    computed: {
        listeners() {
            return Object.assign({}, this.$listeners, {
                click: event => {
                    if (!!this.debounce) {
                        this.debounceFn(event);
                    } else {
                        this.emitEvent(event);
                    }
                },
            });
        },
    },
    created() {
        this.debounceFn = this.$qUtils.debounce(ev => {
            this.emitEvent(ev);
        }, 300);
    },
    methods: {
        emitEvent(event) {
            if (!this.disabled) {
                this.$emit('click', event);
            }
        },
    },
};
</script>
<style lang="less" scoped>
.auth-button {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 44px;
    border: none;
    outline: none;
    border-radius: 4px;
    font-size: 16px;
    width: 100%;
    &.primary {
        background: @primary-color;
        color: white;
    }
    &.disabled {
        background: rgba(193, 198, 205, 0.4);
        color: #c1c6cd;
    }
    &.unfill {
        background: white;
        border: 1px solid #1078f1;
        color: #1078f1;
    }
}
</style>
