<template>
    <div class="default-certify-code">
        <div class="large-title title">输入验证码</div>
        <div class="tip-large body-color">
            验证码已发送至：
            <span class="tip-large link-color">
                <template v-if="isUseMail">
                    {{ mobile }}
                </template>
                <template v-else>
                    {{ mobile | plusPrefix | mobileRender }}
                </template>
            </span>
        </div>

        <div class="code">
            <div
                class="code-item-con"
                :class="{ 'is-focus': inputFocus && hackCode.length === index }"
                v-for="(item, index) in code"
                :key="item.id"
                @click="manualFocus"
            >
                <span class="code-item">
                    {{ item.value }}
                </span>
            </div>
            <input
                type="tel"
                @focus="inputFocus = true"
                @blur="inputFocus = false"
                id="code1"
                class="hack-input"
                maxlength="6"
                v-model="hackCode"
            />
        </div>
        <div class="btn-con">
            <span
                @click="isResendActive ? resend() : ''"
                :class="['tip-large', 'disable-color', { 'resend-active': isResendActive }]"
            >
                重新发送验证码{{ isResendActive ? '' : `（${countDown}s）` }}
            </span>
            <span
                v-if="isVoiceActive"
                @click="isVoiceActive ? byVoice() : ''"
                class="tip-large disable-color voice-btn"
            >
                收不到短信？试试语音验证码
            </span>
        </div>

        <div v-if="showOtherAuthWays" class="tips">
            <span class="tip-large">手机号已不用？</span>
            <span class="tip-large link-color" @click="openAuthWaysModal">尝试其他认证方式</span>
        </div>
    </div>
</template>

<script>
import authWaysModal from '../auth-common/auth-ways.vue';
export default {
    name: 'DefaultCertifyCode',
    filters: {
        mobileRender(mobile) {
            let result = '';
            if (!mobile.includes(' ')) {
                result = mobile.substring(0, 3) + '*'.repeat(4) + mobile.substr(-4);
            } else {
                let areaCode = `${mobile.split(' ')[0]} `;
                let content = mobile.split(' ')[1];
                // 手机号格式：展示除区号部分的前3位后两位，其他的用*代替
                let repeatTimes = content.length - 5;
                result = areaCode + content.substring(0, 3) + '*'.repeat(repeatTimes) + content.substr(-2);
            }
            return result;
        },
    },
    props: {
        user: {
            type: Object,
            default: () => {
                return {};
            },
        },
        otherModes: {
            type: String,
            default: '',
        },
        channel: {
            type: String,
            default: '',
        },
        root: {
            type: Object,
        },
        mobile: {
            type: String,
        },
        isUseMail: {
            default: false,
        },
        accountPrecheck: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            code: [
                { id: 1, value: '' },
                { id: 2, value: '' },
                { id: 3, value: '' },
                { id: 4, value: '' },
                { id: 5, value: '' },
                { id: 6, value: '' },
            ],
            hackCode: '',
            interval: null,
            countDown: 60,
            inputFocus: false,
            step: '',
            authWays: [],
            hasHoldCardPic: false, // 是否有手持证件照
            hasManualed: false,
            faildLogs: [],
        };
    },
    computed: {
        isResendActive() {
            return this.countDown === 0;
        },
        isVoiceActive() {
            return this.countDown <= 30;
        },
        degradeToManual() {
            return this.authWays.includes('MANUAL');
        },
        showOtherAuthWays() {
            if (this.accountPrecheck) {
                return false;
            }
            // 除了当前正在操作的认证，还有其他认证则显示其他认证入口
            let authWays = this.$qUtils.deepClone(this.authWays);
            let index = authWays.findIndex(way => way === 'IVS');
            if (index > -1) {
                authWays.splice(index, 1);
            }

            // 指定默认方式，未失败过不展示人工
            let canManual = !this.hasManualed && this.degradeToManual && !this.hasHoldCardPic;
            if ((this.$appointAuthWay === 'DEFAULT' && this.faildLogs.length === 0) || !canManual) {
                let manualIndex = authWays.findIndex(way => way === 'MANUAL');
                if (manualIndex > -1) {
                    authWays.splice(index, 1);
                }
            }

            return authWays.length !== 0;
        },
    },
    watch: {
        code: {
            async handler(arr) {
                if (arr.every(item => !!item.value)) {
                    let { username, cardNo, mobile, paperType } = this.user;
                    let pin = '';
                    let loading = this.$loading('提交验证中...');
                    this.code.forEach(item => {
                        pin += item.value;
                    });
                    try {
                        let params = {
                            username,
                            cardNo,
                            mobile,
                            pin,
                            paperType,
                        };
                        const { result } = await this.postForm('', {
                            requestData: JSON.stringify({
                                type: 'GET_AUTH_PINVALIDATE',
                                data: {
                                    userId: this.$_qyssdk_userId,
                                    mobile,
                                    pin,
                                },
                            }),
                        });
                        if (this.accountPrecheck) {
                            if (result.result) {
                                this.dealStep('INFO');
                            } else {
                                this.$message('手机号验证失败');
                            }
                        } else {
                            await this.postForm('', {
                                requestData: JSON.stringify({
                                    type: 'UPLOAD_IVS_MATERIAL',
                                    data: {
                                        userId: this.$_qyssdk_userId,
                                        material: params,
                                        pin: params.pin,
                                    },
                                }),
                            });
                            await this.verify();
                        }
                    } catch (error) {
                        console.log(error);
                    } finally {
                        loading.close();
                    }
                }
            },
            deep: true,
        },
        hackCode(nv) {
            this.code.forEach((item, index) => {
                item.value = nv[index] || '';
            });
        },
    },
    async created() {
        const res = await this.postForm('', {
            requestData: JSON.stringify({
                type: 'GET_AUTH_DETAIL',
                data: {
                    userId: this.$_qyssdk_userId,
                },
            }),
        });
        const { result } = this.decryptoData(res.result);
        this.step = result.step;

        await this.$getAppointAuthWay();
        this.authWays = await this.$getAuthWays();

        // 判断证件照类型
        await this.judgeCardPictureType();
        await this.judgeManualed();

        const { result: logs } = await this.postForm('', {
            requestData: JSON.stringify({
                type: 'GET_AUTH_LOG',
                data: {
                    userId: this.$_qyssdk_userId,
                },
            }),
        });
        this.faildLogs = logs;
    },
    mounted() {
        this.$nextTick(() => {
            this.manualFocus();
        });
        this.cleanInterval();
        this.startInterval();
    },
    beforeDestroy() {
        this.cleanInterval();
    },
    methods: {
        async judgeManualed() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_PASS_BEFORE',
                    data: {
                        userId: this.$_qyssdk_userId,
                        name: this.user.username,
                        cardNo: this.user.cardNo,
                    },
                }),
            });
            this.hasManualed = res.result;
        },
        async judgeCardPictureType() {
            try {
                const res = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_AUTH_IMAGE_EXIST',
                        data: {
                            userId: this.$_qyssdk_userId,
                            name: this.user.username,
                            cardNo: this.user.cardNo,
                        },
                    }),
                });
                this.hasHoldCardPic = res.handfrontImage;
            } catch (error) {
                console.log(error);
            }
        },
        openAuthWaysModal() {
            const instance = new Vue({
                components: { authWaysModal },
                render: h =>
                    h(authWaysModal, {
                        props: {
                            root: this,
                            user: this.user,
                            otherModes: this.otherModes,
                            channel: this.channel,
                            useOtherFrom: 'IVS',
                            step: this.step,
                        },
                    }),
            }).$mount();
            document.body.appendChild(instance.$el);
        },
        startInterval() {
            this.interval = setInterval(() => {
                if (this.countDown > 0) {
                    this.countDown--;
                }
            }, 1000);
        },
        cleanInterval() {
            this.interval && clearInterval(this.interval);
        },
        manualFocus() {
            document.querySelector('.hack-input').focus();
        },
        resend() {
            this.countDown = 60;
            this.cleanInterval();
            this.startInterval();
            this.sendPin();
        },
        async sendPin() {
            try {
                const { code } = await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'GET_AUTH_PIN',
                            data: {
                                userId: this.$_qyssdk_userId,
                                mobile: this.mobile,
                            },
                        }),
                    },
                    {
                        headers: {
                            noMes: 'true',
                        },
                    }
                );
                if (code === 0) {
                    this.$message('发送成功');
                }
            } catch (error) {
                console.log(error);
            }
        },
        async byVoice() {
            try {
                const res = await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'GET_AUTH_VOICE',
                            data: {
                                userId: this.$_qyssdk_userId,
                                mobile: this.mobile,
                            },
                        }),
                    },
                    {
                        headers: {
                            noMes: 'true',
                        },
                    }
                );
                if (res.code === 0) {
                    this.$message('发送成功');
                }
            } catch (error) {
                console.log(error);
            }
        },
        async verify() {
            try {
                const res = await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'PHONE_AUTH_VERIFY',
                            data: {
                                userId: this.$_qyssdk_userId,
                            },
                        }),
                    },
                    {
                        headers: {
                            noMes: 'true',
                        },
                    }
                );
                if (res.code === 0 && res.nextStep === 'END') {
                    this.dealStep('SUCCESS');
                    return;
                }
                this.dealStep(res.nextStep);
            } catch (error) {
                console.log(error);
            }
        },
        dealStep(step) {
            this.root.dealStep(step);
            this.close();
        },
        close() {
            this.$destroy(true);
            this.$el.parentNode.removeChild(this.$el);
        },
    },
};
</script>
<style lang="less" scoped>
.default-certify-code {
    position: fixed !important;
    top: 0;
    left: 0;
    background: white;
    width: 100%;
    height: 100%;
    z-index: 100;
    padding: 30px 20px 0;
    .title {
        margin-bottom: 8px;
    }
    .code {
        margin-top: 46px;
        width: 100%;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
        position: relative;
        .code-item-con {
            width: calc(~'(100% - 75px) / 6');
            height: 100%;
            border-bottom: 1px solid #e5e7ea;
            position: relative;
            .code-item {
                font-family: 'Roboto';
                display: inline-block;
                width: 100%;
                background: transparent;
                border: none;
                outline: none;
                text-align: center;
                padding: 7px 0;
                font-size: 40px;
                font-weight: bold;
                overflow: hidden;
            }
            &.is-focus {
                &:after {
                    content: '|';
                    font-size: 22px;
                    animation: blink 1s infinite;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
            @keyframes blink {
                0%,
                to {
                    color: #fff;
                }

                50% {
                    color: #3c76ff;
                }
            }
        }
        .hack-input {
            position: absolute;
            opacity: 0;
            // transform: scale(0);
            top: 0;
            right: 0;
            z-index: 1000;
            width: 100%;
            height: 60px;
        }
    }
    .btn-con {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .voice-btn {
        color: #c1c6cd;
    }
    .resend-active {
        color: @primary-color;
    }
    .tips {
        width: calc(~'100% - 40px');
        position: fixed;
        bottom: 16px;
        text-align: center;
    }
}
</style>
