<template>
    <div ref="outerWrapper" class="info-wrapper">
        <auth-subheader v-if="!idCardImg" :account="user.mobile"></auth-subheader>

        <div v-if="idCardImg" class="card-con">
            <img class="card-img" :src="idCardImg" />
            <span class="tip-large"
                >信息识别有误？直接在下方修改 或
                <span @click="chooseIdCard" class="tip-large link-color">重新上传</span></span
            >
        </div>

        <div class="content-wrapper">
            <div v-if="idCardImg" class="h3-title title-color title">识别结果</div>

            <div class="form-item">
                <div class="input-wrap" :class="`ui-input-${info.username.status}`">
                    <ui-textbox
                        floating-label
                        id="username"
                        ref="username"
                        label="姓名"
                        v-model="info.username.value"
                        iconPosition="right"
                        :disabled="isUsernameDisabled"
                        @focus="inpFocus"
                        @blur="
                            checkData('username');
                            handleIosScroll();
                        "
                        :enforceMaxlength="true"
                        :maxlength="50"
                    >
                        <q-icon
                            class="clear-icon"
                            slot="icon"
                            v-if="!!info.username.value && $refs.$get('username', 'isActive')"
                            @touchstart="info.username.value = ''"
                            icon="close-circle-fill"
                        ></q-icon>
                    </ui-textbox>
                </div>
                <span v-if="info.username.status" class="ui-textbox-tips" :class="`${info.username.status}-tips`">{{
                    info.username.message
                }}</span>
            </div>

            <div
                v-if="!idCardImg"
                class="form-item"
                @click="(isAbortCardDisabled && isIdCard) || !hasManual ? '' : (cardTypeVisible = true)"
            >
                <div class="input-wrap" :class="`ui-input-${info.paperType.status}`">
                    <ui-textbox
                        floating-label
                        id="paperType"
                        ref="paperType"
                        label="证件类型"
                        v-model="info.paperType.label"
                        iconPosition="right"
                        :readonly="true"
                        :disabled="(isAbortCardDisabled && isIdCard) || !hasManual"
                    >
                        <q-icon slot="icon" style="font-size: 18px;" class="caret-down-icon" icon="caret-down"></q-icon>
                    </ui-textbox>
                </div>
                <span v-if="info.paperType.status" class="ui-textbox-tips" :class="`${info.paperType.status}-tips`">{{
                    info.paperType.message
                }}</span>
            </div>

            <div class="form-item">
                <div class="input-wrap" :class="`ui-input-${info.cardNo.status}`">
                    <ui-textbox
                        floating-label
                        id="cardNo"
                        ref="cardNo"
                        label="证件号"
                        v-model="cardNoShow"
                        iconPosition="right"
                        @focus="inpFocus"
                        @blur="
                            checkData('cardNo');
                            handleIosScroll();
                        "
                        @keydown="syncCardNo"
                        :disabled="isAbortCardDisabled"
                    >
                        <q-icon
                            v-if="info.paperType.value === 'IDCARD' && !idCardImg"
                            @click="isAbortCardDisabled ? '' : chooseIdCard()"
                            slot="icon"
                            class="idcard-icon"
                            icon="photorecognition"
                        ></q-icon>
                        <q-icon
                            v-show="idCardImg"
                            class="clear-icon"
                            slot="icon"
                            v-if="info.cardNo.value && $refs.$get('cardNo', 'isActive')"
                            @touchstart="info.cardNo.value = ''"
                            icon="close-circle-fill"
                        ></q-icon>
                    </ui-textbox>
                </div>
                <span v-if="info.cardNo.status" class="ui-textbox-tips" :class="`${info.cardNo.status}-tips`">{{
                    info.cardNo.message
                }}</span>
            </div>

            <div class="privacy-protocol">
                <auth-checkbox :checked.sync="agreeChecked" style="font-size: 15px;">
                    <span class="main-word disable-color" style="margin-left: 3px;">我已阅读并同意</span>
                </auth-checkbox>
                <span class="main-word link-color" @click="viewPrivacy">《隐私保护协议》</span>
            </div>
            <auth-button @click="nextStep" class="next-step-btn" type="primary" :disabled="isDisabled" :debounce="200"
                >下一步</auth-button
            >
        </div>

        <!-- 证件类型选择器 -->
        <mt-popup v-if="cardTypeVisible" class="select-popup" v-model="cardTypeVisible" position="bottom">
            <div
                class="type-item h3-title regular-color"
                :class="{ 'is-active': info.paperType.value === type.value }"
                v-for="type in types"
                :key="type.value"
                @click="selectType(type)"
            >
                {{ type.label }}
            </div>
            <auth-button class="cancel-select-btn h3-title link-color" type="unfill" @click="closeSelect"
                >取消</auth-button
            >
        </mt-popup>
    </div>
</template>

<script>
import privacyProtection from './auth-common/privacy-protection.vue';
import authWaysModal from './auth-common/auth-ways.vue';

export default {
    name: 'DefaultAuth',
    props: {
        user: {
            type: Object,
            default: () => {
                return {};
            },
        },
        staticInfo: {
            type: Object,
            default: () => {
                return {};
            },
        },
        auth: {
            type: Boolean,
            default: true,
        },
        channel: {
            type: String,
            default: '',
        },
        reverifyCert: {
            type: String,
            default: '',
        },
        modifyFieldList: {
            type: Array,
            default: () => [],
        },
        customizedPrivacyAgreement: String,
        accountPrecheck: {
            type: Boolean,
            default: false,
        },
        mode: {
            type: String,
            default: '',
        },
        otherModes: {
            type: String,
            default: '',
        },
        step: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            agreeChecked: false,
            info: {
                username: {
                    value: this.user.username || '',
                    status: '',
                    message: '',
                },
                paperType: {
                    label: '',
                    value: this.user.paperType || 'IDCARD',
                    status: '',
                    message: '',
                },
                cardNo: {
                    value: this.user.cardNo || '',
                    status: '',
                    message: '',
                },
            },
            cardNoShow: '',
            types: [
                { label: '身份证', value: 'IDCARD' },
                { label: '护照', value: 'PASSPORT' },
                { label: '港澳通行证', value: 'HKMP' },
                { label: '台胞证', value: 'MTPS' },
            ],
            cardTypeVisible: false,
            isCurrentFormActive: false,
            updatedInfo: {},
            idCardImg: '',
            isCanScroll: false,
            authWays: [],
            isUseMail: false,
            loadingData: true,
        };
    },
    computed: {
        authAccount() {
            let contactContent = this.getContactCodeAndContent(this.user.mobile || '').contactContent || '';
            let contactCode = this.getAreaCodeObj(this.user.mobile || '').value;
            if (this.isUseMail) {
                return contactContent;
            }
            return this.getContactWithCode(contactCode, contactContent);
        },
        isDisabled() {
            let result = false;
            if (this.loadingData) {
                return true;
            }
            for (let k in this.info) {
                if (!this.info[k].value || this.info[k].status === 'error') {
                    result = true;
                    break;
                }
            }
            return result;
        },
        // 是否禁用用户名
        isUsernameDisabled() {
            return (
                !this.modifyFieldList.includes('NAME') && ((this.auth && this.hasDefaultUsername) || this.isFromSaas)
            );
        },
        // 是否禁用身份证号
        isAbortCardDisabled() {
            return !this.modifyFieldList.includes('CARDNO') && this.auth && this.hasDefaultCardNo && !this.isFromSaas;
        },
        isIdCard() {
            let result = this.$qUtils.validata({
                value: this.user.cardNo,
                noValNotValid: false,
                rules: [{ rule: this.$qUtils.validator.isIdCard }],
            });
            return !result.isInvalid;
        },
        // 是否是契约锁的代理商
        isFromSaas() {
            return this.channel == 'SAAS_PC';
        },
        hasDefaultUsername() {
            return !!this.staticInfo.username;
        },
        hasDefaultCardNo() {
            return !!this.staticInfo.cardNo;
        },
        // 是否有人工审核，无人工审核则证件类型只能选择身份证
        hasManual() {
            return this.authWays.includes('MANUAL');
        },
    },
    watch: {
        'info.paperType.value': {
            handler(type) {
                this.checkData('cardNo');
            },
            deep: true,
        },
        isCanScroll(value) {
            if (value) {
                this.$nextTick(() => {
                    this.$refs.outerWrapper.scrollIntoView();
                });
            }
        },
        cardNoShow(nv) {
            let value = this.info.cardNo.value.split('');
            value.length = nv.length;
            nv.split('').forEach((str, index) => {
                if (str === '*' && value[index] && value[index] !== ' ') {
                    return;
                }
                value[index] = str;
            });
            this.info.cardNo.value = value.join('');
            if (this.cardNoShow.length == 18) {
                this.checkData('cardNo');
            }
        },
    },
    async created() {
        try {
            await this.$getAppointAuthWay();
            this.authWays = await this.$getAuthWays();

            await this.captchaPrepare();

            this.initPaperType();

            // 信息回显最新一次提交的信息
            await this.initData();

            this.isUseMail = !this.$qUtils.validata({
                value: this.user.mobile,
                noValNotValid: false,
                rules: [{ rule: this.$qUtils.validator.isEmail }],
            }).isInvalid;
        } catch (e) {
            console.log(e);
        } finally {
            this.loadingData = false;
        }
    },
    methods: {
        async initData() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_DETAIL',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            const { result } = this.decryptoData(res.result);
            Object.keys(this.info).forEach(key => {
                if (key === 'paperType') {
                    this.info[key].value = result.authInfo[key] || 'IDCARD';
                    this.info[key].label = this.types.find(
                        item => item.value === (result.authInfo[key] || 'IDCARD')
                    ).label;
                } else {
                    this.info[key].value = result.authInfo[key] || '';
                }
            });
            let cardNo = result.$get('authInfo', 'cardNo');
            if (cardNo) {
                this.cardNoShow = cardNo[0] + '*'.repeat(cardNo.length - 2) + cardNo.slice(-1);
            }
        },
        initPaperType() {
            let paperTypeText = this.cardTypeLabel();
            // 无人工审核只能选择身份证
            if (!this.hasManual) {
                paperTypeText = '身份证';
                this.info.paperType.value = 'IDCARD';
                if (!this.$qUtils.validator.isIdCard.test(this.info.cardNo.value)) {
                    this.info.cardNo.value = '';
                }
            }
            this.info.paperType.label = paperTypeText;
        },
        syncCardNo(event) {
            if (event.keyCode == 8) {
                let offset = event.target.selectionStart;
                let value = this.info.cardNo.value;
                this.info.cardNo.value = value.slice(0, offset - 1) + value.slice(offset);
            } else if (event.keyCode == 46) {
                let offset = event.target.selectionStart;
                let value = this.info.cardNo.value;
                this.info.cardNo.value = value.slice(0, offset) + value.slice(offset + 1);
            } else if (
                (event.keyCode >= 65 && event.keyCode <= 90) ||
                (event.keyCode >= 48 && event.keyCode <= 57) ||
                (event.keyCode >= 96 && event.keyCode <= 105)
            ) {
                let offset = event.target.selectionStart;
                let value = this.info.cardNo.value;
                this.info.cardNo.value = `${value.slice(0, offset)} ${value.slice(offset)}`;
            }
        },
        viewPrivacy() {
            const vueOptions = {
                components: { privacyProtection },
                render: h =>
                    h(privacyProtection, {
                        props: {
                            customizedPrivacyAgreement: this.customizedPrivacyAgreement,
                        },
                    }),
            };
            let elm = new Vue(vueOptions).$mount();
            document.body.appendChild(elm.$el);
        },
        closeSelect() {
            this.cardTypeVisible = false;
        },
        selectType(type) {
            Object.assign(this.info.paperType, type);
            this.closeSelect();
        },
        async nextStep() {
            if (!this.triggerCheckData()) {
                return;
            }
            if (!this.agreeChecked) {
                this.$message('请先阅读并同意服务协议');
                return;
            }
            this.updateInfo();

            let loading = this.$loading('正在提交');
            try {
                const res = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_AUTH_IMAGE_EXIST',
                        data: {
                            userId: this.$_qyssdk_userId,
                            name: this.info.username.value,
                            cardNo: this.info.cardNo.value,
                        },
                    }),
                });

                // (非大陆用户直接跳转人工审核 && 无证件照) 跳转人工
                if (this.info.paperType.value !== 'IDCARD' && !res.faceImage && !res.handfrontImage) {
                    this.dealStep('MANUALINFO');
                    return;
                }
                // 非大陆用户直接跳转人工审核 && 有证件照 跳转人脸对比
                if (this.info.paperType.value !== 'IDCARD' && (res.faceImage || res.handfrontImage)) {
                    await this.submitInfo();
                    this.dealStep('FACEID');
                    return;
                }

                if (this.$appointAuthWay === 'FACE') {
                    await this.submitInfo();
                    this.dealStep('FACEID');
                    return;
                }

                // 员工编号实名认证
                if (!this.user.mobile || this.isUseMail) {
                    await this.submitInfo();
                    const next = {
                        IVS: 'threeElementsAuth',
                        BANK: 'BANKINFO',
                    };
                    if (['DEFAULT', 'FACE'].includes(this.$appointAuthWay)) {
                        this.openAuthWaysModal();
                    } else {
                        this.dealStep(next[this.$appointAuthWay]);
                    }
                    return;
                }

                // 认证前验证过手机号，则三要素无需再次获取验证码
                if (this.accountPrecheck && this.$appointAuthWay === 'IVS') {
                    this.submitInfoWithoutCode();
                    return;
                }

                // 未指定 || 之前指定的DEFAULT || 指定人脸 都让用户选择认证方式
                if (
                    this.$appointAuthWay === '' ||
                    this.$appointAuthWay === 'DEFAULT' ||
                    this.$appointAuthWay === 'FACE'
                ) {
                    this.submitInfoWithoutCode(false);
                    this.openAuthWaysModal();
                    return;
                }

                if (this.$appointAuthWay === 'IVS') {
                    this.submitInfoWithoutCode();
                    return;
                }
                if (this.$appointAuthWay === 'BANK') {
                    this.dealStep('BANKINFO');
                    return;
                }
            } catch (error) {
                console.log(error);
            } finally {
                loading.close();
            }
        },
        async submitInfo() {
            let loading = this.$loading('验证中...');

            try {
                await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'UPLOAD_FACE_MATERIAL',
                        data: {
                            userId: this.$_qyssdk_userId,
                            material: {
                                username: this.info.username.value,
                                paperType: this.info.paperType.value,
                                cardNo: this.info.cardNo.value,
                            },
                        },
                    }),
                });
            } catch (error) {
                console.log(error);
            } finally {
                loading.close();
            }
        },
        async submitInfoWithoutCode(verify = true) {
            let loading = this.$loading('提交验证中...');
            let params = {
                username: this.info.username.value,
                paperType: this.info.paperType.value,
                cardNo: this.info.cardNo.value,
                mobile: this.user.mobile,
            };
            try {
                await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'UPLOAD_IVS_MATERIAL',
                            data: {
                                userId: this.$_qyssdk_userId,
                                material: params,
                            },
                        }),
                    },
                    {
                        headers: {
                            'reverify-certificate': this.reverifyCert,
                        },
                    }
                );
                if (!verify) {
                    return;
                }
                await this.verify();
            } catch (error) {
                console.log(error);
            } finally {
                loading.close();
            }
        },
        async verify() {
            try {
                const res = await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'PHONE_AUTH_VERIFY',
                            data: {
                                userId: this.$_qyssdk_userId,
                            },
                        }),
                    },
                    {
                        headers: {
                            noMes: 'true',
                        },
                    }
                );
                if (res.code === 0 && res.nextStep === 'END') {
                    this.dealStep('SUCCESS');
                    return;
                }
                this.dealStep(res.nextStep);
            } catch (error) {
                console.log(error);
            }
        },
        updateInfo() {
            let updatedInfo = {};
            for (let k in this.info) {
                updatedInfo[k] = this.info[k].value;
            }
            this.user = Object.assign(this.user, updatedInfo);
        },
        async chooseIdCard() {
            this.initFormStatus();
            const e = await this.$qUtils.chooseFile({
                accept: 'image/*',
            });
            this.uploadIdCardPic(e);
        },
        async uploadIdCardPic(e) {
            try {
                const blob = await this.uploadImageHandler(e);
                if (!blob) {
                    return;
                }
                this.idCardImg = window.URL.createObjectURL(blob);
                const res = await this.postMulti('', {
                    file: blob,
                    requestData: JSON.stringify({
                        type: 'ORC_IDCARD',
                        data: {
                            userId: this.$_qyssdk_userId,
                        },
                    }),
                });
                this.updateFormData(res.result);
                this.triggerCheckData();
            } catch (error) {
                console.log(error);
            }
        },
        triggerCheckData() {
            ['username', 'cardNo'].forEach(item => {
                this.checkData(item);
            });
            if (this.isDisabled) {
                return false;
            }
            return true;
        },
        updateFormData(data) {
            let { username, cardNo } = data;
            let info = this.$qUtils.deepClone(this.info);
            info.username.value = username;
            info.cardNo.value = cardNo;
            this.info = info;
        },
        trimData(id, type = 'all') {
            let val = this.info[id].value;
            if (val) {
                if (type === 'terminal') {
                    val = val.trim();
                } else if (type === 'all') {
                    val = val.replace(/\s/g, '');
                }
                this.info[id].value = val;
            }
            return val;
        },
        checkData(id) {
            let target = this.info[id].value;
            let result = {};

            switch (id) {
                case 'username':
                    if (this.$qUtils.validator.isZHUserName.test(target)) {
                        target = this.trimData(id);
                    } else if (this.$qUtils.validator.isENUserName.test(target)) {
                        target = this.trimData(id, 'terminal');
                    }

                    result = this.$qUtils.validata({
                        value: target,
                        rules: [
                            {
                                rule: this.$qUtils.validator.isZHENUserName,
                                level: 'error',
                                message: '仅允许输入中文或英文',
                            },
                        ],
                    });
                    break;
                case 'cardNo':
                    target = this.trimData(id);
                    if (this.info.paperType.value === 'IDCARD') {
                        result = this.$qUtils.validata({
                            value: target,
                            rules: [
                                {
                                    rule: this.$qUtils.validator.isIdCard,
                                    level: 'error',
                                    message: '证件号码格式不正确',
                                },
                            ],
                        });
                    }
                    break;
            }

            if (result.isInvalid) {
                this.info[id].status = result.status;
                this.info[id].message = result.message;
            } else {
                this.info[id].status = '';
                this.info[id].message = '';
            }
        },
        // 获取手机号码区号
        getAreaCode(codeItem) {
            this.code = codeItem;
        },
        toggleSelectCode(id) {
            // 解决ios不可聚焦的问题
            // setTimeout(() => {
            //     this.$refs[id].focus();
            // });
            this.$refs[id].focus();
        },
        async captchaPrepare() {
            await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_CAPTCHA_INIT',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
        },
        initFormStatus() {
            let info = this.$qUtils.deepClone(this.info);
            for (let k in info) {
                info[k].status = '';
                info[k].message = '';
            }
            this.info = info;
        },
        cardTypeLabel() {
            return this.types.find(item => item.value === (this.user.paperType || 'IDCARD')).label;
        },
        // 处理iOS键盘将页面顶上去，键盘收起页面不会自动下来的问题
        inpFocus(e) {
            this.isCanScroll = false;
        },
        handleIosScroll() {
            this.isCanScroll = true;
        },
        openAuthWaysModal() {
            this.updateInfo();
            const instance = new Vue({
                components: { authWaysModal },
                render: h =>
                    h(authWaysModal, {
                        props: {
                            isModalDisplay: false,
                            root: this,
                            user: this.user,
                            channel: this.channel,
                            reverifyCert: this.reverifyCert,
                            otherModes: this.otherModes,
                            accountPrecheck: this.accountPrecheck,
                            step: this.step,
                        },
                    }),
            }).$mount();
            document.body.appendChild(instance.$el);
        },
    },
};
</script>
<style lang="less" scoped>
.info-wrapper {
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    padding-bottom: 40px;
    .card-con {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        background: url('./assets/img/orc-result-bg.png');
        background-repeat: no-repeat;
        background-size: 100%;
        margin-bottom: 17px;
        padding: 15px 0;
        .card-img {
            width: 260px;
            border-radius: 8px;
            margin-bottom: 9px;
        }
    }
    .content-wrapper {
        width: 100%;
        padding: 0 20px;
        .mobile-icon {
            font-size: 18px;
            color: #7f8997;
        }
        .idcard-icon {
            font-size: 16px;
            color: #bbbbf1;
        }
        .caret-down-icon {
            color: #c1c6cd;
            font-size: 16px;
            bottom: 9px;
        }
        .clear-icon {
            font-size: 14px;
            color: #e5e7ea;
            bottom: 8px;
        }
        .privacy-protocol {
            margin: 16px 0 32px;
        }

        .next-step-btn {
            width: 100%;
        }
    }
    .select-popup {
        width: 100%;
        background: @body-background;
        .type-item {
            width: 100%;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-bottom: 1px solid @body-background;
            background: white;
            &.is-active {
                background: @body-background;
                opacity: 0.8;
                color: #001330;
            }
        }
        :nth-last-child(2) {
            border-bottom: none;
