<template>
    <div class="auth-success">
        <img width="222px" src="@assets/img/auth-success.png" />

        <span class="h1-title title">认证成功</span>
        <span
            v-if="(!successPage && channel != 'PRIVAPP' && channel != 'THIRDAPP') || channel === 'CLOUD_PC'"
            class="main-word"
        >
            <!-- 请在【电脑端】刷新页面，继续操作 -->
            <template v-if="mode == 'DEFAULT'">
                您已通过个人实名认证！
            </template>
            <template v-if="mode == 'BANK'">
                您已通过银行卡信息验证！
            </template>
            <template v-if="mode == 'FACE'">
                您已通过人脸识别认证！
            </template>
        </span>

        <!-- <span v-else-if="channel != 'THIRDAPP'" class="main-word">即将为您自动跳转...</span> -->
        <div class="tips">即将为您跳转({{ time }}秒)...</div>
        <div class="jump-next" @click="jump">
            立即跳转 >
        </div>
    </div>
</template>

<script>
export default {
    name: 'AuthSuccess',
    props: {
        user: {
            type: Object,
            default: () => {
                return {};
            },
        },
        mode: {
            type: String,
            default: '',
        },
        successPage: {
            type: String,
            default: '',
        },
        channel: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            time: 3,
            interval: '',
        };
    },
    mounted() {
        this.interval = setInterval(() => {
            if (this.time != 1) {
                this.time--;
            } else {
                this.jump();
            }
        }, 1000);
    },
    beforeDestroy() {
        this.interval && clearInterval(this.interval);
    },
    methods: {
        returnApp() {
            if (navigator.userAgent.indexOf('iPhone') > -1 || navigator.userAgent.indexOf('iPad') > -1) {
                window.webkit.messageHandlers.returnApp.postMessage();
            } else {
                window: android.returnApp();
            }
        },
        miniProgramBack() {
            if (!window.WeixinJSBridge || !WeixinJSBridge.invoke) {
                document.addEventListener('WeixinJSBridgeReady', this.ready, false);
            } else {
                this.ready();
            }
        },
        ready() {
            wx.miniProgram.navigateBack({
                delta: 3,
            });
        },
        async jump() {
            await this.postForm(
                '',
                {
                    requestData: JSON.stringify({
                        type: 'CLEAR_AUTH_CERTIFICATION',
                        data: { userId: this.$_qyssdk_userId },
                    }),
                },
                { headers: { noMes: 'true' } }
            );
            if (this.$_qyssdk_options) {
                if (this.$_qyssdk_options.authSuccessCbWrapper) {
                    this.$_qyssdk_options.authSuccessCbWrapper(this.mode, this.user);
                }
                if (this.$_qyssdk_options.callType === 'SIGN_AUTH') {
                    this.$emit('goPersonalSign');
                }
            }

            clearInterval(this.interval);
        },
    },
};
</script>
<style lang="less" scoped>
.auth-success {
    display: flex;
    flex-direction: column;
    align-items: center;
    img {
        margin: 40px 0 10px;
        width: 222px;
    }
    .title {
        margin-bottom: 4px;
    }
}
</style>