<template>
<transition name="modal">
    <div class="modal-mask" id="imagemodal" v-show="visible"
        @click="triggerHead">
        <header v-if="showHead" @click.stop.prevent="">
            <svg @click="close" class="icon icon-back" aria-hidden="true">
                <use xlink:href="#iconclose_o-"></use>
            </svg>
        </header>
        <show-image :url="url" @load="loadSuccess"></show-image>
    </div>
</transition>
</template>

<script>
import Hammer from 'hammerjs';

import showImage from './showImage.vue';
export default {
    data() {
        return {
            visible: true,
            showHead: false,
            url: '',
            loading: null
        };
    },
    components: {
        showImage
    },
    methods: {
        triggerHead() {
            this.showHead = !this.showHead;
        },
        close() {
            this.visible = false;
        },
        destroyNode() {
            if (this.visible) {
                return;
            }
            this.$el.removeEventListener('transitionend', this.destroyNode);
            this.$destroy(true);
            this.$el.parentNode.removeChild(this.$el);
        },
        closeLoading() {
            this.loading.close();
        },
        loadSuccess() {
            this.closeLoading();
        }
    },
    created() {},
    mounted() {
        let hammertime = new Hammer(this.$el);
        hammertime.get('swipe').set({ direction: Hammer.DIRECTION_VERTICAL });
        hammertime.on('swipeup', logEventType).on('swipedown', logEventType);
        function logEventType(ev) {
            ev.preventDefault();
        }
        this.$el.addEventListener('transitionend', this.destroyNode);
        this.$nextTick(() => {
            this.loading = this.$loading('正在加载');
        });
    }
};
</script>


<style lang="less" scoped>
@import './extend.less';
header {
    width: 100%;
    background: white;
    position: fixed;
    top: 0;
    z-index: 9999;
    color: #135;
    height: 40px;
    text-align: center;
    line-height: 40px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
    .icon-back {
        float: left;
        font-size: 20px;
        margin-top: 10px;
        margin-left: 10px;
    }
}
.modal-mask {
    &:extend(.modal-mask);
    background-color: #fff;
}
</style>
