import loading from './loading.js';
import message from './message.js';
import modal from './modal.js';
import imageModal from './imagemodal.js';
import showImage from './showImage.vue';
import modalComp from './Modal.vue';
import formInput from './formInput.vue';
import authButton from './auth-button.vue';
import authCheckbox from './auth-checkbox.vue';
import authSubheader from './auth-subheader.vue';
import authSuccess from './auth-success.vue';
import authInvalid from './auth-invalid.vue';
import areaCode from './auth-area-code.vue';
import skeleton from './skeleton.vue';

const components = [
    showImage,
    formInput,
    modalComp,
    authButton,
    authCheckbox,
    authSubheader,
    authSuccess,
    authInvalid,
    areaCode,
    skeleton,
];
const install = function(Vue, opts = {}) {
    components.map(component => {
        Vue.component(component.name, component);
    });
    Vue.prototype.$loading = loading;
    Vue.prototype.$message = message;
    Vue.prototype.$modal = modal;
    Vue.prototype.$imageModal = imageModal;
};

/* istanbul ignore if */
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

export default {
    install,
};
