<template>
    <div class="manual-in-audit">
        <div class="result-top">
            <q-icon class="exit-auth" icon="export" @click="showExitAuthModal"></q-icon>
            <img class="res-img" src="../assets/img/manual-in-audit.png" />
            <span class="h2-title title">审核中</span>
            <span class="tip-large text-center"
                >审核结果将在10~30分钟内通知您。<br /><a
                    class="tip-large"
                    style="text-decoration: none;"
                    href="tel:400-805-6850"
                    >客服热线：400-805-6850</a
                ></span
            >
        </div>

        <div class="result-bottom">
            <div class="info-module">
                <div class="h3-title regular-color title">您提交的认证信息</div>
                <div class="info-row">
                    <span class="main-word">姓&nbsp;&nbsp;&nbsp;&nbsp;名</span
                    ><span class="main-word regular-color">{{ info.name }}</span>
                </div>
                <div class="info-row">
                    <span class="main-word">证件类型</span><span class="main-word regular-color">{{ paperType }}</span>
                </div>
                <div class="info-row">
                    <span class="main-word">证件号</span><span class="main-word regular-color">{{ info.cardNo }}</span>
                </div>
                <div class="info-row">
                    <span class="main-word">{{ isUseMail ? '邮箱' : '手机号' }}</span
                    ><span class="main-word regular-color">{{ info.contact }}</span>
                </div>
            </div>

            <div class="info-module">
                <div class="main-word upload-title">{{ paperType }}上传</div>
                <div class="img-container">
                    <div class="img-item">
                        <div class="img-con">
                            <img @load="$qUtils.fillContainerWhenImgLoad" :src="photoSrc(info.front)" />
                        </div>
                        <span class="tip-large">{{ paperType }}（正面）</span>
                    </div>
                    <div class="img-item">
                        <div class="img-con">
                            <img @load="$qUtils.fillContainerWhenImgLoad" :src="photoSrc(info.handFront)" />
                        </div>
                        <span class="tip-large">手持{{ paperType }}</span>
                    </div>
                </div>
            </div>

            <div class="info-module">
                <div class="info-row">
                    <span class="main-word">备注</span
                    ><span class="main-word regular-color">{{ info.description || '无' }}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ManualInAudit',
    props: {
        successPage: {
            type: String,
            default: '',
        },
        channel: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            info: {
                name: '',
                paperType: '',
                cardNo: '',
                contact: '',
                description: '',
                mode: '',
            },
        };
    },
    computed: {
        isUseMail() {
            let result = this.$qUtils.validata({
                value: this.info.contact,
                noValNotValid: false,
                rules: [{ rule: this.$qUtils.validator.isEmail }],
            });
            return !result.isInvalid;
        },
        paperType() {
            let result = '';
            let obj = {
                IDCARD: '身份证',
                PASSPORT: '护照',
                HKMP: '港澳通行证',
                MTPS: '台胞证',
            };
            return obj[this.info.paperType] || '身份证';
        },
    },
    async created() {
        await this.getAuthInfo();
    },
    methods: {
        async getAuthInfo() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_MANUAL_DETAIL',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            this.info = res.result;
        },
        photoSrc(fileKey) {
            return `${this.configApi}?${encodeURI(
                `requestData=${JSON.stringify({
                    type: 'DOWNLOAD_AUTH_IMAGE',
                    data: {
                        fileKey,
                        thumb: true,
                        userId: this.$_qyssdk_userId,
                        type: this.$_qyssdk_options.callType,
                        contractId: this.$_qyssdk_options.callType === 'SIGN_AUTH' ? this.$_qyssdk_contractId : null,
                    },
                })}${(() => {
                    let str = '';
                    Object.keys(window.qysQuery).forEach(key => {
                        str += `&${key}=${window.qysQuery[key]}`;
                    });
                    return str;
                })()}`
            )}`;
        },
        async showExitAuthModal() {
            try {
                await this.$mt.MessageBox.confirm('', {
                    message: '确定退出认证流程？',
                    confirmButtonClass: 'main-word link-color',
                });
                this.goBack();
            } catch (e) {
                console.log(e);
            }
        },
        goBack() {
            window.location.replace(this.successPage);
        },
        goRepeatAuth() {
            const authWays = {
                DEFAULT: 'PHONEINFO',
                FACE: 'FACEINFO',
                BANK: 'BANKINFO',
            };
            this.info.originalMode && this.dealStep(authWays[this.info.originalMode]);
        },
    },
};
</script>
<style lang="less" scoped>
.manual-in-audit {
    width: 100%;
    height: 100%;
    background: @body-background;
    .result-top {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 19px 20px 28px;
        background: white;
        margin-bottom: 10px;
        position: relative;
        .exit-auth {
            position: absolute;
            top: 16px;
            right: 16px;
            font-size: 18px;
            color: #c1c6cd;
        }
        .res-img {
            width: 138px;
        }
        .title {
            margin-top: -10px;
        }
        .text-center {
            text-align: center;
        }
    }
    .result-bottom {
        display: flex;
        flex-direction: column;
        padding-left: 20px;
        background: white;
        .info-module {
            padding: 16px 20px 6px 0;
            border-bottom: 1px solid @body-background;
            &:last-child {
                padding-bottom: 24px;
                border-bottom: none;
            }
        }
        .title {
            margin-bottom: 16px;
        }
        .info-row {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 12px;
            :first-child {
                min-width: 80px;
            }
            > :last-child {
                text-align: right;
            }
        }
        .upload-title {
            margin-bottom: 8px;
        }
        .img-container {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            margin-bottom: 16px;
            .img-item {
                width: calc(~'(100% - 14px) / 2');
                display: flex;
                flex-direction: column;
                align-items: center;
                &:first-child {
                    margin-right: 14px;
                }
                .img-con {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    width: 100%;
                    height: 110px;
                    border-radius: 6px;
                    margin-bottom: 4px;
                    background: @body-background;
                    overflow: hidden;
                }
            }
        }
    }
    .btn-wrapper {
        display: flex;
        border-top: 1px solid @body-background;
        background: white;
        padding: 20px;
        .back-btn {
            margin-right: 10px;
        }
    }
}
</style>
