<template>
    <div class="auth-invalid">
        <q-icon class="warning-circle-fill-icon" icon="warning-circle-fill"></q-icon>
        <span class="h1-title title">认证页面已失效</span>

        <span v-if="channel == 'THIRD' || !successPage" class="main-word disable-color">
            请重新认证，即将为您跳转({{ time }}秒)...
        </span>
    </div>
</template>

<script>
export default {
    name: 'AuthInvalid',
    props: {
        successPage: {
            type: String,
            default: '',
        },
        channel: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            time: 3,
            interval: '',
        };
    },
    mounted() {
        this.interval = setInterval(() => {
            if (this.time != 1) {
                this.time--;
            } else {
                this.jump();
                this.interval && clearInterval(this.interval);
            }
        }, 1000);
    },
    beforeDestroy() {
        this.interval && clearInterval(this.interval);
    },
    methods: {
        async jump() {
            this.dealStep('');
            this.$emit('refetchUserInfo');
        },
    },
};
</script>
<style lang="less" scoped>
.auth-invalid {
    width: 100%;
    height: 100%;
    background: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    .q-icon {
        font-size: 60px;
        color: @warning-color;
        margin: 70px 0 20px;
    }
    .title {
        margin-bottom: 6px;
    }
}
</style>
