<template>
    <transition name="modal">
        <div class="modal-mask" v-show="visible">
            <div class="modal-container">
                <img src="./loading.svg" width="64" alt="" />
                <div v-html="msg"></div>
            </div>
        </div>
    </transition>
</template>
<script>
export default {
    data() {
        return {
            msg: '',
            closed: false,
            visible: false,
        };
    },
    watch: {
        closed(newVal) {
            if (newVal) {
                this.visible = false;
                this.$el.addEventListener('transitionend', this.destroyElement);
            }
        },
    },
    methods: {
        destroyElement() {
            this.$el.removeEventListener('transitionend', this.destroyElement);
            this.$destroy(true);
            this.$el.parentNode.removeChild(this.$el);
        },
        close() {
            this.closed = true;
            if (typeof this.onClose === 'function') {
                this.onClose(this);
            }
        },
    },
};
</script>

<style lang="less" scoped>
@import './extend.less';

.modal-mask {
    &:extend(.modal-mask);
    background-color: rgba(0, 0, 0, 0.5);
}
.modal-container {
    &:extend(.middle-center);
    width: 80%;
    max-width: 350px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
    font-family: Helvetica, Arial, sans-serif;
    text-align: center;
    padding: 15px 0;
    img {
        margin-top: 15px;
        vertical-align: middle;
    }
    div {
        margin: 18px 0 15px;
        font-size: 16px;
        color: #7f8997;
        line-height: 22px;
        text-align: center;
    }
}
</style>
