<template>
    <div class="">
        <label class="form-label">
            {{ label }}
        </label>
        <input
            :value="value"
            v-on:input="input = $event.target.value"
            type="text"
            class="form-input"
            :class="{ 'input-error': error }"
            @change="validate"
            :placeholder="'请输入' + label"
        />
        <div v-show="error" class="error">请输入{{ label }}</div>
    </div>
</template>

<script>
export default {
    name: 'formInput',
    props: {
        label: {
            default: '',
        },
        value: {
            default: '',
        },
        required: {
            default: false,
        },
    },
    data() {
        return {
            error: false,
            input: '',
        };
    },
    watch: {
        input() {
            this.$emit('update:value', this.input);
        },
    },
    methods: {
        validate() {
            if (this.required === false) {
                this.error = false;
                return;
            }
            if (!this.input) {
                this.error = true;
            } else {
                this.error = false;
            }
        },
    },
};
</script>


<style scoped lang="less">
.form-label {
    font-size: 14px;
    color: #113355;
    line-height: 20px;
    text-align: left;
    margin-bottom: 6px;
    display: inline-block;
}

.error {
    color: @danger-color;
    margin-bottom: 16px;
}
.form-input {
    border: 1px solid #d9d9d9;
    border-radius: 3px;
    display: block;
    margin: 0 auto 16px;
    width: e('calc(100% - 26px)');
    height: 40px;
    line-height: 40px;
    padding: 0 12px;
    font-size: 16px;
    &.input-error {
        margin: 0;
        border-color: @danger-color;
    }
}
</style>
