<template>
    <div class="doc" ref="doc" v-show="!(contractMes.viewNeedAuth && needRealName)">
        <header>
            <div class="contract-title ellipsis">当前文件：{{ currentDoc.title }}</div>
            <div
                class="click-area"
                @click="
                    changeComponent('contractList');
                    rectanglesAllOutSelect();
                "
            >
                <i class="iconfont iconicon_write_spread- middle-center"></i>
            </div>
        </header>
        <div
            class="page-list"
            @click="rectanglesAllOutSelect"
            :class="{
                'can-not-sign': !canSign,
            }"
            ref="page-list"
            @scroll="
                calcCurrentPageNo($event);
                isScrolling = true;
                scrollingStop();
            "
        >
            <div class="jump-page" v-if="canPre">
                <div class="info" @click="preDoc">切换至上一份文件</div>
                <div class="title">{{ contractMes.$get('documents', currentDocIndex - 1, title) }}</div>
            </div>
            <div class="wrapper" :style="{ width: `${resize * 100}%` }">
                <div v-for="page in currentPages" :key="page.docId + page.pageNo" class="page" ref="pageNo">
                    <div class="page-img-wrapper" style="position: relative;">
                        <skeleton
                            v-if="!page.docimg.loaded"
                            style="position: absolute;"
                            :height="page.size.heightPixel"
                            :widthMM="page.size.widthMM"
                            :heightMM="page.size.heightMM"
                        ></skeleton>

                        <lazy-component>
                            <img :src="page.imgObj.src" :style="page.style" @load="page.docimg.loaded = true" />
                        </lazy-component>
                    </div>

                    <template v-if="canSign && isRectShow(page, rectangle)" v-for="rectangle in rectangles">
                        <div
                            :id="rectangle.id"
                            class="rectangle-area"
                            :style="rectangle.style"
                            :class="{ focus: rectangle.isSelected, signed: rectangle.hasSigned }"
                            @click="
                                rectangle.isSelected = true;
                                selectSeal(rectangle);
                            "
                        >
                            <template v-if="!rectangle.hasSigned">
                                <span>点击签名</span>
                            </template>
                        </div>
                    </template>
                    <template v-if="isStamperShow(page, stamper)" v-for="(stamper, index) in stamperListShow">
                        <div
                            :key="stamper.id"
                            @touchmove="popoverHidden(stamper.id)"
                            @click="rectangleSelect(stamper)"
                            v-popover="{ name: stamper.id, position: 'top' }"
                            :style="stamper.style"
                            :id="stamper.id"
                            v-drag="{
                                canDrag: contractMes.extraSign !== false,
                                hideDragStamperHelper: hideDragStamperHelper,
                                location: stamperList[index],
                                locations: stamperList,
                                rectangles: rectangles,
                            }"
                            class="stamper-img"
                            :class="{
                                'has-location': stamper.locationId,
                                'need-background': stamper.sealType == 'PERSONAL' || stamper.sealType == 'TIMESTAMP',
                            }"
                        >
                            <img :src="stamperSrc(stamper.sealId)" />
                        </div>

                        <popover
                            v-if="!(stamper.sealType == 'TIMESTAMP' && stamper.locationId)"
                            style="height:24px;"
                            :width="stamper.sealType != 'TIMESTAMP' ? 105 : 56"
                            :ref="stamper.id"
                            :name="stamper.id"
                        >
                            <span
                                v-if="stamper.sealType != 'TIMESTAMP'"
                                class="popover-action"
                                @click="reSelectSeal(stamper)"
                                >重签</span
                            >
                            <span class="popover-action" @click="deleteStamper(stamper.id)">删除</span>
                        </popover>
                    </template>
                </div>
            </div>
            <div class="jump-page" v-if="canNext">
                <div class="info" @click="nextDoc">切换至下一份文件</div>
                <div class="title">
                    {{ contractMes.$get('documents', currentDocIndex + 1, 'title') }}
                </div>
            </div>
            <div v-if="isScrolling" class="current-page tip-large title-color-dark horizon-center">
                {{ this.currentPageNo }}/{{ this.totalPageNo }}
            </div>
        </div>
        <template v-if="canSign">
            <div class="fix-location">
                <i
                    @click="showHelpModal"
                    class="iconfont iconquestioncircle"
                    style="margin-bottom:15px;color:#c9c9c9;"
                ></i>
                <div style="width:50px;height:50px;" v-if="hasRectangles" @click="locateRectangle()">
                    <div class="middle-center">
                        <i class="iconfont icondingwei blue-text"></i>
                        <div class="status">
                            <span class="blue-text">{{ currentRectangleIndex }}</span>
                            <span class="grey-text">/{{ rectangleWithoutTimestamp.length }}</span>
                        </div>
                    </div>
                </div>
            </div>
            <footer class="sign-action">
                <div class="sign-area">
                    <span
                        v-popover.top="{ name: 'sign' }"
                        class="sign-button"
                        @click="hasRectangles && rectangleWithoutTimestamp.length > 1 ? '' : selectSeal()"
                    >
                        <i class="sign-button-icon iconfont iconsign1"></i>
                        签名
                    </span>

                    <span class="sign-button" @click="addStamper()">
                        <i class="sign-button-icon iconfont iconicon-2-32"></i>
                        时间戳
                    </span>
                </div>
                <div @click="confirmSign" class="confirm-sign" :class="{ 'can-confirm-sign': canConfirmSign }">
                    确认签署
                </div>
            </footer>
            <popover v-if="hasRectangles && rectangleWithoutTimestamp.length > 1" :width="153" ref="sign" name="sign">
                <div
                    class="popover-action"
                    @click="
                        popoverHidden('sign');
                        selectSeal();
                    "
                >
                    添加一个签名
                </div>
                <div
                    class="popover-action"
                    @click="
                        popoverHidden('sign');
                        modifyAllRectangles = true;
                        selectSeal();
                    "
                >
                    添加所有指定签名
                </div>
            </popover>
        </template>

        <component
            :is="activeComponent"
            :contact="contact"
            :documentsList="contractMes.documents"
            :currentDocIndex="currentDocIndex"
            :sealList="sealList"
            :selectSealOptions="selectSealOptions"
            :stamperList="stamperList"
            :contractId="contractId"
            :activityId="contractMes.activityId"
            @signContract="signContract"
            @componentClose="componentClose"
            @chooseContract="chooseContract"
            @changeComponent="changeComponent"
            @addStamper="addStamper"
            @modifyStamper="modifyStamper"
            @changeSignature="changeSignature"
            @setFacePhotoKey="setFacePhotoKey"
        ></component>
    </div>
</template>

<script>
import smsPinGet from './personal-sign-sms-pin-get.vue';
import contractList from './personal-sign-contract-list.vue';
import createSignature from './personal-sign-create-signature.vue';
import selectSeal from './personal-sign-select-seal.vue';
import faceAuth from './personal-sign-faceauth.vue';
import helpModal from './personal-sign-help-modal.vue';
import Hammer from 'hammerjs';

let lastTouchEnd = 0,
    touchstart = event => {
        if (event.touches.length > 1) {
            event.preventDefault();
        }
    },
    touchend = event => {
        const now = new Date().getTime();
        if (now - lastTouchEnd <= 300) {
            event.preventDefault();
        }
        lastTouchEnd = now;
    };
export default {
    name: 'PersonalSign',
    components: {
        smsPinGet,
        contractList,
        selectSeal,
        faceAuth,
        createSignature,
    },
    data() {
        return {
            currentDocIndex: 0,
            timeStamp: new Date().getTime(),
            contractMes: {
                documents: [],
            },
            activeComponent: '',
            modifyAllRectangles: false,
            stamperList: [],
            selectSealOptions: {},
            watchStampers: [],
            rectangles: [],
            lastScrollTop: 0,
            nowPageNo: 1,
            facePhotoKey: '',
            hammertime: '',
            resize: 1,
            preScale: 1,
            isScrolling: false,
            scrollingStop: null,
            currentPageNo: 1,
        };
    },
    props: {
        contractId: {
            type: String,
        },
        contact: {
            type: String,
        },
    },
    watch: {
        stopScroll(nv) {
            if (nv) {
                this.$refs['page-list'].style['overflow'] = 'hidden';
            } else {
                this.$refs['page-list'].style['overflow'] = 'auto';
            }
        },
        stamperList: {
            handler() {
                this.getRectangles();
            },
            deep: true,
        },
        'stamperList.length'() {
            this.watchStampers.map(unwatch => {
                unwatch();
            });
            this.watchStampers = [];
            Object.keys(this.stamperList).map(key => {
                let detectOverlap = this.$watch(
                    `stamperList.${key}`,
                    stamper => {
                        if (stamper.docId !== this.currentDoc.id) {
                            return;
                        }
                        if (stamper.sealType == 'TIMESTAMP') {
                            return;
                        }
                        this.detectOverlap(stamper);
                    },
                    {
                        deep: true,
                    }
                );
                let changeStamperDirection = this.$watch(`stamperList.${key}`, this.changeStamperDirection, {
                    deep: true,
                    immediate: true,
                });
                this.watchStampers.push(detectOverlap);
                this.watchStampers.push(changeStamperDirection);
            });
        },
    },
    computed: {
        needRealName() {
            let currentSignatory =
                this.contractMes.signatories.find(signatory => signatory.id === this.contractMes.signatoryId) || {};
            return currentSignatory.preAction == 'REALNAME';
        },
        stopScroll() {
            return !!this.activeComponent;
        },
        totalPageNo() {
            return this.currentDoc.$get('docimgs', 'length') || 1;
        },
        canNext() {
            return this.currentDocIndex != this.contractMes.documents.length - 1;
        },
        canPre() {
            return this.currentDocIndex != 0;
        },
        currentDoc() {
            return this.contractMes.documents[this.currentDocIndex] || { docimgs: [] };
        },
        hasRectangles() {
            return this.contractMes.documents.some(doc => doc.rectangles);
        },
        canConfirmSign() {
            if (this.hasRectangles) {
                let unsigned = this.rectangles.some(rectangle => !rectangle.hasSigned);
                if (unsigned) {
                    return false;
                }
            }
            let flag = this.stamperList.every(sign => sign.sealType == 'TIMESTAMP');
            if (flag) {
                return false;
            }
            return true;
        },
        canSign() {
            return (
                this.contractMes.actStep != 'AUDIT' &&
                this.contractMes.actStep != 'CONFIG_FLOW' &&
                (this.contractMes.status == 'REQUIRED' || this.contractMes.status == 'INVALIDING') &&
                this.contractMes.signable
            );
        },
        rectangleWithoutTimestamp() {
            return this.rectangles.filter(rectangle => rectangle.rectType != 'TIMESTAMP');
        },
        currentRectangleIndex() {
            let index = this.rectangleWithoutTimestamp.findIndex(rectangle => rectangle.isSelected) + 1;
            return index;
        },
        currentPages() {
            let imgs = [];
            this.currentDoc.docimgs.map(img => {
                let page = {};

                // 初始化 docimg 的 loaded 字段
                if (img.loaded === undefined) {
                    this.$set(img, 'loaded', false);
                }
                page.docimg = img; // 保存引用，以通过 page 修改 docimg 的 loaded 字段

                let url = `${this.configApi}?${encodeURI(
                    `requestData=${JSON.stringify({
                        type: 'DOWNLOAD_DOCUMENT_IMAGE',
                        data: {
                            contractId: this.contractId,
                            documentId: img.docId,
                            userId: this.$_qyssdk_userId,
                            page: img.pageNo,
                            isThumb: false,
                        },
                    })}&_=${this.timeStamp}${(() => {
                        let str = '';
                        Object.keys(window.qysQuery).forEach(key => {
                            str += `&${key}=${window.qysQuery[key]}`;
                        });
                        return str;
                    })()}`
                )}`;
                page.imgObj = {
                    src: url,
                    attempt: 1,
                };

                const widthPixel = this.$refs.doc.offsetWidth;
                const heightPixel = (widthPixel / img.width) * img.height;
                page.size = {
                    widthMM: img.width,
                    heightMM: img.height,
                    widthPixel,
                    heightPixel,
                };
                page.style = {
                    width: '100%',
                    'min-width': widthPixel + 'px',
                    'min-height': heightPixel + 'px',
                };

                page.pageNo = img.pageNo;
                page.docId = img.docId;

                imgs.push(page);
            });
            return imgs;
        },
        stamperListShow() {
            let stamperList = this.$qUtils.deepClone(this.stamperList);
            stamperList.map(sign => {
                sign.style = {
                    width: 100 * sign.width + '%',
                    height: 100 * sign.height + '%',
                    left: 100 * sign.left + '%',
                    top: 100 * sign.top + '%',
                };
            });
            return stamperList;
        },
        sealList() {
            return this.contractMes.seals;
        },
        unSignedDoc() {
            let signedDoc = [];
            let stampers = this.stamperList.filter(stamper => stamper.sealType != 'TIMESTAMP');
            stampers.map(stamper => {
                if (!signedDoc.includes(stamper.docId)) {
                    signedDoc.push(stamper.docId);
                }
            });
            return this.contractMes.documents.length - signedDoc.length;
        },
        unSignedDocId() {
            let signedDoc = [];
            let stampers = this.stamperList.filter(stamper => stamper.sealType != 'TIMESTAMP');
            stampers.map(stamper => {
                if (!signedDoc.includes(stamper.docId)) {
                    signedDoc.push(stamper.docId);
                }
            });
            return this.contractMes.documents.find(doc => !signedDoc.includes(doc.id)).id;
        },
    },
    created() {
        this.scrollingStop = this.$qUtils.debounce(() => {
            this.isScrolling = false;
        }, 500);
        this.init();
    },
    mounted() {
        document.addEventListener('touchstart', touchstart, { passive: false });
        document.addEventListener('touchend', touchend, { passive: false });
        this.hammertime = new Hammer(this.$refs['page-list'], { touchAction: 'pan-x pan-y' });
        this.hammertime.get('pinch').set({ enable: true });
        this.hammertime
            .on('pinchmove', ev => {
                let step = 0.05;
                let ele = this.$refs['page-list'];
                let offsetTop = this.$refs[`pageNo`][this.nowPageNo - 1].offsetTop - 50;
                if (this.preScale > ev.scale) {
                    if (this.resize > 1) {
                        this.resize -= step;
                        ele.scrollTop = offsetTop + (this.resize - 1) * (ev.center.y - 50);
                        ele.scrollLeft = (this.resize - 1) * ev.center.x;
                    }
                } else {
                    if (this.resize < 3) {
                        this.resize += step;
                        ele.scrollTop = offsetTop + (this.resize - 1) * (ev.center.y - 50);
                        ele.scrollLeft = (this.resize - 1) * ev.center.x;
                    }
                }
                this.preScale = ev.scale;
                this.$nextTick(() => {
                    this.stamperList.map(sign => {
                        let hasPopover = document.querySelector(`[data-popover=${sign.id}]`);
                        if (hasPopover) {
                            document.getElementById(sign.id).click();
                            document.getElementById(sign.id).click();
                        }
                    });
                });
                ev.preventDefault();
            })
            .on('pinchend', ev => {
                this.preScale = 1;
            });
    },
    beforeDestroy() {
        document.removeEventListener('touchstart', touchstart, { passive: false });
        document.removeEventListener('touchend', touchend, { passive: false });
    },
    methods: {
        showHelpModal() {
            const $el = this.$createElement(helpModal, {
                props: {
                    hasRectangles: this.hasRectangles,
                },
            });
            this.$modal({
                visible: false,
                showCancel: false,
                showOk: false,
                okfn() {},
                cancel() {},
                content: $el,
            });
        },
        setFacePhotoKey(data) {
            this.facePhotoKey = data;
            this.signContract();
        },
        stamperSrc(sealId) {
            return `${this.configApi}?${encodeURI(
                `requestData=${JSON.stringify({
                    type: 'DOWNLOAD_SEAL',
                    data: {
                        contractId: this.contractId,
                        userId: this.$_qyssdk_userId,
                        datePattern: 'HYPHEN',
                        sealId,
                    },
                })}${(() => {
                    let str = '';
                    Object.keys(window.qysQuery).forEach(key => {
                        str += `&${key}=${window.qysQuery[key]}`;
                    });
                    return str;
                })()}`
            )}`;
        },

        async init() {
            let loading = this.$loading('正在加载文件，请稍候');
            try {
                let detail = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_CONTRACT',
                        data: {
                            contractId: this.contractId,
                            userId: this.$_qyssdk_userId,
                            simple: false,
                        },
                    }),
                });
                this.contractMes = detail.result;
                if (this.contractMes.viewNeedAuth && this.needRealName) {
                    this.$modal({
                        visible: false,
                        showCancel: false,
                        okText: '确定',
                        okfn() {},
                        cancelFn() {},
                        content: `您当前还是未认证用户，请继续完成实名认证，以便预览合同内容！`,
                    });
                    return;
                }
                if (!this.canSign) {
                    return;
                }
                this.getRectangles();
                this.contractMes.documents.map(doc => {
                    if (!doc.rectangles) {
                        return;
                    }
                    doc.rectangles.map(rectangle => {
                        if (rectangle.rectType == 'TIMESTAMP') {
                            this.addStamper(undefined, rectangle);
                        }
                    });
                });
            } catch (e) {
                console.log(e);
            } finally {
                loading.close();
            }
        },
        guid() {
            let temp = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
                let r = (Math.random() * 16) | 0,
                    v = c == 'x' ? r : (r & 0x3) | 0x8;
                return v.toString(16);
            });
            return `stamper-${temp}`;
        },
        changeComponent(name) {
            this.activeComponent = name;
        },
        isRectShow(page, rectangle) {
            return page.pageNo == rectangle.page && page.docId == rectangle.documentId;
        },
        isStamperShow(page, stamper) {
            return page.pageNo == stamper.pageNo && page.docId == stamper.docId;
        },
        scrollToPage(pageNum) {
            let scrollTop = this.$refs[`pageNo`][pageNum - 1].offsetTop;
            this.$refs['page-list'].scrollTop = scrollTop;
        },
        deleteStamper(stamperId) {
            let index = this.stamperList.findIndex(stamper => stamperId == stamper.id);
            this.stamperList.splice(index, 1);
        },
        chooseContract(index) {
            if (index === this.currentDocIndex && this.nowPageNo == 1) {
                return;
            }
            this.currentDocIndex = index;
            this.nowPageNo = 1;
            this.$nextTick(() => {
                if (this.currentDocIndex == 0) {
                    this.$refs['page-list'].scrollTop = 0;
                } else {
                    this.$refs['page-list'].scrollTop = 50;
                }
            });
        },
        preDoc() {
            this.currentDocIndex--;
            this.nowPageNo = this.totalPageNo;
            this.$nextTick(() => {
                let dom = this.$refs['page-list'];
                dom.scrollTop = dom.scrollHeight - dom.offsetHeight;
            });
        },
        nextDoc() {
            this.currentDocIndex++;
            this.nowPageNo = 1;
            this.$nextTick(() => {
                this.$refs['page-list'].scrollTop = 60;
            });
        },

        calcCurrentPageNo(e) {
            const wrapperEl = e.srcElement;
            const curScrollTop = wrapperEl.scrollTop;
            const stepStatus = curScrollTop - this.lastScrollTop > 0 ? 'down' : 'up';
            this.lastScrollTop = curScrollTop;

            //*最后一页是横版的 上部是不能到达页面中央
            const lastPageNo = this.totalPageNo;
            const lastPageEl = [lastPageNo - 1];
            if (lastPageEl.offsetTop + lastPageEl.offsetHeight / 2 < curScrollTop + wrapperEl.offsetHeight) {
                this.currentPageNo = lastPageNo;
                return;
            }

            const pageElList = this.$refs['pageNo'];
            let pageNo = this.currentPageNo;
            if (stepStatus == 'down') {
                let pageEl = pageElList[pageNo - 1];
                while (pageEl.offsetTop - pageEl.offsetHeight * 0.5 < curScrollTop + 10) {
                    this.currentPageNo = pageNo;
                    if (pageNo < this.totalPageNo) {
                        pageNo++;
                        pageEl = pageElList[pageNo - 1];
                    } else {
                        break;
                    }
                }
            } else {
                let pageEl = pageElList[pageNo - 1];
                while (pageEl.offsetTop + pageEl.offsetHeight * 0.5 > curScrollTop + 10) {
                    this.currentPageNo = pageNo;
                    if (pageNo > 1) {
                        pageNo--;
                        pageEl = pageElList[pageNo - 1];
                    } else {
                        break;
                    }
                }
            }
        },

        scrollToRectangle(rectangle) {
            let index = this.contractMes.documents.findIndex(doc => rectangle.documentId == doc.id);
            if (index != this.currentDocIndex) {
                this.chooseContract(index);
                this.$nextTick(() => {
                    scrollToRectangle.bind(this)(rectangle);
                });
                return;
            }
            scrollToRectangle.bind(this)(rectangle);
            function scrollToRectangle(rectangle) {
                if (!document.getElementById(rectangle.id)) return;
                let offsetTop =
                    document.getElementById(rectangle.id).offsetTop +
                    this.$refs[`pageNo`][rectangle.page - 1].offsetTop;
                let offsetHeight = this.$refs['page-list'].offsetHeight;
                let scrollTop = offsetTop - offsetHeight / 2;
                if (scrollTop > 0) {
                    this.$refs['page-list'].scrollTop = scrollTop;
                } else {
                    this.$refs['page-list'].scrollTop = 0;
                }
            }
        },

        locateRectangle() {
            if (this.currentRectangleIndex === 0) {
                this.rectangleWithoutTimestamp[0].isSelected = true;
                this.scrollToRectangle(this.rectangleWithoutTimestamp[0]);
                return;
            }
            let index = this.rectangleWithoutTimestamp.findIndex(rectangle => rectangle.isSelected);
            this.rectangleWithoutTimestamp[index].isSelected = false;
            let next;
            if (index + 1 == this.rectangleWithoutTimestamp.length) {
                next = this.rectangleWithoutTimestamp[0];
            } else {
                next = this.rectangleWithoutTimestamp[index + 1];
            }
            next.isSelected = true;
            this.scrollToRectangle(next);
        },
        rectanglesAllOutSelect() {
            this.rectangles.map(rectangle => {
                rectangle.isSelected = false;
            });
        },
        changeSignature(id) {
            let personalSignature = this.sealList.find(seal => seal.type == 'PERSONAL');
            if (personalSignature) {
                personalSignature.id = id;
            } else {
                this.sealList.push({
                    type: 'PERSONAL',
                    id: id,
                });
            }
        },
        selectSeal(rectangle) {
            this.selectSealOptions.rectangle = rectangle;
            this.selectSealOptions.modifyStamperId = null;
            if (this.sealList.length == 0) {
                this.changeComponent('createSignature');
            } else if (this.sealList.length == 1) {
                this.addStamper(this.sealList[0], rectangle);
            } else {
                this.changeComponent('selectSeal');
            }
        },
        reSelectSeal(modifyStamper) {
            this.selectSealOptions.modifyStamperId = modifyStamper.id;
            if (this.sealList.length == 1) {
                this.changeComponent('createSignature');
            } else {
                this.changeComponent('selectSeal');
            }
        },
        addStamper(seal, rectangle) {
            if (this.modifyAllRectangles) {
                this.rectangleWithoutTimestamp.map(rectangle => {
                    if (rectangle.hasSigned) {
                        let modify = this.stamperList.find(sign => sign.locationId == rectangle.id);
                        this.modifyStamper(modify.id, seal);
                    } else {
                        this.addSingleStamper(seal, rectangle);
                    }
                });
                this.modifyAllRectangles = false;
                this.$message('已将签名添加至所有指定位置！');
            } else {
                this.addSingleStamper(seal, rectangle);
            }
        },
        componentClose() {
            this.modifyAllRectangles = false;
            this.changeComponent('');
        },
        addSingleStamper(seal = { id: 0, type: 'TIMESTAMP' }, rectangle) {
            let sealType = seal.type;
            if (!rectangle) {
                if (sealType !== 'TIMESTAMP') {
                    rectangle = this.rectangles.find(rectangle => !rectangle.hasSigned) || { id: null };
                } else {
                    rectangle = { id: null };
                }
            }
            if (this.contractMes.extraSign === false && !rectangle.id) {
                this.$message('不能在指定位置之外签署');
                return;
            }

            let width, height;
            switch (sealType) {
                case 'PERSONAL':
                    height = 0.051;
                    width = 0.179;
                    break;
                case 'PERSONALSEAL':
                    height = seal.spec.height / 297;
                    width = seal.spec.width / 210;
                    break;
                case 'TIMESTAMP':
                    height = 0.025;
                    width = 0.133;
                    break;
            }

            if (!rectangle.id) {
                this.scrollToPage(this.nowPageNo);
            }
            if (sealType != 'TIMESTAMP' && rectangle.id) {
                this.scrollToRectangle(rectangle);
            }
            let randomX = Math.random() / 5 - 0.1;
            let randomY = Math.random() / 5 - 0.1;
            this.stamperList.push({
                pageNo: rectangle.page || this.nowPageNo,
                docId: rectangle.documentId || this.currentDoc.id,
                width: width,
                height: height,
                left: rectangle.id ? rectangle.offsetX : randomX + (1 - width) / 2,
                top: rectangle.id ? 1 - rectangle.offsetY - height : randomY + (1 - height) / 2,
                sealType: sealType,
                sealId: seal.id,
                id: this.guid(),
                draging: false,
                locationId: rectangle.id,
            });
        },
        getRectangles() {
            let rectangles = [];
            this.contractMes.documents.map(doc => {
                rectangles = rectangles.concat(this.$qUtils.deepClone(doc.rectangles || []));
            });
            rectangles.map(rectangle => {
                let signedStamper = this.stamperList.find(sign => sign.locationId == rectangle.id);
                let width, height;
                if (signedStamper) {
                    rectangle.hasSigned = true;
                    width = signedStamper.width;
                    height = signedStamper.height;
                } else {
                    rectangle.hasSigned = false;
                    width = rectangle.width;
                    height = rectangle.height;
                }
                rectangle.style = {
                    width: 100 * width + '%',
                    height: 100 * height + '%',
                    left: 100 * rectangle.offsetX + '%',
                    top: 100 * (1 - rectangle.offsetY - height) + '%',
                };
                rectangle.isSelected = false;
            });
            this.rectangles = rectangles;
        },
        rectangleSelect(stamper) {
            let rectangle = this.rectangles.find(rectangle => rectangle.id == stamper.locationId);
            if (rectangle) {
                rectangle.isSelected = true;
            }
        },
        detectOverlap(stamper) {
            let right1 = stamper.width + stamper.left;
            let left1 = stamper.left;
            let top1 = stamper.top;
            let bottom1 = stamper.top + stamper.height;
            let rectangle = this.rectangles.find(rectangle => {
                if (rectangle.hasSigned && rectangle.id != stamper.locationId) {
                    return false;
                }
                if (rectangle.rectType == 'TIMESTAMP') {
                    return false;
                }
                if (rectangle.documentId !== this.currentDoc.id) {
                    return false;
                }
                let right2 = rectangle.width + rectangle.offsetX;
                let left2 = rectangle.offsetX;
                let top2 = 1 - rectangle.offsetY - rectangle.height;
                let bottom2 = 1 - rectangle.offsetY;
                let overlapX = stamper.width + rectangle.width - (Math.max(right1, right2) - Math.min(left1, left2));
                let overlapY = stamper.height + rectangle.height - (Math.max(bottom1, bottom2) - Math.min(top1, top2));
                return (
                    stamper.pageNo == rectangle.page &&
                    overlapX > 0 &&
                    overlapY > 0 &&
                    overlapX * overlapY > 0.01 * rectangle.width * rectangle.height
                );
            }) || {
                id: '',
            };
            stamper.locationId = rectangle.id;
            if (rectangle.id) {
                if (!stamper.draging) {
                    rectangle.isSelected = false;
                    stamper.left = rectangle.offsetX;
                    stamper.top = 1 - rectangle.offsetY - stamper.height;
                } else {
                    rectangle.isSelected = true;
                }
            } else {
                this.rectanglesAllOutSelect();
            }
        },
        changeStamperDirection(stamper) {
            let doc = this.contractMes.documents.find(doc => doc.id == stamper.docId);
            let page = doc.docimgs[stamper.pageNo - 1];

            let width, height, pageWidth, pageHeight;
            if (page.width > page.height) {
                pageWidth = 297;
                pageHeight = 210;
            } else {
                pageWidth = 210;
                pageHeight = 297;
            }
            switch (stamper.sealType) {
                case 'PERSONAL':
                    height = 16 / pageHeight;
                    width = 40 / pageWidth;
                    break;
                case 'PERSONALSEAL':
                    let seal = this.sealList.find(seal => seal.id == stamper.sealId);
                    height = seal.spec.height / pageHeight;
                    width = seal.spec.width / pageWidth;
                    break;
                case 'TIMESTAMP':
                    height = 7.43 / pageHeight;
                    width = 27.93 / pageWidth;
                    break;
            }
            stamper.top = stamper.top + stamper.height - height;

            stamper.height = height;
            stamper.width = width;
        },
        modifyStamper(modifyId, seal) {
            let modify = this.stamperList.find(stamper => stamper.id == modifyId);
            let sealType = seal.type;
            modify.sealId = seal.id;
            modify.sealType = sealType;
            let width, height;
            switch (sealType) {
                case 'PERSONAL':
                    height = 0.051;
                    width = 0.179;
                    break;
                case 'PERSONALSEAL':
                    height = seal.spec.height / 297;
                    width = seal.spec.width / 210;
                    break;
                case 'TIMESTAMP':
                    height = 0.025;
                    width = 0.133;
                    break;
            }
            modify.top = modify.top + modify.height - height;
            modify.width = width;
            modify.height = height;
        },
        confirmSignValidate() {
            const self = this;
            this.$modal({
                visible: false,
                showCancel: true,
                cancelText: '忽略',
                okText: '立即前往',
                okfn() {
                    let index = self.contractMes.documents.findIndex(doc => doc.id == self.unSignedDocId);
                    self.chooseContract(index);
                },
                cancelFn() {
                    self.chooseSignMethod();
                },
                content: `<div>
                    仍有${this.unSignedDoc}个文件未签署，是否前往签署?
                </div>`,
            });
        },
        async chooseSignMethod() {
            if (this.contractMes.signNeedFace) {
                let data = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_FACESIGN_KEY',
                        data: {
                            contractId: this.contractId,
                            userId: this.$_qyssdk_userId,
                            activityId: this.contractMes.activityId,
                        },
                    }),
                });
                if (data.facePhotoKey) {
                    this.facePhotoKey = data.facePhotoKey;
                    console.log(data.facePhotoKey);
                    console.log(data);
                    this.signContract();
                } else {
                    this.changeComponent('faceAuth');
                }
                return;
            }
            this.changeComponent('smsPinGet');
        },
        confirmSign() {
            if (this.hasRectangles) {
                let unsigned = this.rectangles.filter(rectangle => !rectangle.hasSigned);
                if (unsigned.length) {
                    this.$message(`您还有${unsigned.length}处未签署`);
                    return;
                }
            }
            let flag = this.stamperList.every(sign => {
                return sign.sealType == 'TIMESTAMP';
            });
            if (flag) {
                this.$message('您的合同尚未添加签名');
                return false;
            }

            if (this.unSignedDoc && !this.hasRectangles && !this.contractMes.extraSign) {
                this.confirmSignValidate();
                return;
            }
            this.chooseSignMethod();
        },
        // todo
        hideDragStamperHelper() {
            this.stamperList.map(sign => {
                this.popoverHidden(`drag-${sign.id}`);
            });
        },

        popoverTrigger(id, flag) {
            let component = this.$refs[id];
            if (component) {
                if (Object.prototype.toString.call(component) === '[object Array]') {
                    this.$refs[id].map(item => {
                        item.visible = flag;
                    });
                } else {
                    this.$refs[id].visible = flag;
                }
            }
        },
        popoverShow(id) {
            this.popoverTrigger(id, true);
        },
        popoverHidden(id) {
            this.popoverTrigger(id, false);
        },

        async signContract(pin) {
            let loading = this.$loading('正在签署，请稍候...');
            let signDocs = [];
            let stamperList = this.$qUtils.deepClone(this.stamperList);

            stamperList.map(sign => {
                let index = signDocs.findIndex(doc => doc.documentId == sign.docId);
                if (index == -1) {
                    signDocs.push({
                        documentId: sign.docId,
                        stampers: [],
                    });
                    index = signDocs.length - 1;
                }
                let temp = {};

                temp = {
                    page: sign.pageNo,
                    width: sign.width,
                    height: sign.height,
                    offsetX: sign.left,
                    offsetY: 1 - sign.top - sign.height,
                    sealType: sign.sealType,
                    sealId: sign.sealId,
                };
                if (sign.locationId) {
                    temp.locationId = sign.locationId;
                }
                signDocs[index].stampers.push(temp);
            });

            try {
                let detail = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'SIGN_CONTRACT',
                        data: {
                            contractId: this.contractId,
                            userId: this.$_qyssdk_userId,
                            activityId: this.contractMes.activityId,
                            signDocs,
                            pin,
                            facePhotoKey: this.facePhotoKey,
                        },
                    }),
                });
                let defaultData = this.$options.data();
                this.contractMes = defaultData['contractMes'];
                this.stamperList = defaultData['stamperList'];
                this.currentDocIndex = defaultData['currentDocIndex'];
                this.timeStamp = defaultData['timeStamp'];
                this.init();
                if (this.$_qyssdk_options && this.$_qyssdk_options.signSuccessCb) {
                    this.$_qyssdk_options.signSuccessCb();
                }
            } catch (e) {
                console.log(e);
            } finally {
                loading.close();
            }
        },
    },
    directives: {
        drag: {
            inserted(el, binding, vnode) {
                let oDiv = el;
                const self = this;
                let location = binding.value.location;
                let locations = binding.value.locations;
                let rectangles = binding.value.rectangles;
                let hideDragStamperHelper = binding.value.hideDragStamperHelper;
                if (!binding.value.canDrag) {
                    return;
                }
                if (location.locationId) {
                    let flag = rectangles.find(
                        rectangle => rectangle.rectType == 'ACROSS_PAGE' && location.locationId == rectangle.id
                    );
                    if (flag) {
                        return;
                    }
                }
                if (location.locationId && location.sealType == 'TIMESTAMP') {
                    oDiv.ontouchstart = function(e) {
                        e.preventDefault();
                    };
                    return;
                }
                oDiv.ontouchstart = function(e) {
                    oDiv.parentNode.parentNode.parentNode.style['overflow'] = 'hidden';
                    let touch = e.touches[0];
                    let disX = touch.clientX - oDiv.offsetLeft;
                    let disY = touch.clientY - oDiv.offsetTop;
                    oDiv.ontouchmove = function(e) {
                        location.draging = true;
                        let touch = e.touches[0];

                        let l = touch.clientX - disX;
                        let t = touch.clientY - disY;
                        if (l < 0) {
                            l = 0;
                        }
                        let maxWidth = oDiv.parentNode.parentNode.offsetWidth;
                        if (l + oDiv.offsetWidth > maxWidth) {
                            l = maxWidth - oDiv.offsetWidth;
                        }
                        let minHeight = oDiv.parentNode.parentNode.offsetTop - oDiv.parentNode.offsetTop;
                        if (t < minHeight) {
                            t = minHeight;
                        }
                        let maxHeight =
                            oDiv.parentNode.parentNode.offsetTop +
                            oDiv.parentNode.parentNode.offsetHeight -
                            oDiv.parentNode.offsetTop -
                            oDiv.offsetHeight;
                        if (t > maxHeight) {
                            t = maxHeight;
                        }
                        l = l / oDiv.parentNode.offsetWidth;
                        t = t / oDiv.parentNode.offsetHeight;
                        location.left = l;
                        location.top = t;
                    };
                    oDiv.ontouchend = function(e) {
                        hideDragStamperHelper();
                        setTimeout(function() {
                            let notHasPopover = !document.querySelector(`[data-popover=${location.id}]`);
                            if (notHasPopover) {
                                document.getElementById(location.id).click();
                            }
                        }, 100);
                        //换页
                        oDiv.parentNode.parentNode.parentNode.style['overflow'] = 'auto';
                        location.draging = false;
                        oDiv.onmousemove = null;
                        oDiv.onmouseup = null;
                        let top = location.top;
                        let height = location.height;
                        let jumpNum;
                        let pageNum = 0;
                        let margin = 5;
                        let offset = margin / oDiv.parentNode.offsetHeight;
                        if (top > 1) {
                            pageNum = top - 1;
                            jumpNum = Math.ceil(pageNum + location.pageNo);
                        } else if (top < 0) {
                            if (top > -offset / 2) {
                                location.top = 0;
                                return;
                            }
                            if (top < -offset / 2) {
                                pageNum = top;
                                jumpNum = Math.floor(pageNum + location.pageNo);
                            }
                        } else {
                            if (top + height > 1) {
                                if (top + height <= 1 + offset / 2) {
                                    location.top = 1 - height;
                                    return;
                                }
                                if (top + height > 1 + offset / 2) {
                                    location.top = 1 + offset;
                                    jumpNum = Math.floor(1 + location.pageNo);
                                }
                            } else {
                                return;
                            }
                        }

                        location.top =
                            location.top - (jumpNum - location.pageNo) - offset * (jumpNum - location.pageNo);

                        if (location.top < 0 || location.top == -0) {
                            location.top = 0;
                        }
                        if (location.top + height > 1) {
                            location.top = 1 - height;
                        }
                        location.pageNo = jumpNum;
                    };
                };
            },
        },
    },
};
</script>
