<template>
    <div>
        <strong class="title">
            {{ steps[index] | title }}
            <i class="anticon icon-back fr icon-guanbi" style="color:#ccc;" @click="close"></i>
        </strong>
        <div class="tips" v-html="tip"></div>
        <img v-hammer:swipe.horizontal="swipe" :src="imgSrc" />
        <div style="text-align:center;">
            <div class="circle" :class="{ selected: index + 1 == i }" v-for="i in 3"></div>
        </div>
    </div>
</template>

<script>
export default {
    filters: {
        title(value) {
            switch (value) {
                case 1:
                    return '点击添加签名/签章';
                case 2:
                    return '拖动签名位置';
                case 3:
                    return '文档缩放查看';
                case 4:
                    return '定位签署位置';
                case 5:
                    return '一键完成签署';
            }
        },
    },
    props: {
        hasRectangles: Boolean,
    },
    data() {
        return {
            index: 0,
            steps: this.hasRectangles ? [4, 5, 3] : [1, 2, 3],
        };
    },
    computed: {
        imgSrc() {
            return require(`./assets/img/doc-helper${this.steps[this.index]}.png`);
        },
        tip() {
            switch (this.steps[this.index]) {
                case 1:
                    return '点击<i class="anticon blue-text split icon-sign1"></i><i class="anticon blue-text icon-seal1"></i>在文档中添加签名、印章';
                case 2:
                    return '按住签名并拖动，可调整签名位置';
                case 3:
                    return '支持手势缩放查看文档';
                case 4:
                    return '点击 <i class="anticon blue-text icon-dingwei" style="font-size:16px;"></i> 定位文档的签名或签章位置';
                case 5:
                    return '点击<span class="blue-text">【签名】</span>选择<span class="blue-text">【添加签名到所有位置】</span>';
            }
        },
    },
    methods: {
        close() {
            this.$parent.visible = false;
        },
        swipe(event) {
            if (event.type == 'swipeleft' && this.index < 2) {
                this.index++;
            }
            if (event.type == 'swiperight' && this.index > 0) {
                this.index--;
            }
        },
    },
};
</script>

<style lang="less" scoped>
.title {
    font-size: 16px;
    color: #383838;
    line-height: 20px;
    text-align: left;
    margin-bottom: 5px;
    display: block;
}
.circle {
    background: #dddddd;
    width: 8px;
    height: 8px;
    display: inline-block;
    border-radius: 100%;
}
.circle:not(:last-child) {
    margin-right: 10px;
}
.circle.selected {
    background: #00a0e9;
}
.tips {
    text-align: left;
    margin-bottom: 15px;
}
.split {
    border-right: 1px solid #00a0e9;
    margin-right: 5px;
    padding-right: 5px;
}
img {
    width: 100%;
}
</style>
