<template>
    <div class="face-wrapper">
        <!-- 拍视频-人脸识别【Android或无证件照的iOS】 -->
        <template v-if="isShotVideo" class="video-auth">
            <div class="content">
                <img class="face-img" src="../assets/img/face-auth.png" />

                <span class="main-word regular-color"
                    ><span class="main-word link-color">{{ user.username | nameRender }}</span>
                    请录制3-5秒朗读下方数字的视频</span
                >

                <div class="number-wrap" v-if="randomData.generatedNumbers">
                    <span
                        class="number-item"
                        :class="{
                            'number-style5': randomData.generatedNumbers.length === 5,
                            'number-style6': randomData.generatedNumbers.length === 6,
                        }"
                        v-for="(num, index) in randomData.generatedNumbers.slice()"
                        :key="index"
                        >{{ num }}</span
                    >
                </div>

                <auth-button
                    class="start-button"
                    type="primary"
                    :disabled="status === 'loading'"
                    @click="triggerUploadFaceVideo"
                    >{{ status === 'loading' ? '正在上传...' : '开始录制' }}</auth-button
                >

                <div v-if="showOtherAuthWays" class="tips">
                    <span class="tip-large">不方便录制视频？</span>
                    <span class="tip-large link-color" @click="openAuthWaysModal">尝试其他认证方式</span>
                </div>
            </div>
        </template>

        <!-- 拍照-人脸识别【iOS且有证件照】 -->
        <template v-else class="photo-auth">
            <div class="content">
                <div v-if="imgSrc && (status === 'loading' || status === 'failed')" class="face-photo-con">
                    <img :src="imgSrc" @load="$qUtils.fillContainerWhenImgLoad" />
                    <img v-show="status === 'loading'" class="scan-img" src="../assets/img/scan.png" />
                </div>
                <img v-else class="face-img" src="../assets/img/face-auth.png" />

                <span v-if="status === 'init'" class="main-word regular-color photo-tips-init"
                    ><span class="main-word link-color">{{ user.username | nameRender }}</span
                    >请现场拍摄你的正脸照片</span
                >
                <span v-if="status === 'loading'" class="main-word link-color photo-tips-loading"
                    >正在进行身份比对，请稍后…</span
                >
                <span v-if="status === 'failed'" class="main-word error-color photo-tips-failed">
                    <q-icon class="close-icon" icon="close-circle-fill"></q-icon>
                    <span>{{ failMsg }}</span>
                </span>

                <div class="shot-button-wrapper">
                    <auth-button
                        v-if="status !== 'loading'"
                        @click="triggerUploadFacePic"
                        class="tack-photo-btn"
                        type="primary"
                        :disabled="failedTimes >= 3"
                        >{{ status === 'failed' ? '重新拍摄' : '开始拍摄' }}</auth-button
                    >

                    <span v-if="status === 'failed'" class="tip-large fail-times-tips" style="margin-bottom: 20px;"
                        >剩余{{ 3 - failedTimes }}次拍照校验机会</span
                    >
                </div>

                <div v-if="showOtherAuthWays" class="tips">
                    <span class="tip-large">不方便拍照？</span>
                    <span class="tip-large link-color" @click="openAuthWaysModal">尝试其他认证方式</span>
                </div>
            </div>
        </template>
    </div>
</template>

<script>
import authWaysModal from '../auth-common/auth-ways.vue';
import axios from 'axios';

export default {
    name: 'Face',
    filters: {
        nameRender(name) {
            const len = name.length;
            if (len === 2) {
                return `${name.substring(0, 1)}*`;
            } else if (len > 2) {
                return name.substring(0, 1) + '*'.repeat(len - 2) + name.substr(-1);
            }
        },
    },
    props: {
        user: {
            type: Object,
            default: () => {
                return {};
            },
        },
        otherModes: {
            type: String,
            default: '',
        },
        channel: {
            type: String,
            default: '',
        },
        step: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            randomData: {
                generatedNumbers: '',
                bizno: '',
            },
            isAndroid: false,
            status: 'init', // init, loading, failed
            imgSrc: '',
            failedTimes: 0,
            failMsg: '比对结果：人脸匹配度较低',
            hasCardPic: false, // 是否有证件照
            hasHoldCardPic: false, // 是否有手持证件照
            hasManualed: false,
            axiosSource: axios.CancelToken.source(),
            isShotVideo: false,
            authWays: [],
            faildLogs: [],
        };
    },
    computed: {
        // 数据库中是否存有认证图片（如果有，那么意味着人脸验证通过/人工审核通过）
        hasPic() {
            return this.hasCardPic || this.hasHoldCardPic;
        },
        degradeToManual() {
            return this.authWays.includes('MANUAL');
        },
        showOtherAuthWays() {
            // 除了当前正在操作的认证，还有其他认证则显示其他认证入口
            let authWays = this.$qUtils.deepClone(this.authWays);
            let index = authWays.findIndex(way => way === 'FACE');
            if (index > -1) {
                authWays.splice(index, 1);
            }
            // 未失败过，降级方式不展示
            if (this.$appointAuthWay === 'FACE' && this.faildLogs.length === 0) {
                return false;
            }
            // 指定默认方式，未失败过不展示人工
            let canManual = !this.hasManualed && this.degradeToManual && !this.hasHoldCardPic;
            if ((this.$appointAuthWay === 'DEFAULT' && this.faildLogs.length === 0) || !canManual) {
                let manualIndex = authWays.findIndex(way => way === 'MANUAL');
                if (manualIndex > -1) {
                    authWays.splice(index, 1);
                }
            }

            return authWays.length !== 0;
        },
    },
    async created() {
        const deviceType = this.getDeviceType();
        this.isAndroid = deviceType === 'android';
        this.isShotVideo = this.isAndroid || (!this.isAndroid && !this.hasPic);

        // * Android或无证件照的iOS => true (录视频)
        // * 有证件照的iOS => false (拍照片)
        if (this.isShotVideo) {
            await this.getRandomNumber();
        }

        await this.$getAppointAuthWay();
        this.authWays = await this.$getAuthWays();

        // 判断证件照类型
        await this.judgeCardPictureType();
        await this.judgeManualed();

        const { result } = await this.postForm('', {
            requestData: JSON.stringify({
                type: 'GET_AUTH_LOG',
                data: {
                    userId: this.$_qyssdk_userId,
                },
            }),
        });
        this.faildLogs = result;
    },
    methods: {
        async judgeManualed() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_PASS_BEFORE',
                    data: {
                        userId: this.$_qyssdk_userId,
                        name: this.user.username,
                        cardNo: this.user.cardNo,
                    },
                }),
            });
            this.hasManualed = res.result;
        },
        openAuthWaysModal() {
            const instance = new Vue({
                components: { authWaysModal },
                render: h =>
                    h(authWaysModal, {
                        props: {
                            root: this,
                            user: this.user,
                            otherModes: this.otherModes,
                            channel: this.channel,
                            useOtherFrom: 'FACE',
                            step: this.step,
                        },
                    }),
            }).$mount();
            document.body.appendChild(instance.$el);
        },
        async judgeCardPictureType() {
            try {
                const res = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_AUTH_IMAGE_EXIST',
                        data: {
                            userId: this.$_qyssdk_userId,
                            name: this.user.username,
                            cardNo: this.user.cardNo,
                        },
                    }),
                });
                this.hasCardPic = res.faceImage;
                this.hasHoldCardPic = res.handfrontImage;
            } catch (error) {
                console.log(error);
            }
        },
        async triggerUploadFacePic() {
            const e = await this.$qUtils.chooseFile({
                accept: 'image/*',
                capture: 'camera',
            });
            this.uploadFacePic(e);
        },
        async triggerUploadFaceVideo() {
            const e = await this.$qUtils.chooseFile({
                accept: 'video/*',
                capture: 'user',
            });
            this.uploadVideo(e);
        },
        async uploadFacePic(e) {
            try {
                const target = e.target;
                if (window.FileReader) {
                    let reader = new FileReader();
                    let fileArr = target.files[0].name.split('.');
                    let fileType = fileArr[fileArr.length - 1].toLowerCase();
                    let fileSize = Number(Math.round((target.files[0].size * 100) / (1024 * 1024)) / 100);
                    if (fileType !== 'png' && fileType !== 'jpg' && fileType !== 'jpeg') {
                        this.$message('请上传图片类型文件！');
                        return false;
                    } else if (fileSize > 20) {
                        // 选择图片的时候做第一步拦截,压缩图片时会进一步拦截，上传文件的大小不能超过20M
                        this.$message('您上传的图片太大了！');
                        return false;
                    } else {
                        this.status = 'loading';

                        // 压缩操作, 压缩后最大限制2M
                        let blob = await this.$qUtils.compressImg(target.files[0], {
                            outputType: 'blob',
                            maxDefaultSize: 2,
                            quality: 1,
                            type: 'jpeg',
                            maxWidth: 360,
                            maxHeight: 540,
                        });
                        this.imgSrc = window.URL.createObjectURL(blob);

                        const response = await this.postMulti(
                            '',
                            {
                                file: blob,
                                requestData: JSON.stringify({
                                    type: 'GET_FACE_COMPARE',
                                    data: {
                                        userId: this.$_qyssdk_userId,
                                    },
                                }),
                            },
                            {
                                headers: {
                                    noMes: 'true',
                                },
                            }
                        );

                        // 3次以内 failedTimes从0计数
                        if (response.code !== 0 && this.failedTimes < 2) {
                            this.failMsg = `比对结果：${response.message}`;
                            this.increaseFailTimes();
                            this.status = 'failed';
                            return;
                        }

                        if (response.code === 0) {
                            this.dealStep('SUCCESS');
                        } else {
                            this.dealStep(response.nextStep);
                        }

                        reader.onload = function(event) {
                            reader = '';
                        };
                        reader.onerror = function(event) {
                            this.$message('读取图片失败，请重新选择！');
                        };
                    }
                } else {
                    this.$message('浏览器插件不支持');
                }
            } catch (error) {
                console.log(error);
            }
        },
        async uploadVideo(e) {
            this.status = 'loading';
            let loading = this.$loading('正在上传识别...');
            let target = e.target;
            //* 重置cancelToken
            this.axiosSource = axios.CancelToken.source();
            try {
                if (target.files[0]) {
                    let fileSize = Number(Math.round((target.files[0].size * 100) / (1024 * 1024)) / 100);

                    // 视频大小最大20M
                    if (fileSize > 20) {
                        this.status = '';
                        this.$message('视频大小不能超过20M！');
                        return false;
                    }

                    const res = await this.uploadVideoWithTimeout('', {
                        file: target.files[0],
                        requestData: JSON.stringify({
                            type: 'BAIDUAI_LIVENESS_VERIFY',
                            data: {
                                userId: this.$_qyssdk_userId,
                                bizno: this.randomData.bizno,
                            },
                        }),
                    });

                    if (res.code === 0) {
                        this.dealStep('SUCCESS');
                    } else {
                        if (res.nextStep === 'END') {
                            this.dealStep('FACEFAILED');
                        } else {
                            this.dealStep(res.nextStep);
                        }
                        this.status = 'failed';
                    }
                }
            } catch (e) {
                if (this.$qUtils.isObject(e) && e.errorMsg) {
                    this.axiosSource.cancel(); // 取消请求
                    this.status = 'init';
                    this.$message(e.errorMsg);
                }
                console.log(e);
            } finally {
                loading.close();
            }
        },
        // 上传视频文件超时限制
        uploadVideoWithTimeout(uri, params) {
            return Promise.race([
                this.uploadVideoPromise(uri, params),
                this.uploadVideoTimeout(60000), // 1分钟超时
            ]);
        },
        uploadVideoPromise(uri, params) {
            return this.postMulti(
                uri,
                params,
                {
                    headers: {
                        noMes: 'true',
                    },
                },
                this.axiosSource.token
            );
        },
        uploadVideoTimeout(ms) {
            const delayInfo = {
                errorMsg: '上传文件超时!',
            };
            return new Promise((resolve, reject) => {
                setTimeout(() => {
                    reject(delayInfo);
                }, ms);
            });
        },
        increaseFailTimes() {
            this.failedTimes++;
        },
        async getRandomNumber() {
            let loading = this.$loading('正在生成随机数');
            try {
                const genNumKey = 'videoCode';
                const { result } = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'BAIDUAI_LIVENESS_CODE',
                        data: {
                            userId: this.$_qyssdk_userId,
                        },
                    }),
                });
                this.randomData = {
                    generatedNumbers: result[genNumKey],
                    bizno: result.bizno,
                };
            } catch (error) {
                console.log(error);
            } finally {
                loading.close();
            }
        },
    },
};
</script>
<style lang="less" scoped>
.face-wrapper {
    width: 100%;
    .content {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 20px;
    }
    .face-img {
        width: 200px;
        margin-top: 48px;
        margin-bottom: 24px;
    }
    .face-photo-con {
        width: 260px;
        height: 195px;
        position: relative;
        margin-top: 24px;
        overflow: hidden;
        display: flex;
        justify-content: center;
        .scan-img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            animation: moveimg 1.7s infinite;
        }
        @keyframes moveimg {
            from {
                top: 0;
            }
            to {
                top: 100%;
            }
        }
    }
    .start-button {
        margin-bottom: 16px;
    }
    .other-button {
        border-color: #e5e7ea !important;
        white-space: nowrap;
        color: #4c596e !important;

        .other-button-tip {
            font-size: 12px;
            color: #7f8997;
        }
    }
    .tips {
        margin-top: 24px;
    }
    .photo-tips-init {
        margin-top: 10px;
    }
    .photo-tips-loading {
        margin-top: 40px;
    }
    .photo-tips-failed {
        margin: 40px 0;
        display: flex;
        align-items: center;
        .close-icon {
            margin-right: 3px;
        }
    }
    .number-wrap {
        width: 100%;
        height: 75px;
        display: flex;
        justify-content: space-between;
        margin-top: 24px;
        margin-bottom: 24px;
        .number-item {
            font-family: 'ALIBABA FONT';
            display: flex;
            justify-content: center;
            align-items: center;
            width: calc(~'(100% - 36px)/ 4');
            height: 100%;
            font-size: 60px;
            color: #001330;
            font-weight: bold;
            background: #ffffff;
            box-shadow: 0 0 4px 0 rgba(217, 217, 217, 0.5);
            border-radius: 3px;
            &.number-style5 {
                width: calc(~'(100% - 36px)/ 5');
                font-size: 52px;
            }
            &.number-style6 {
                width: calc(~'(100% - 36px)/ 6');
                font-size: 46px;
            }
        }
    }

    .shot-button-wrapper {
        margin-bottom: 16px;
        width: 100%;
        text-align: center;

        .tack-photo-btn {
            margin-top: 45px;
        }
    }

    .fail-times-tips {
        margin-top: 16px;
    }

    .tips {
        width: calc(~'100% - 40px');
        position: fixed;
        bottom: 16px;
        text-align: center;
    }
}
</style>
