<template>
    <div class="manual-fail">
        <div class="result-top">
            <q-icon class="exit-auth" icon="export" @click="showExitAuthModal"></q-icon>
            <img class="res-img" src="../assets/img/default-result-fail.png" />
            <span class="h2-title title">认证失败</span>
            <span class="tip-large">{{ info.reason }}</span>
            <div class="modify-btn main-word body-color-dark" type="primary" @click="repeatAuth">
                修改信息重新认证
            </div>
        </div>

        <div class="result-bottom">
            <div class="info-module">
                <span class="main-word title-color title">核对身份信息</span>
                <div class="info-row">
                    <span class="main-word regular-color">姓&nbsp;&nbsp;&nbsp;&nbsp;名</span
                    ><span class="main-word">{{ info.name }}</span>
                </div>
                <div class="info-row">
                    <span class="main-word regular-color">证件类型</span><span class="main-word">{{ paperType }}</span>
                </div>
                <div class="info-row">
                    <span class="main-word regular-color">证件号</span><span class="main-word">{{ info.cardNo }}</span>
                </div>
                <div v-if="info.contact" class="info-row">
                    <span class="main-word regular-color">{{ isUseMail ? '邮箱' : '手机号' }}</span
                    ><span class="main-word">{{ info.contact }}</span>
                </div>
            </div>

            <div class="info-module">
                <div class="main-word upload-title">{{ paperType }}上传</div>
                <div class="img-container">
                    <div class="img-item">
                        <div class="img-con">
                            <img :src="photoSrc(info.front)" />
                        </div>
                        <span class="tip-large">{{ paperType }}（正面）</span>
                    </div>
                    <div class="img-item">
                        <div class="img-con">
                            <img :src="photoSrc(info.handFront)" />
                        </div>
                        <span class="tip-large">手持{{ paperType }}</span>
                    </div>
                </div>
            </div>

            <div class="info-module">
                <div class="info-row">
                    <span class="main-word regular-color">备注</span
                    ><span class="main-word">{{ info.description || '无' }}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ManualFail',
    props: {
        user: {
            type: Object,
            default: {},
        },
        channel: {
            type: String,
            default: '',
        },
        successPage: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            info: {
                name: '',
                paperType: '',
                cardNo: '',
                contact: '',
                description: '',
            },
        };
    },
    computed: {
        paperType() {
            let result = '';
            let obj = {
                IDCARD: '身份证',
                PASSPORT: '护照',
                HKMP: '港澳通行证',
                MTPS: '台胞证',
            };
            return obj[this.info.paperType];
        },
        isUseMail() {
            let result = this.$qUtils.validata({
                value: this.info.contact,
                noValNotValid: false,
                rules: [{ rule: this.$qUtils.validator.isEmail }],
            });
            return !result.isInvalid;
        },
    },
    created() {
        this.getAuthInfo();
    },
    methods: {
        async getAuthInfo() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_MANUAL_DETAIL',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            this.info = res.result;
        },
        async repeatAuth() {
            this.$emit('reVerify');
            const { result } = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_NEXTSTEP',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            if (result === 'END') {
                this.dealStep('INVALID');
                return;
            }
            this.dealStep('INFO');
        },
        async showExitAuthModal() {
            try {
                await this.$mt.MessageBox.confirm('', {
                    message: '确定退出认证流程？',
                    confirmButtonClass: 'main-word link-color',
                });
                this.$giveUpAuth();
            } catch (e) {
                console.log(e);
            }
        },
        photoSrc(fileKey) {
            return `${this.configApi}?${encodeURI(
                `requestData=${JSON.stringify({
                    type: 'DOWNLOAD_AUTH_IMAGE',
                    data: {
                        fileKey,
                        thumb: true,
                        userId: this.$_qyssdk_userId,
                        type: this.$_qyssdk_options.callType,
                        contractId: this.$_qyssdk_options.callType === 'SIGN_AUTH' ? this.$_qyssdk_contractId : null,
                    },
                })}${(() => {
                    let str = '';
                    Object.keys(window.qysQuery).forEach(key => {
                        str += `&${key}=${window.qysQuery[key]}`;
                    });
                    return str;
                })()}`
            )}`;
        },
    },
};
</script>
<style lang="less" scoped>
.manual-fail {
    width: 100%;
    height: 100%;
    background: @body-background;
    .result-top {
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 19px 20px 28px;
        background: white;
        margin-bottom: 10px;
        position: relative;
        .exit-auth {
            position: absolute;
            top: 16px;
            right: 16px;
            font-size: 18px;
            color: #c1c6cd;
        }
        .res-img {
            width: 138px;
        }
        .title {
            margin-top: -10px;
        }
        .text-center {
            text-align: center;
        }
        .modify-btn {
            display: inline-block;
            border-radius: 4px;
            background: @primary-color;
            padding: 5px 14px;
            margin-top: 12px;
        }
    }
    .result-bottom {
        flex-grow: 1;
        padding-left: 20px;
        background: #fff;
        overflow: auto;
        .info-module {
            padding: 16px 20px 6px 0;
            border-bottom: 1px solid @body-background;
            background: #fff;
            &:last-child {
                padding-bottom: 24px;
                border-bottom: none;
            }
        }
        .title {
            margin-bottom: 16px;
        }
        .info-row {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 12px;
            :first-child {
                min-width: 80px;
            }
            > :last-child {
                text-align: right;
            }
        }
        .upload-title {
            margin-bottom: 8px;
        }
        .img-container {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            margin-bottom: 16px;
            .img-item {
                width: calc(~'(100% - 14px) / 2');
                display: flex;
                flex-direction: column;
                align-items: center;
                &:first-child {
                    margin-right: 14px;
                }
                .img-con {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    width: 100%;
                    height: 110px;
                    border-radius: 6px;
                    margin-bottom: 4px;
                    background: @body-background;
                    img {
                        height: 100%;
                        max-width: 100%;
                    }
                }
            }
        }
    }
}
</style>
