<template>
    <transition name="modal">
        <div class="modal-mask">
            <div class="modal-wrapper">
                <div class="modal-header">
                    <i class="iconfont iconguanbi vertical-center" @click="close"></i>
                    选择签名
                    <i class="iconfont iconsign1 vertical-center" @click="changeComponent('createSignature')"></i>
                </div>

                <div class="modal-body">
                    <template v-if="sealList.length !== 0">
                        <div class="image-container" v-hammer:swipe.horizontal="swipe">
                            <img :src="imgSrc" />
                        </div>

                        <div class="circle-container">
                            <div
                                class="circle"
                                :class="{ selected: currentSealIndex + 1 == sealIndex }"
                                v-for="sealIndex in sealList.length"
                            ></div>
                        </div>
                        <button class="choose-signature" @click="submit(sealList[currentSealIndex])">
                            使用签名
                        </button>
                    </template>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import Hammer from 'hammerjs';
export default {
    components: {},
    props: {
        selectSealOptions: {
            type: Object,
        },
        sealList: {
            type: Array,
        },
        stamperList: {
            type: Array,
        },
        contractId: {
            type: String,
        },
    },
    data() {
        return {
            currentSealIndex: 0,
        };
    },
    computed: {
        isModify() {
            return !!this.selectSealOptions.modifyStamperId;
        },
        imgSrc() {
            return `${this.configApi}?${encodeURI(
                `requestData=${JSON.stringify({
                    type: 'DOWNLOAD_SEAL',
                    data: {
                        contractId: this.contractId,
                        userId: this.$_qyssdk_userId,
                        datePattern: 'HYPHEN',
                        sealId: this.sealList[this.currentSealIndex].id,
                    },
                })}${(() => {
                    let str = '';
                    Object.keys(window.qysQuery).forEach(key => {
                        str += `&${key}=${window.qysQuery[key]}`;
                    });
                    return str;
                })()}`
            )}`;
        },
    },
    created() {
        if (!this.isModify) {
            return;
        }
        let sealId = this.stamperList.find(stamper => stamper.id == this.selectSealOptions.modifyStamperId).sealId;
        this.currentSealIndex = this.sealList.findIndex(seal => seal.id == sealId);
    },
    methods: {
        changeComponent(name) {
            this.$emit('changeComponent', name);
        },
        close() {
            this.$emit('componentClose');
        },
        submit(seal) {
            if (this.isModify) {
                this.$emit('modifyStamper', this.selectSealOptions.modifyStamperId, seal);
            } else {
                this.$emit('addStamper', seal, this.selectSealOptions.rectangle);
            }
            this.close();
        },

        swipe(event) {
            if (event.type == 'swipeleft') {
                if (this.currentSealIndex < this.sealList.length - 1) {
                    this.currentSealIndex++;
                } else {
                    this.currentSealIndex = 0;
                }
            } else if (event.type == 'swiperight') {
                if (this.currentSealIndex > 0) {
                    this.currentSealIndex--;
                } else {
                    this.currentSealIndex = this.sealList.length - 1;
                }
            }
        },
    },
};
</script>


<style lang="less" scoped>
.modal-mask {
    position: absolute;
    z-index: 999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    transition: opacity 0.3s ease;
}

.modal-wrapper {
    bottom: 0;
    position: absolute;
    width: 100%;
    height: 310px;
    margin: 0px auto;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
    transition: all 0.3s ease;
    font-family: Helvetica, Arial, sans-serif;

    height: 274px;
}

.modal-header {
    text-align: center;
    position: relative;
    height: 45px;
    font-size: 16px;
    line-height: 45px;
    border-bottom: 1px solid #eaeaeb;
    color: #383838;
    i {
        font-size: 16px;
        height: 40px;
        width: 50px;
        color: #c0c0c0;
        &.iconsign1 {
            right: 0px;
        }
        &.iconguanbi {
            left: 0px;
        }
    }
}
.modal-body {
    height: 228px;

    overflow: auto;
    position: relative;
    background-color: #fff;

    .image-container {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 150px;

        img {
            height: 90px;
        }
    }
    .circle-container {
        text-align: center;
        margin-bottom: 10px;

        .circle {
            background: #dddddd;
            width: 8px;
            height: 8px;
            display: inline-block;
            border-radius: 100%;
        }
        .circle:not(:last-child) {
            margin-right: 10px;
        }
        .circle.selected {
            background: #00a0e9;
        }
    }
    .choose-signature {
        width: 100%;
        height: 50px;
        border: none;
        border-radius: 0;
        color: white;
        font-size: 16px;
        background: #00a0e9;
    }
}
</style>
