<template>
    <div ref="outerWrapper" class="manual-wrapper">
        <auth-subheader :account="user.mobile"></auth-subheader>

        <div class="content">
            <span class="h3-title title-color title">{{ cardName }}上传</span>
            <div class="image-row">
                <div @click="uploadIdCard('faceSide')" class="image-item">
                    <div :class="['img-operate', { 'has-img': !!faceImgSrc }]">
                        <q-icon
                            class="camera-fill-icon"
                            :class="{ 'white-color': !!faceImgSrc }"
                            icon="camera-fill"
                        ></q-icon>
                        <span :class="['tip-large', 'body-color', { 'white-color': !!faceImgSrc }]">{{
                            faceImgSrc ? '点击重新拍摄' : `${cardName}（正面）`
                        }}</span>
                    </div>
                    <img class="middle-center upload-img" @load="$qUtils.fillContainerWhenImgLoad" :src="faceImgSrc" />
                </div>
                <div @click="uploadIdCard('hold')" class="image-item">
                    <div :class="['img-operate', { 'has-img': !!holdImgSrc }]">
                        <q-icon
                            class="camera-fill-icon"
                            :class="{ 'white-color': !!holdImgSrc }"
                            icon="camera-fill"
                        ></q-icon>
                        <span :class="['tip-large', 'body-color', { 'white-color': !!holdImgSrc }]">{{
                            holdImgSrc ? '点击重新拍摄' : `手持${cardName}`
                        }}</span>
                    </div>
                    <img class="middle-center upload-img" @load="$qUtils.fillContainerWhenImgLoad" :src="holdImgSrc" />
                </div>
            </div>
            <div v-if="!hasAccount" class="form-item" @click="activeForm('mobile')">
                <div :class="['input-wrap', !isUseMail && 'area-code-con', `ui-input-${mobile.status}`]">
                    <area-code
                        v-if="!isUseMail"
                        type="mobile"
                        className="area-code-selecter"
                        v-show="isCurrentFormActive || !!mobile.value"
                        :code="code"
                        @toggleSelectCode="toggleSelectCode('mobile')"
                        @getCode="getAreaCode"
                    />
                    <ui-textbox
                        floating-label
                        id="mobile"
                        ref="mobile"
                        :label="isUseMail ? '邮箱' : '手机号'"
                        v-model="mobile.value"
                        @blur="contactBlur"
                        iconPosition="right"
                    >
                        <q-icon
                            slot="icon"
                            v-if="$refs.$get('mobile', 'isActive') && !isUseMail"
                            @touchstart="
                                isUseMail = !isUseMail;
                                inputBlur();
                            "
                            class="mobile-icon"
                            icon="mail-change-o-"
                        ></q-icon>
                        <q-icon
                            slot="icon"
                            v-if="$refs.$get('mobile', 'isActive') && isUseMail"
                            @touchstart="
                                isUseMail = !isUseMail;
                                inputBlur();
                            "
                            class="mobile-icon"
                            icon="iphone-change-o-"
                        ></q-icon>
                    </ui-textbox>
                </div>
                <span
                    v-if="mobile.status"
                    :class="[
                        'ui-textbox-tips',
                        mobile.status === 'error' && 'error-tips',
                        mobile.status === 'warning' && 'warning-tips',
                    ]"
                    >{{ mobile.message }}</span
                >
            </div>
            <div class="form-item">
                <div class="input-wrap" :class="`ui-input-${remark.status}`">
                    <ui-textbox
                        floating-label
                        id="remark"
                        ref="remark"
                        label="添加备注"
                        v-model="remark.value"
                        iconPosition="right"
                        :enforceMaxlength="true"
                        :maxlength="30"
                        @focus="inpFocus"
                        @blur="handleIosScroll"
                    >
                        <q-icon
                            slot="icon"
                            class="clear-icon"
                            icon="close-circle-fill"
                            v-if="!!remark.value && $refs.$get('remark', 'isActive')"
                            @touchstart="remark.value = ''"
                        ></q-icon>
                    </ui-textbox>
                </div>
                <span v-if="remark.status" class="ui-textbox-tips" :class="`${remark.status}-tips`">{{
                    remark.message
                }}</span>
            </div>
            <auth-button @click="submit" class="submit-btn" type="primary" :disabled="isDisabled" :debounce="300"
                >提交</auth-button
            >
        </div>
    </div>
</template>

<script>
import uploadImgTipsModal from './manual-photo-modal';
import faceIdCard from '../assets/img/idcard-face.png';
import holdIdCard from '../assets/img/idcard-hold.png';
export default {
    name: 'Manual',
    props: {
        user: {
            type: Object,
            default: () => {
                return {};
            },
        },
    },
    data() {
        return {
            remark: {
                value: '',
                status: '',
                message: '',
            },
            mobile: {
                value: '',
                status: '',
                message: '',
            },
            imgSide: '',
            faceImgSrc: '',
            holdImgSrc: '',
            imgKey: {
                faceImgFileKey: '', // 为了解决不必现问题：拍照上传第二张图片会将第一张图片key清空，将key值放入对象中
                holdImgFileKey: '',
            },
            code: { label: '大陆', areaAbbr: 'ZH', code: '+86', value: '' },
            activeForm: null,
            isCurrentFormActive: false,
            isUseMail: false,
            isCanScroll: false,
            manualStatus: '',
        };
    },
    computed: {
        hasAccount() {
            return !!this.user.mobile;
        },
        isDisabled() {
            if (this.hasAccount) {
                return !this.faceImgSrc || !this.holdImgSrc || this.remark.status === 'error';
            }
            return (
                !this.faceImgSrc ||
                !this.holdImgSrc ||
                this.remark.status === 'error' ||
                !this.mobile.value ||
                this.mobile.status === 'error'
            );
        },
        cardName() {
            const cardMap = {
                IDCARD: '身份证',
                PASSPORT: '护照',
                HKMP: '港澳通行证',
                MTPS: '台胞证',
            };
            return cardMap[this.user.paperType];
        },
    },
    watch: {
        isCanScroll(value) {
            if (value) {
                this.$nextTick(() => {
                    this.$refs.outerWrapper.scrollIntoView();
                });
            }
        },
    },
    async created() {
        this.activeForm = this.$qUtils.debounce(id => {
            this.isCurrentFormActive = this.$refs[id] && this.$refs[id].isActive;
        }, 200);

        await this.hasAuditOrReject();
        if (this.manualStatus) {
            if (this.manualStatus === 'audit') {
                this.dealStep('MANUALING');
            }
            return;
        }
    },
    mounted() {
        document.addEventListener('touchstart', this.bindEvent, false);
    },
    beforeDestroy() {
        document.removeEventListener('touchstart', this.bindEvent, false);
    },
    methods: {
        bindEvent() {
            this.activeForm('mobile');
        },
        async hasAuditOrReject() {
            let { username: name, cardNo } = this.user;
            try {
                const { result } = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_MANUAL_AUDITREJECT',
                        data: {
                            userId: this.$_qyssdk_userId,
                            name,
                            cardNo,
                        },
                    }),
                });
                this.manualStatus = result.key;
            } catch (error) {
                console.log(error);
            }
        },
        async submit() {
            this.inputBlur();
            if (this.mobile.status === 'error') {
                return;
            }
            let { username, cardNo, mobile, paperType } = this.user;
            if (!this.hasAccount) {
                if (this.isUseMail) {
                    mobile = this.mobile.value;
                } else {
                    mobile = this.getContactWithCode(this.code.value, this.mobile.value);
                }
            }
            const params = {
                username,
                cardNo,
                paperType,
                mobile,
                front: this.imgKey.faceImgFileKey,
                handFront: this.imgKey.holdImgFileKey,
                description: this.remark.value,
            };
            let loading = this.$loading('正在提交');
            try {
                const res = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'UPLOAD_MANUAL_MATERIAL',
                        data: {
                            userId: this.$_qyssdk_userId,
                            material: params,
                        },
                    }),
                });
                this.dealStep(res.nextStep);
            } catch (error) {
                console.log(error);
            } finally {
                loading.close();
            }
        },
        uploadIdCard(type) {
            this.imgSide = type;
            let tips = [],
                img;
            if (type === 'faceSide') {
                if (this.faceImgSrc) {
                    this.uploadImg();
                    return;
                }
                img = faceIdCard;
                tips = ['示例图为身份证，请根据您的证件类型上传照片', '确保证件中的头像与文字清晰可辨、无反光'];
            } else if (type === 'hold') {
                if (this.holdImgSrc) {
                    this.uploadImg();
                    return;
                }
                img = holdIdCard;
                tips = [
                    '示例图为身份证，请根据您的证件类型上传照片',
                    '确保您的脸部与证件信息清晰可辨、无反光',
                    '确保证件没有左右倒置',
                ];
            }
            const instance = new Vue({
                components: { uploadImgTipsModal },
                render: h =>
                    h(uploadImgTipsModal, {
                        props: {
                            img,
                            tips,
                        },
                        on: {
                            goUploadImg: this.uploadImg,
                        },
                    }),
            }).$mount();
            document.body.appendChild(instance.$el);
        },
        async uploadImg() {
            const e = await this.$qUtils.chooseFile({
                accept: 'image/*',
            });
            this.uploadIdCardPic(e);
        },
        async uploadIdCardPic(e) {
            let loading = this.$loading('图片压缩上传中...');
            try {
                const blob = await this.uploadImageHandler(e);
                if (!blob) {
                    return;
                }

                const result = await this.postMulti('', {
                    file: blob,
                    requestData: JSON.stringify({
                        type: 'UPLOAD_MANUAL_FRONT',
                        data: {
                            userId: this.$_qyssdk_userId,
                        },
                    }),
                });
                const res = this.$qUtils.deepClone(result);

                if (this.imgSide === 'faceSide') {
                    this.faceImgSrc = window.URL.createObjectURL(blob);
                    this.imgKey.faceImgFileKey = res.result;
                } else if (this.imgSide === 'hold') {
                    this.holdImgSrc = window.URL.createObjectURL(blob);
                    this.imgKey.holdImgFileKey = res.result;
                }
            } catch (error) {
                console.log(error);
            } finally {
                loading.close();
            }
        },
        // 处理iOS键盘将页面顶上去，键盘收起页面不会自动下来的问题
        inpFocus(e) {
            this.isCanScroll = false;
        },
        handleIosScroll() {
            this.isCanScroll = true;
        },
        contactBlur() {
            this.activeForm('mobile');
            this.inputBlur();
        },
        trimData(id, type = 'all') {
            let val = this.info[id].value;
            if (val) {
                if (type === 'terminal') {
                    val = val.trim();
                } else if (type === 'all') {
                    val = val.replace(/\s/g, '');
                }
                this.info[id].value = val;
            }
            return val;
        },
        inputBlur() {
            let target = this.mobile.value.replace(/\s/g, '');
            let result = null;

            if (this.isUseMail) {
                result = this.$qUtils.validata({
                    value: target,
                    rules: [{ rule: this.$qUtils.validator.isEmail, level: 'error', message: '邮箱格式不正确' }],
                });
            } else {
                let regKey = `is${this.code.areaAbbr}Phone`;
                result = this.$qUtils.validata({
                    value: target,
                    rules: [{ rule: this.$qUtils.validator[regKey], level: 'error', message: '手机号码格式不正确' }],
                });
            }
            if (result.isInvalid) {
                this.mobile.status = result.status;
                this.mobile.message = result.message;
            } else {
                this.mobile.status = '';
                this.mobile.message = '';
            }
        },
        // 获取手机号码区号
        getAreaCode(codeItem) {
            this.code = codeItem;
        },
        toggleSelectCode(id) {
            // 解决ios不可聚焦的问题
            this.$refs[id].focus();
        },
    },
};
</script>
<style lang="less" scoped>
.manual-wrapper {
    width: 100%;
    background: white;
    .content {
        width: 100%;
        padding: 24px 20px;
        display: flex;
        flex-direction: column;
        .clear-icon {
            font-size: 14px;
            color: #e5e7ea;
            bottom: 8px;
        }
        .mobile-icon {
            font-size: 18px;
            color: #7f8997;
        }
        .title {
            margin-bottom: 16px;
        }
        .image-row {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            height: 110px;
            margin-bottom: 32px;
            .image-item {
                position: relative;
                width: calc(~'(100% - 15px) / 2');
                height: 100%;
                border-radius: 4px;
                background: @body-background;
                overflow: hidden;
                .img-operate {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    z-index: 9;
                    &.has-img {
                        background: rgba(0, 19, 48, 0.34);
                    }
                    .camera-fill-icon {
                        font-size: 30px;
                        color: #7f8997;
                        margin-bottom: 10px;
                        &.white-color {
                            color: #e5e7ea;
                        }
                    }
                    span.white-color {
                        color: white;
                    }
                }
                .upload-img {
                    position: absolute;
                }
            }
        }
        .info-inp-title {
            margin-bottom: 8px;
        }

        .submit-btn {
            margin-top: 40px;
        }

        .change-contact {
            position: absolute;
            right: 0;
            top: 7px;
            z-index: 9;
        }
    }
}
</style>
