<template>
    <transition name="modal">
        <div class="modal-mask">
            <div class="modal-container">
                <div class="modal-header">
                    <i class="iconfont iconclose" @click="close"></i>
                    短信验证码签署
                </div>
                <div class="modal-body">
                    <div class="form-item">
                        <span class="label">
                            <template v-if="isMail">
                                邮箱：
                            </template>
                            <template v-else>
                                手机号：
                            </template>
                        </span>
                        <strong style="font-size:20px;">{{ contactShow }}</strong>
                    </div>

                    <div class="form-item">
                        <span class="label">
                            验证码：
                        </span>
                        <div class="content">
                            <input
                                type="text"
                                @blur="resetScroll()"
                                v-model="pin"
                                placeholder="请输入验证码"
                                maxlength="6"
                            />
                            <div class="get-pin" v-bind:class="{ 'disabled-button': smsPinGetting }" @click="getSmsPin">
                                <template v-if="isFrist">
                                    获取验证码
                                </template>
                                <template v-else>
                                    重新获取
                                    <template v-if="smsPinGetting"> ({{ smsCountNumber }}) </template>
                                </template>
                            </div>
                        </div>
                    </div>
                    <div
                        v-if="canVoiceVerify"
                        :class="{ 'disabled-text': voicePinGetting }"
                        class="item-tip"
                        @click="voiceCaptcha"
                    >
                        <template v-if="!voicePinGetting">
                            收不到短信，试试语音验证码
                        </template>
                        <template v-else> 您即将收到语音电话({{ voiceCountNumber }}) </template>
                    </div>
                </div>
                <div class="modal-footer">
                    <div class="confirm-button" @click="submit">
                        确定
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
export default {
    data() {
        return {
            pin: null,
            isFrist: true,
            smsCountNumber: 0,
            voiceCountNumber: 0,
            canVoiceVerify: false,
        };
    },
    props: {
        contact: {
            type: String,
        },
        contractId: {
            type: String,
        },
    },
    watch: {
        smsCountNumber(val) {
            if (val === 40) {
                this.canVoiceVerify = true;
            }
        },
    },
    computed: {
        voicePinGetting() {
            return this.voiceCountNumber != 0;
        },
        smsPinGetting() {
            return this.smsCountNumber != 0;
        },
        contactShow() {
            if (this.isMail) {
                return this.contact.slice(0, 3) + '****' + this.contact.slice(7);
            } else {
                return this.contact;
            }
        },
        isMail() {
            let mail = /^[\.A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
            return mail.test(this.contact || '');
        },
    },
    methods: {
        submit() {
            this.$emit('signContract', this.pin);
            this.close();
        },
        close() {
            this.$emit('componentClose');
        },
        resetScroll() {
            setTimeout(() => {
                window.scrollTo(0, 0);
            }, 300);
        },
        async voiceCaptcha() {
            let interval = setInterval(() => {
                if (!this.voiceCountNumber) {
                    clearInterval(interval);
                } else {
                    this.voiceCountNumber--;
                }
            }, 1000);
            try {
                await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_SIGN_VOICE',
                        data: {
                            contractId: this.contractId,
                            userId: this.$_qyssdk_userId,
                        },
                    }),
                });
                this.voiceCountNumber = 60;
            } catch (e) {
                console.log(e);
            }
        },
        async getSmsPin() {
            this.isFrist = false;
            let interval = setInterval(() => {
                if (this.smsCountNumber === 0) {
                    clearInterval(interval);
                } else {
                    this.smsCountNumber--;
                }
            }, 1000);
            try {
                await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_SIGN_PIN',
                        data: {
                            contractId: this.contractId,
                            userId: this.$_qyssdk_userId,
                        },
                    }),
                });
                this.smsCountNumber = 60;
            } catch (e) {
                console.log(e);
            }
        },
    },
};
</script>

<style lang="less" scoped>
*,
*:before,
*:after {
    box-sizing: border-box;
}

.modal-mask {
    position: absolute;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    transition: opacity 0.3s ease;
}

.modal-container {
    width: 100%;
    position: absolute;
    bottom: 0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
    transition: all 0.3s ease;
    background: white;
    .modal-header {
        margin: auto;
        padding: 15px 0;
        border-bottom: 1px solid #eaeaeb;
        color: #383838;
        text-align: center;

        i {
            display: inline-block;
            float: left;
            margin-left: 15px;
            margin-right: -15px;
        }
    }
    .modal-body {
        padding: 16px;
        font-size: 14px;
        color: #383838;
        .form-item {
            display: flex;
            align-items: center;
            &:not(:last-child) {
                margin-bottom: 20px;
            }
            .label {
                width: 70px;
                flex-shrink: 0;
            }
            .content {
                justify-content: space-between;
                display: flex;
                width: 100%;
                input {
                    width: 100%;
                    background-color: #fff;
                    background-image: none;
                    border: 1px solid #eaeaeb;
                    border-radius: 4px;
                    color: #555;
                    font-size: 14px;
                    padding: 6px;
                    transition: border-color 0.15s ease-in-out 0s, box-shadow 0.15s ease-in-out 0s;
                    vertical-align: middle;
                }
                .get-pin {
                    width: 100px;
                    margin-left: 10px;
                    flex-shrink: 0;
                    line-height: 40px;
                    border: 1px solid #eaeaeb;
                    font-size: 14px;
                    color: #fff;
                    text-align: center;
                    background: #00a0e9;
                    &.disabled-button {
                        color: #cecece;
                        background: #f2f2f2;
                        pointer-events: none;
                    }
                }
            }
        }
        .item-tip {
            font-size: 12px;
            color: #00a0e9;
            margin-top: -15px;
            margin-left: 70px;
            &.disabled-text {
                color: #cecece;
                pointer-events: none;
            }
        }
    }
    .modal-footer {
        padding: 0 15px 35px;
        .confirm-button {
            height: 40px;
            width: 100%;
            color: #fff;
            background-color: #00a0e9;
            text-align: center;
            line-height: 40px;
        }
    }
}
</style>
