<template>
<transition name="modal">
    <div class="modal-mask" v-show="visible">
        <div class="modal-container">
            <div class="modal-body">
                <slot>
                    <div v-html="content"></div>
                </slot>
            </div>
            <div class="modal-footer">
                <button style="border-bottom-right-radius: 2px;" :class="{'only-one':!showCancel}" class="blue-text" @click="submit">{{okText?okText:"确定"}}</button>
                <button style="border-bottom-left-radius: 2px;" v-if="showCancel" @click="cancle">{{cancelText?cancelText:"取消"}}</button>
            </div>
        </div>
    </div>
</transition>
</template>
<script>
export default {
    name: 'modal',
    data() {
        return {
            visible: false,
            showCancel: false,
            cancelText: '',
            okText: '',
            content: '',
            okfn() {},
            cancel() {}
        };
    },
    props: {
        options: {
            default: false
        }
    },
    created: function() {
        if (this.options) {
            Object.assign(this.$data, this.options);
        }
    },
    watch: {
        options: {
            handler() {
                if (this.options) {
                    Object.assign(this.$data, this.options);
                }
            },
            deep: true
        }
    },
    methods: {
        submit: function() {
            this.okfn();
            this.visible = false;
        },
        cancle: function() {
            this.visible = false;
        },
        destroyNode() {
            if (this.visible) {
                return;
            }
            this.$el.removeEventListener('transitionend', this.destroyNode);
            this.$destroy(true);
            this.$el.parentNode.removeChild(this.$el);
        },
        changeOptions() {
            this.options.visible = this.visible;
        }
    },
    mounted() {
        if (!this.options) {
            this.$el.addEventListener('transitionend', this.destroyNode);
        } else {
            this.$el.addEventListener('transitionend', this.changeOptions);
        }
    }
};
</script>
<style lang="less" scoped>
@import './extend.less';
.modal-mask {
    background-color: rgba(0, 0, 0, 0.5);
    &:extend(.modal-mask);
}
.modal-container {
    width: 90%;
    &:extend(.middle-center);
    max-width: 400px;
    min-height: 100px;
    margin: 0 auto;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
    transition: all 0.3s ease;
    text-align: center;
}
.modal-body {
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    padding: 18px 16px;
    line-height: 1.5;
    background-color: #fff;
    color: #364766;
    text-align: center;
    max-height: 375px;
    overflow: auto;
    div {
        text-align: left;
        display: inline-block;
    }
}
.modal-footer {
    background-color: #fff;
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
    border-top: 1px solid #eaeaeb;
    height: 50px;

    button {
        height: 100%;
        border-radius: 0;
        width: 50%;
        background-color: #fff;
        border: none;
        font-size: 16px;
        color: #999;
        padding: 0;
        display: inline-block;
        float: right;
        &:not(:last-child) {
            border-left: 1px solid #dcdfe6;
        }
    }

    .only-one {
        width: 100%;
        border-bottom-left-radius: 2px;
    }
}
</style>
