<template>
    <div class="skeleton" :style="{ padding, height: `${height}px`, width: `${width}px` }">
        <ul class="skeleton-wrapper">
            <li class="skeleton-item" v-for="i in itemLength" :key="i" :style="itemStyle"></li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'Skeleton',
    props: {
        padding: {
            type: String,
            default: '42px 42px',
        },
        height: {
            type: Number,
            default: 0, // P.S. ip5:453 ip6:530.7
            required: true,
        },
        widthMM: {
            type: Number,
            default: 1,
            required: true,
        },
        heightMM: {
            type: Number,
            default: 1,
            required: true,
        },
        itemHeight: {
            type: Number,
            default: 16,
        },
        itemMarginBottom: {
            type: Number,
            default: 16,
        },
    },
    computed: {
        paddingObj() {
            const tmp = this.padding.split(/\s/);
            if (tmp.length === 3) {
                tmp[3] = tmp[1];
            } else if (tmp.length === 2) {
                tmp[3] = tmp[1];
                tmp[2] = tmp[0];
            } else if (tmp.length === 1) {
                tmp[3] = tmp[0];
                tmp[2] = tmp[0];
                tmp[1] = tmp[0];
            }

            const [top, right, bottom, left] = tmp.map(str => parseInt(str));

            return {
                top,
                right,
                bottom,
                left,
            };
        },

        width() {
            return (this.height / this.heightMM) * this.widthMM;
        },

        itemLength() {
            const { top, bottom } = this.paddingObj;
            const viewHeight = this.height - top - bottom;
            const itemOccupy = this.itemHeight + this.itemMarginBottom;

            return Math.floor((viewHeight + this.itemMarginBottom) / itemOccupy);
        },

        itemStyle() {
            return {
                height: `${this.itemHeight}px`,
                marginBottom: `${this.itemMarginBottom}px`,
            };
        },
    },
};
</script>

<style lang="less" scoped>
@keyframes ant-skeleton-loading {
    0% {
        background-position: 100% 0;
    }
    100% {
        background-position: 0% 0;
    }
}

.skeleton {
    max-width: 100%;
    width: 100%;
    height: 100%;
    background: #fff;

    .skeleton-wrapper {
        overflow: hidden;
        display: block;
        margin: 0;
        padding: 0;
        height: 100%;
        list-style: none;

        .skeleton-item {
            width: 100%;
            background: linear-gradient(90deg, #f2f2f2 25%, #e6e6e6 37%, #f2f2f2 63%);
            background-size: 400% 100%;
            animation: ant-skeleton-loading 1.4s ease infinite;

            &:nth-of-type(4n) {
                width: 36%;
            }
            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }
}
</style>