<template>
    <div ref="outerWrapper" class="auth-graphic-code" @touchmove.prevent @click="maskClose ? close : ''">
        <div class="code-content">
            <div class="h2-title title">{{ title }}</div>
            <div class="graphic">
                <img class="graphic-img" :src="captchaSrc" />
                <div @click="refreshCaptcha" class="main-word link-color refresh-btn">
                    <q-icon class="reload-icon" icon="reload"></q-icon>
                    <span>换一张</span>
                </div>
            </div>
            <input
                ref="codeIpt"
                @focus="isCanScroll = false"
                @blur="
                    code = code.replace(/\s/g, '');
                    isCanScroll = true;
                "
                class="code-input"
                v-model="code"
                placeholder="请输入图形验证码"
            />
            <div class="footer">
                <button @click="close" class="btn cancel-btn"><span class="middle-center">取消</span></button>
                <button @click="code ? getCode() : ''" class="btn sure-btn" :class="{ 'is-active': code }">
                    <span class="middle-center">确定</span>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'AuthGraphicCode',
    props: {
        title: {
            type: String,
            default: '',
        },
        maskClose: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            captchaToken: '',
            timeStamp: new Date().getTime(), // 用于图形验证码 动态更新使用
            code: '',
            isCanScroll: false,
        };
    },
    computed: {
        captchaSrc() {
            const requestData = {
                type: 'DOWNLOAD_AUTH_CAPTCHA',
                data: {
                    userId: this.$_qyssdk_userId,
                    captchaToken: this.captchaToken,
                    type: this.$_qyssdk_options.callType,
                },
            };
            const queryStr = (() => {
                let str = '';
                Object.keys(window.qysQuery).forEach(key => {
                    str += `&${key}=${window.qysQuery[key]}`;
                });
                return str;
            })();
            return `${this.configApi}?${encodeURI(`requestData=${JSON.stringify(requestData)}${queryStr}`)}&timeStamp=${
                this.timeStamp
            }`;
        },
    },
    watch: {
        isCanScroll(value) {
            if (value) {
                this.$nextTick(() => {
                    this.$refs.outerWrapper.scrollIntoView();
                });
            }
        },
    },
    async created() {
        await this.captchaPrepare();
    },
    methods: {
        async captchaPrepare() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_CAPTCHA_INIT',
                    data: { userId: this.$_qyssdk_userId },
                }),
            });
            this.captchaToken = res.result;
        },
        refreshCaptcha() {
            this.timeStamp = new Date().getTime();
        },
        close() {
            this.$destroy(true);
            const el = this.$el;
            el.parentNode.removeChild(el);
        },
        getCode() {
            this.$emit('getCode', this.code, this.captchaToken);
            this.close();
        },
    },
};
</script>
<style lang="less" scoped>
.auth-graphic-code {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    .code-content {
        position: relative;
        width: 76%;
        background: white;
        border-radius: 6px;
        padding: 24px 24px 0;
        .title {
            margin-bottom: 16px;
            text-align: center;
        }
        .graphic {
            margin-bottom: 16px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .graphic-img {
                width: 145px;
                height: 40px;
            }
            .refresh-btn {
                margin-left: 10px;
                .reload-icon {
                    margin-right: 3px;
                }
            }
        }
        .code-input {
            width: 100%;
            height: 40px;
            border: 1px solid #e5e7ea;
            border-radius: 3px;
            outline: none;
            margin-bottom: 64px;
            padding: 5px 12px;
            &::placeholder {
                line-height: 20px !important;
                font-size: 16px;
            }
            &:active,
            &:focus {
                border: 1px solid @primary-color;
            }
        }
        .footer {
            position: absolute;
            width: 100%;
            height: 48px;
            border-top: 1px solid #e5e7ea;
            display: flex;
            bottom: 0;
            left: 0;
            .btn {
                position: relative;
                flex: 1;
                display: flex;
                justify-content: center;
                align-items: center;
                height: 100%;
                border: none;
                background: white;
                font-size: 16px;
                color: #7f8997;
                &.cancel-btn {
                    border-radius: 0 0 0 6px;
                    border-right: 1px solid #e5e7ea;
                }
                &.sure-btn {
                    border-radius: 0 0 6px 0;
                    color: #c1c6cd;
                    &.is-active {
                        color: #1078f1;
                    }
                }
            }
        }
    }
}
</style>
