let instance = null;
async function uploadImageHandler(e) {
    const target = e.target;
    if (window.FileReader) {
        let reader = new FileReader();
        let fileArr = target.files[0].name.split('.');
        let fileType = fileArr[fileArr.length - 1].toLowerCase();
        let fileSize = Number(Math.round((target.files[0].size * 100) / (1024 * 1024)) / 100);
        if (fileType !== 'png' && fileType !== 'jpg' && fileType !== 'jpeg') {
            instance.$message('请上传图片类型文件！');
            return false;
        } else if (fileSize > 20) {
            // 选择图片的时候做第一步拦截,压缩图片时会进一步拦截，上传文件的大小不能超过20M
            instance.$message('您上传的图片太大了！');
            return false;
        } else {
            try {
                // 压缩操作, 压缩后最大限制2M
                const result = await instance.$qUtils.compressImg(target.files[0], {
                    outputType: 'blob',
                    maxDefaultSize: 10,
                    quality: 1,
                    type: 'jpeg',
                    maxWidth: 360,
                    maxHeight: 540,
                });
                return result;
            } catch (err) {
                // 压缩异常
                console.log(err);
            }

            reader.onload = function(event) {
                reader = '';
            };
            reader.onerror = function(event) {
                instance.$message('读取图片失败，请重新选择！');
            };
        }
    } else {
        instance.$message('浏览器插件不支持');
    }
}

const install = function(Vue, opts = {}) {
    Vue.prototype.uploadImageHandler = function(e) {
        instance = this;
        return uploadImageHandler.bind(this)(e);
    };
};

export default {
    install,
};
