<template>
    <div class="content">
        <template v-if="status == 'init'">
            <strong class="header">
                人证比对验证
            </strong>
            <img
                width="110px"
                src="./assets/img/face-auth.png"
                alt=""
                @click="triggerUploadFacePic"
                style="margin-bottom:15px;"
            />
            <div class="tips">
                请拍摄您的正脸照片<br />
                以确保当前签署人的身份真实性
            </div>
            <div class="footer">
                若当前浏览器不支持拍照<br />
                请使用本地相机现场拍摄您的正脸照片，然后上传
            </div>
        </template>
        <template v-if="status == 'loading'">
            <strong class="header">
                人证比对验证
            </strong>
            <img width="120px" src="./assets/img/face-auth.gif" alt="" style="margin-bottom:15px;" />
            <div class="tips">
                正在与公安系统中的人脸数据进行比对...
            </div>
        </template>
        <template v-if="status == 'failed'">
            <div class="error-tip">
                <i class="iconfont iconexclamationcircle failed"></i>
                比对结果：人脸匹配度较低
            </div>
            <img width="200px" :src="imgSrc" alt="" style="margin-bottom:35px;" />
            <div class="re-take-photo" style="margin-bottom:30px;" @click="triggerUploadFacePic">
                重新拍摄
            </div>
        </template>
        <template v-if="status == 'success'">
            <i class="iconfont iconcheckcircle success-icon"></i>
            <div class="auth-result">
                验证通过
            </div>
            <div class="tips">
                请在电脑端继续您的操作
            </div>
        </template>
        <input type="file" v-show="false" @change="uploadFacePic" id="photo" />
    </div>
</template>

<script>
export default {
    data() {
        return {
            status: 'init',
            imgSrc: '',
        };
    },
    async created() {},
    computed: {},
    props: {
        activityId: '',
        contractId: '',
    },
    methods: {
        async uploadFacePic(e) {
            let type = e.target.files[0].type;
            if (!type.match(/jpg|JPG|jpeg|JPEG|gif|GIF|png|PNG/i)) {
                this.$message('目前支持的图片类型为 .jpg,.jpeg,.png,.gif');
                this.status = 'init';
                this.imgSrc = '';
                return;
            }

            try {
                let input = document.getElementById('photo');
                if (input.files && input.files[0]) {
                    this.status = 'loading';
                    let file = await this.$qUtils.compressImg(input.files[0], {
                        outputType: 'blob',
                        quality: 1,
                        type: 'png',
                    });
                    this.imgSrc = window.URL.createObjectURL(file);
                    let data = await this.postMulti(
                        '',
                        {
                            file,
                            requestData: JSON.stringify({
                                type: 'GET_FACESIGN_CHECK',
                                data: {
                                    contractId: this.contractId,
                                    userId: this.$_qyssdk_userId,
                                    activityId: this.activityId,
                                    type: 'face_sign',
                                },
                            }),
                        },
                        {
                            headers: {
                                noMes: 'true',
                            },
                        }
                    );
                    if (data.facePhotoKey) {
                        this.status = 'success';
                        this.$emit('setFacePhotoKey', data.facePhotoKey);
                        this.$emit('componentClose');
                    } else {
                        this.status = 'failed';
                    }
                }
            } catch (e) {
                console.log(e);
            } finally {
            }
        },
        triggerUploadFacePic() {
            document.getElementById('photo').click();
        },
    },
};
</script>
<style lang="less" scoped>
.re-take-photo {
    color: #fff;
    background-color: #00a0e9;
    text-align: center;
    width: 100%;
    height: 44px;
    font-size: 16px;
    line-height: 44px;
    border: 0;
    border-radius: 3px;
}
.content {
    background: white;
    height: 100%;
    padding: 60px 30px 0;
    position: fixed;
    z-index: 999;
    top: 0px;
    left: 0px;
    right: 0px;
    overflow: auto;
    text-align: center;
    box-sizing: border-box;
    .header {
        font-size: 18px;
        color: #303133;
        margin-bottom: 70px;
        line-height: 30px;
        display: block;
    }
    .auth-result {
        font-size: 18px;
        color: #273039;
        line-height: 20px;
        margin-bottom: 10px;
    }
    .footer {
        font-size: 12px;
        color: #999;
        position: fixed;
        line-height: 20px;
        bottom: 20px;
        left: 0;
        width: 100%;
        padding: 0px 10px;
        box-sizing: border-box;
    }
    .tips {
        font-size: 14px;
        color: #666666;
        line-height: 20px;
        margin-bottom: 10px;
    }
    .success-icon {
        font-size: 60px;
        margin-bottom: 10px;
        margin-top: 70px;
        display: inline-block;
        color: #00cc99;
    }
    .error-tip {
        font-size: 14px;
        color: #f74555;
        text-align: center;
        line-height: 20px;
        margin-bottom: 20px;
    }
}
</style>
