<template>
    <div class="content">
        <component
            :is="status"
            :mode="mode"
            :status.sync="status"
            :user="user"
            :auth="auth"
            :successPage="successPage"
            :step="step"
            :otherModes="otherModes"
            :channel="channel"
            :canEditBankPhone="canEditBankPhone"
            :staticInfo="staticInfo"
            :modifyFieldList="modifyFieldList"
            :reverifyCert="reverifyCert"
            :accountPrecheck="accountPrecheck"
            @updateStep="getUserInfo"
            @goPersonalSign="goPersonalSign"
            @refetchUserInfo="refetchUserInfo"
            @reVerify="reVerify"
        ></component>
    </div>
</template>
<script>
import AuthGraphicCode from '@mobile-common/auth-graphic-code.vue';
import authInformation from './auth-information';
import defaultFail from './auth-default/default-fail';
import certifyCode from './auth-default/default-certify-code.vue';
import faceAuth from './auth-face/face';
import faceFail from './auth-face/face-fail';
import threeElementsAuth from './auth-three-elements/three-elements';
import bankAuth from './auth-bank/bank';
import bankFail from './auth-bank/bank-fail';
import manualAuth from './auth-manual/manual';
import manualInAudit from './auth-manual/manual-in-audit';
import manualFail from './auth-manual/manual-fail';

export default {
    name: 'Auth',
    components: {
        authInformation,
        defaultFail,
        certifyCode,
        faceAuth,
        faceFail,
        threeElementsAuth,
        bankAuth,
        bankFail,
        manualAuth,
        manualInAudit,
        manualFail,
    },
    props: {
        signatoryId: {
            type: String,
        },
        contractId: {
            type: String,
        },
        certificationExtraData: {
            type: Object,
        },
    },
    data() {
        return {
            status: '',
            mode: '', // 认证方式
            channel: '', // 认证来源
            successPage: '',
            user: {
                username: '',
                cardNo: '',
                mobile: '',
                contact: '',
            },
            auth: true, // 录入信息是否可改，手机号永不可改
            canEditBankPhone: true, // 是否可改银行预留手机号
            step: '',
            otherModes: '', // 降级处理方式'IVS,FACE,BANK,MANUAL'
            isManual: false,
            staticInfo: {},
            modifyFieldList: [], // 可以修改的字段
            reverifyCert: '',
            alipay: false,
            accountPrecheck: false, // 认证前是否需要校验手机号接口
        };
    },
    async created() {
        await this.judgeBusiness();
    },
    methods: {
        goPersonalSign() {
            this.$emit('goPersonalSign');
        },
        async reAuth() {
            // do nothing
        },
        async judgeBusiness() {
            try {
                const res = await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'GET_MANUAL_SHOWMANUAL',
                            data: { userId: this.$_qyssdk_userId },
                        }),
                    },
                    { headers: { noMes: 'true' } }
                );
                // 失效 || 过期
                if ([2002, 401, 402, 403].includes(res.code)) {
                    this.dealStep('INVALID');
                    return;
                }
                if (res.code === 0) {
                    this.isManual = res.result;
                    if (res.result) {
                        await this.getManualAuthInfo();
                    }
                    await this.phoneCheck();
                    await this.getUserInfo();
                }
            } catch (error) {
                console.log(error);
            }
        },
        async phoneCheck() {
            const { result } = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_PHONECHECK',
                    data: { userId: this.$_qyssdk_userId },
                }),
            });
            this.accountPrecheck = result;
        },
        async getManualAuthInfo() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_MANUAL_DETAIL',
                    data: { userId: this.$_qyssdk_userId },
                }),
            });
            if (res.$get('result', 'status', 'key') === 'audit') {
                this.dealStep('MANUALING');
            }
            if (res.$get('result', 'status', 'key') === 'reject') {
                this.dealStep('MANUALFAILED');
            }
        },
        async refetchUserInfo() {
            await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_CERTIFICATION',
                    data: Object.assign({ userId: this.$_qyssdk_userId }, this.certificationExtraData),
                }),
            });
            await this.judgeBusiness();
        },
        async reVerify() {
            try {
                const { result } = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_AUTH_REVERIFY',
                        data: {
                            userId: this.$_qyssdk_userId,
                        },
                    }),
                });
                this.reverifyCert = result.certificate;
            } catch (e) {
                console.log(e);
            }
        },
        async getUserInfo() {
            try {
                const res = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_AUTH_DETAIL',
                        data: {
                            userId: this.$_qyssdk_userId,
                        },
                    }),
                });
                const { result } = this.decryptoData(res.result);

                this.user = result.authInfo;
                this.auth = result.auth;
                this.step = result.step;
                this.canEditBankPhone = !!result.canEditBankPhone;
                this.mode = result.mode;
                this.otherModes = result.otherModes;
                this.channel = result.channel;
                this.successPage = result.successPage;
                this.staticInfo = {
                    mobile: result.mobile,
                    username: result.username,
                    cardNo: result.cardNo,
                    bankNo: result.bankNo,
                    contact: result.contact,
                };
                this.modifyFieldList = (result.modifyFields || '').split(',');

                if (this.isManual) {
                    return;
                }
                if (this.accountPrecheck && this.user.mobile) {
                    this.checkAccount();
                    return;
                }
                if (this.accountPrecheck && !this.user.mobile) {
                    this.dealStep('threeElementsAuth');
                    return;
                }
                if (result.mode === 'FACE') {
                    if (result.authInfo.realname && result.step === 'END') {
                        this.dealStep('SUCCESS');
                    } else if (!result.authInfo.realname && result.step === 'END') {
                        this.dealStep('FACEFAILED');
                    } else {
                        this.dealStep('INFO');
                    }
                } else {
                    if (result.authInfo.realname) {
                        this.dealStep('SUCCESS');
                    } else if (!result.authInfo.realname && result.step === 'END') {
                        this.dealStep('INVALID');
                    } else {
                        this.dealStep('INFO');
                    }
                }
            } catch (error) {
                console.log(error);
            }
        },
        async checkAccount() {
            const { result } = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_CAPTCHAREQUIRED',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            if (!result) {
                this.getCode(null, result);
                return;
            }
            const instance = new Vue({
                components: { AuthGraphicCode },
                render: h =>
                    h(AuthGraphicCode, {
                        props: {
                            title: '输入图形验证码',
                        },
                        on: {
                            getCode: (captcha, captchaToken) => {
                                this.getCode(captcha, result, captchaToken);
                            },
                        },
                    }),
            }).$mount();
            document.body.appendChild(instance.$el);
        },
        async getCode(captcha, validCaptcha, captchaToken = null) {
            await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_PIN',
                    data: {
                        userId: this.$_qyssdk_userId,
                        mobile: this.user.mobile,
                        validCaptcha,
                        captchaToken,
                        captcha,
                    },
                }),
            });
            const vueOptions = {
                components: { certifyCode },
                render: h =>
                    h(certifyCode, {
                        props: {
                            root: this,
                            user: this.user,
                            mobile: this.user.mobile,
                            isUseMail: this.isUseMail,
                            accountPrecheck: this.accountPrecheck,
                            otherModes: this.otherModes,
                            channel: this.channel,
                        },
                    }),
            };
            let elm = new Vue(vueOptions).$mount();
            document.body.appendChild(elm.$el);
        },
        async goNext() {
            try {
                const res = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_AUTH_NEXTSTEP',
                        data: {
                            userId: this.$_qyssdk_userId,
                        },
                    }),
                });
                this.dealStep(res.result);
            } catch (error) {
                console.log(error);
            }
        },
    },
};
</script>
<style lang="less">
@import '../assets/css/normalize.less';
@import '../assets/css/common.less';

.icon {
    width: 1em;
    height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
}
</style>
<style scoped lang="less">
*,
*:before,
*:after {
    box-sizing: border-box;
}
.content {
    position: relative;
    overflow: auto;
}
</style>
