<template>
    <div class="default-fail">
        <div class="default-result-top">
            <q-icon class="exit-auth" icon="export" @click="showExitAuthModal"></q-icon>
            <img class="res-img" src="../assets/img/default-result-fail.png" />
            <span class="h2-title title">认证失败</span>
            <span class="tip-large">姓名、身份证号与手机号实名信息不匹配</span>
        </div>

        <div class="default-result-bottom">
            <span class="main-word title-color title">核对身份信息</span>
            <div class="info-row">
                <span class="main-word regular-color">姓&nbsp;&nbsp;&nbsp;&nbsp;名</span
                ><span class="main-word">{{ user.username }}</span>
            </div>
            <div class="info-row">
                <span class="main-word regular-color">证件类型</span><span class="main-word">身份证</span>
            </div>
            <div class="info-row">
                <span class="main-word regular-color">证件号</span><span class="main-word">{{ user.cardNo }}</span>
            </div>
            <div class="info-row">
                <span class="main-word regular-color">手机号</span>
                <span class="main-word">{{ isUseMail ? mobile : user.mobile }}</span>
            </div>
        </div>
        <div class="modify-info main-word body-color-dark" type="primary" @click="modifyInfo">
            修改信息重新认证
        </div>
        <div v-if="isShowOtherWays" class="change-other main-word" type="primary" @click="openAuthWaysModal">
            尝试其他认证方式
        </div>
    </div>
</template>

<script>
import authWaysModal from '../auth-common/auth-ways.vue';

export default {
    name: 'DefaultFail',
    props: {
        user: {
            type: Object,
            default: () => {
                return {};
            },
        },
        successPage: {
            type: String,
            default: '',
        },
        otherModes: {
            type: String,
            default: '',
        },
        channel: {
            type: String,
            default: '',
        },
        step: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            hasHoldCardPic: false,
            hasManualed: false,
            otherAuthWays: [],
            mobile: '',
        };
    },
    computed: {
        degradeToManual() {
            return this.otherAuthWays.includes('MANUAL');
        },
        degradeToIvs() {
            return this.otherAuthWays.includes('IVS');
        },
        degradeToFace() {
            return this.otherAuthWays.includes('FACE');
        },
        isShowOtherWays() {
            let authWays = this.$qUtils.deepClone(this.otherAuthWays);

            let canManual = !this.hasManualed && this.degradeToManual && !this.hasHoldCardPic;
            if (canManual) {
                return true;
            }
            if (!canManual && authWays.every(item => item === 'MANUAL')) {
                return false;
            }
            return !!this.otherAuthWays.length;
        },
        isUseMail() {
            let result = this.$qUtils.validata({
                value: this.user.mobile,
                noValNotValid: false,
                rules: [{ rule: this.$qUtils.validator.isEmail }],
            });
            return !result.isInvalid;
        },
    },
    async created() {
        this.otherAuthWays = await this.$getAuthWays();

        await this.judgeManualed();
        await this.judgeCardPictureType();

        const res = await this.postForm('', {
            requestData: JSON.stringify({
                type: 'GET_AUTH_DETAIL',
                data: {
                    userId: this.$_qyssdk_userId,
                },
            }),
        });
        const { result } = this.decryptoData(res.result);
        this.mobile = result.authInfo.cellphone || '';
    },
    methods: {
        async judgeManualed() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_PASS_BEFORE',
                    data: {
                        userId: this.$_qyssdk_userId,
                        name: this.user.username,
                        cardNo: this.user.cardNo,
                    },
                }),
            });
            this.hasManualed = res.result;
        },
        async judgeCardPictureType() {
            try {
                const res = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_AUTH_IMAGE_EXIST',
                        data: {
                            userId: this.$_qyssdk_userId,
                            name: this.user.username,
                            cardNo: this.user.cardNo,
                        },
                    }),
                });
                this.hasHoldCardPic = res.handfrontImage;
            } catch (error) {
                console.log(error);
            }
        },
        async modifyInfo() {
            this.$emit('reVerify');
            this.dealStep('INFO');
        },
        openAuthWaysModal() {
            const instance = new Vue({
                components: { authWaysModal },
                render: h =>
                    h(authWaysModal, {
                        props: {
                            root: this,
                            information: this.user,
                            user: this.user,
                            channel: this.channel,
                            otherModes: this.otherModes,
                            step: this.step,
                        },
                    }),
            }).$mount();
            document.body.appendChild(instance.$el);
        },
        // 禁止ios橡皮筋效果
        preventTouchmove(e) {
            e.preventDefault();
        },
        async showExitAuthModal() {
            try {
                await this.$mt.MessageBox.confirm('', {
                    message: '确定退出认证流程？',
                    confirmButtonClass: 'main-word link-color',
                });
                this.exitAuth();
            } catch (e) {
                console.log(e);
            }
        },
        exitAuth() {
            this.$giveUpAuth({ channel: this.channel, successPage: this.successPage }, window.vueInstance);
        },
    },
};
</script>
<style lang="less" scoped>
.default-fail {
    width: 100%;
    height: 100%;
    background: @body-background;
    .default-result-top {
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 19px 20px 28px;
        background: white;
        margin-bottom: 10px;
        position: relative;
        .exit-auth {
            position: absolute;
            top: 16px;
            right: 16px;
            font-size: 18px;
            color: #c1c6cd;
        }
        .res-img {
            width: 138px;
        }
        .title {
            margin-top: -10px;
        }
    }
    .default-result-bottom {
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        padding: 16px 20px;
        background: white;
        .title {
            flex-shrink: 0;
            margin-bottom: 16px;
        }
        .info-row {
            flex-shrink: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 12px;
            > :last-child {
                text-align: right;
            }
        }
        .modify-btn {
            margin-top: 28px;
            margin-bottom: 16px;
        }
        .change-btn {
            margin-bottom: 8px;
        }
    }

    .modify-info {
        padding: 10px 0px;
        background: @primary-color;
        border: 1px solid #2489f2;
        border-radius: 4px;
        text-align: center;
        margin: 16px;
    }
    .change-other {
        margin: 0 16px 16px 16px;
        padding: 10px 0px;
        border-radius: 4px;
        text-align: center;
        border: 1px solid #2489f2;
        color: #2489f2;
        margin-right: 11px;
        background: #fff;
    }
}
</style>
