<template>
    <transition name="modal">
        <div class="modal-mask" v-show="visible">
            <div class="modal-wrapper">
                <div v-html="msg"></div>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
    data() {
        return {
            visible: true,
            msg: '',
            time: '',
        };
    },
    methods: {
        destroyNode() {
            if (this.visible) {
                return;
            }
            this.$el.removeEventListener('transitionend', this.destroyNode);
            this.$destroy(true);
            this.$el.parentNode.removeChild(this.$el);
        },
    },
    created() {
        const self = this;
        let time = self.time || 1500;
        setTimeout(() => {
            self.visible = false;
        }, time);
    },
    mounted() {
        this.$el.addEventListener('transitionend', this.destroyNode);
    },
};
</script>

<style lang="less" scoped>
@import './extend.less';
.modal-mask {
    &:extend(.modal-mask);
}

.modal-wrapper {
    &:extend(.middle-center);
    display: inline-block;
    max-width: 90%;
    min-width: 120px;
    transition: all 0.3s ease;
    font-family: Helvetica, Arial, sans-serif;
    text-align: center;
    text-align: center;
    &.is-tr {
        transform: rotate(90deg);
        -moz-transform: rotate(90deg);
        /* Firefox */
        -webkit-transform: rotate(90deg);
        /* Safari 和 Chrome */
        -o-transform: rotate(90deg);
    }
    border-radius: 4px;
    padding: 8px 8px 6px;
    line-height: 1.5;
    background-color: rgba(0, 0, 0, 0.6);
    color: #fff;
    text-align: center;

    > div {
        text-align: left;
        display: inline-block;
    }
}
.modal-footer {
    background-color: #fff;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    border-top: 1px solid #eaeaeb;
    height: 39px;
    width: 100%;

    button {
        height: 100%;
        width: 100%;
        border-radius: 0;
        background-color: #fff;
        border: none;
        color: #418ad3;
        font-size: 16px;
        padding: 0;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
    }
}
</style>
