<template>
    <div class="modal-mask" @click="close">
        <transition
            name="custom-classes-transition"
            enter-active-class="animated fadeInDown"
            leave-active-class="animated fadeOutUp"
        >
            <div class="modal-container">
                <div v-for="(doc, index) in documentsList" @click="choose(index)" class="list ellipsis">
                    <span>{{ index + 1 }}.</span>{{ doc.title }}
                    <i
                        class="iconfont iconfuxuankuang blue-text"
                        v-if="index == currentDocIndex"
                        style="right: 15px;position: absolute;"
                    ></i>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
export default {
    data() {
        return {};
    },
    props: {
        documentsList: {
            type: Array,
            required: true
        },
        currentDocIndex: {
            default: 0
        }
    },
    methods: {
        choose(index) {
            this.$emit('chooseContract', index);
        },
        close() {
            this.$emit('componentClose');
        }
    }
};
</script>


<style lang="less" scoped>
.modal-mask {
    position: absolute;
    z-index: 199;
    top: 0;
    left: 0;
    right: 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    transition: opacity 0.3s ease;
}
.animated {
    animation-duration: 0.3s;
    -webkit-animation-duration: 0.3s; /* Safari 和 Chrome */
}

.modal-container {
    width: 100%;
    margin-top: 50px;
    background-color: #fff;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
    transition: all 0.3s ease;
    font-family: Helvetica, Arial, sans-serif;
}

.list {
    border-bottom: 1px solid #eaeaeb;
    line-height: 2em;
    display: block;
    position: relative;
    color: #333;
    text-decoration: none;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 5px 35px 5px 15px;
    &:hover {
        background-color: #f2f2f2;
    }
}
</style>
