<template>
    <div
        ref="signPad"
        class="sign-pad"
        :class="{ 'in-vertical-direction': direction === 'vertical' }"
        :style="containerStyle"
    >
        <div class="sign-title">
            <div></div>
            请绘制清晰可辨的签名
            <i class=" iconfont iconclose" @click="close"></i>
        </div>
        <div ref="signContent" class="sign-content">
            <div ref="signCanvasWrapper" class="sign-canvas-wrapper" :style="signCanvasWrapperStyle">
                <canvas
                    ref="signCanvas"
                    class="sign-canvas"
                    @touchstart="writeStart"
                    @touchend="writeEnd"
                    @touchmove="writeMove"
                ></canvas>
            </div>
        </div>
        <div class="sign-footer">
            <div style="width:140px;">
                <i class="iconfont iconhuabi3" :class="{ blue: lineWidth == 'tiny' }" @click="changeWidth('tiny')"></i>
                <i
                    class="iconfont iconhuabi4"
                    :class="{ blue: lineWidth == 'medium' }"
                    @click="changeWidth('medium')"
                ></i>
                <i
                    class="iconfont iconhuabi5"
                    :class="{ blue: lineWidth == 'large' }"
                    @click="changeWidth('large')"
                ></i>
            </div>
            <div>
                <button class="clear" @click="clear">清除</button>
                <button class="save" @click="save">保存</button>
            </div>
            <div>
                <div
                    @click="changePenColor('red')"
                    :class="{ 'outer-circle': penColor == 'red' }"
                    class="red circle "
                ></div>
                <div
                    @click="changePenColor('blue')"
                    :class="{ 'outer-circle': penColor == 'blue' }"
                    class="blue circle"
                ></div>
                <div
                    @click="changePenColor('black')"
                    :class="{ 'outer-circle': penColor == 'black' }"
                    class="black circle"
                ></div>
            </div>
        </div>
    </div>
</template>

<script>
import SignaturePad from 'signature_pad';
export default {
    data() {
        return {
            bodyHeight: '',
            signaturePad: '',
            lineWidth: 'medium',
            penColor: 'black',
            direction: '',
            userOrientationDb: null,
            bodyClientHeight: 0,

            containerSize: {}, // 根节点大小
            signCanvasWrapperSize: {}, // canvas容器大小
        };
    },
    props: {
        selectSealOptions: {
            default: {},
        },
        contractId: {
            type: String,
        },
    },
    computed: {
        isModify() {
            return !!this.selectSealOptions.modifyStamperId;
        },
        headerStyle() {
            return {
                width: this.bodyHeight + 1.5 + 'px',
                right: 23.5 - this.bodyHeight / 2 + 'px',
                top: this.bodyHeight / 2 - 25 + 'px',
            };
        },
        footerStyle() {
            return {
                width: this.bodyHeight + 'px',
                left: 24 - this.bodyHeight / 2 + 'px',
                top: this.bodyHeight / 2 - 26 + 'px',
            };
        },
        signCanvasWrapperStyle() {
            // direction === 'vertical' 的时候，canvas不知道(FIXME:)为啥会有白色背景色（会遮住placeholder字样），暂时加一个透明效果
            const opacity = this.isSignaturePadEmpty ? 0 : 1;
            return { opacity, ...this.signCanvasWrapperSize };
        },
        // 根节点样式
        containerStyle() {
            const opacity = this.isUserOrientationCalled ? 1 : 0; // !Note: 在第一次界面初始化完(布局计算完)时，才显示给用户
            const top = this.isIphone6sInWx && this.bodyClientHeight == 343 ? '20px' : '0';
            return { opacity, top, ...this.containerSize };
        },
    },
    watch: {
        signCanvasWrapperSize() {
            // 等 signCanvasWrapper (的大小)随新的 signCanvasWrapperSize 渲染完毕
            this.$nextTick(() => {
                this.refreshCanvas();
            });
        },
    },
    mounted() {
        this.userOrientationDb = this.$qUtils.debounce(() => {
            this.userOrientation();
        }, 150);
        const ua = navigator.userAgent;
        if (/iphone/i.test(ua) && /MicroMessenger/i.test(ua) && screen.height == 667 && screen.width == 375) {
            this.isIphone6sInWx = true;
        }
        this.userOrientationDb();
        window.addEventListener('resize', this.userOrientationDb, false);

        this.ratio = Math.max(window.devicePixelRatio || 1, 1);
        setTimeout(() => {
            if (this.isPCSignPage && this.$route.query.sealId) {
                const sealId = this.$route.query.sealId;
                this.replaceSignatureIdFromQuery = sealId;
            }
        }, 0);
    },
    beforeDestroy() {
        window.removeEventListener('resize', this.userOrientationDb, false);
    },
    methods: {
        refreshCanvas() {
            const canvas = this.$refs.signCanvas;
            canvas.width = canvas.offsetWidth * this.ratio;
            canvas.height = canvas.offsetHeight * this.ratio;
            const canvasCxt = canvas.getContext('2d');
            canvasCxt.scale(this.ratio, this.ratio);
            if (this.signaturePad.clear) {
                this.signaturePad.clear();
            } else {
                this.signaturePad = new SignaturePad(canvas);
                this.signaturePad.penColor = 'black';
                this.signaturePad.minWidth = 0.5;
                this.signaturePad.maxWidth = 2;
            }
        },
        userOrientation() {
            this.bodyClientHeight = document.body.clientHeight;

            let currentDirection;
            switch (window.orientation) {
                case -90:
                    currentDirection = 'horizontal';
                    break;
                case 90:
                    currentDirection = 'horizontal';
                    break;
                case 0:
                    currentDirection = 'vertical';
                    break;
                case 180:
                    currentDirection = 'vertical';
                    break;
            }

            // 如果横竖屏没改变，那么不需要后续处理
            if (this.direction === currentDirection) {
                this.isUserOrientationCalled = true;
                return;
            }

            this.direction = currentDirection;

            if (currentDirection === 'vertical') {
                this.containerSize = {
                    width: `${this.$refs.signPad.offsetHeight}px`,
                    height: `${this.$refs.signPad.offsetWidth}px`,
                };
                // 等 signContent (的大小)随新的 containerSize 渲染完毕
                this.$nextTick(() => {
                    this.signCanvasWrapperSize = {
                        height: `${this.$refs.signContent.offsetWidth}px`,
                        width: `${this.$refs.signContent.offsetHeight}px`,
                    };
                });
            } else {
                this.containerSize = {};
                this.signCanvasWrapperSize = {};
            }

            this.isUserOrientationCalled = true;
        },
        changePenColor(color) {
            switch (color) {
                case 'black':
                    this.signaturePad.penColor = '#273039';
                    break;
                case 'blue':
                    this.signaturePad.penColor = '#00a0e9';
                    break;
                case 'red':
                    this.signaturePad.penColor = '#f74555';
                    break;
                default:
            }
            this.penColor = color;
        },
        changeWidth(type) {
            switch (type) {
                case 'tiny':
                    this.signaturePad.minWidth = 1;
                    this.signaturePad.maxWidth = 1;
                    break;
                case 'medium':
                    this.signaturePad.minWidth = 2;
                    this.signaturePad.maxWidth = 1;
                    break;
                case 'large':
                    this.signaturePad.minWidth = 2.5;
                    this.signaturePad.maxWidth = 2.5;
                    break;
                default:
            }
            this.lineWidth = type;
        },
        writeStart(e) {
            this.signaturePad._handleTouchStart(e);
        },
        writeMove(e) {
            this.signaturePad._handleTouchMove(e);
        },
        writeEnd(e) {
            this.signaturePad._handleTouchEnd(e);
        },
        convertBase64UrlToBlobFinFile(base64, fileName) {
            // base64 -> blob
            function dataURLtoBlob(durl) {
                var arr = durl.split(','),
                    mime = arr[0].match(/:(.*?);/)[1],
                    bstr = atob(arr[1]),
                    n = bstr.length,
                    u8arr = new Uint8Array(n);
                while (n--) {
                    u8arr[n] = bstr.charCodeAt(n);
                }
                return new Blob([u8arr], { type: mime });
            }
            // blob -> file
            function blobToFile(theBlob, theFileName) {
                theBlob.lastModifiedDate = new Date();
                theBlob.name = theFileName;
                return theBlob;
            }

            // 调用
            const blob = dataURLtoBlob(base64);
            const file = blobToFile(blob, fileName);

            return file;
        },
        rotateImgDataURL(dataUrl) {
            const self = this;
            const promise = new Promise((resolve, reject) => {
                const img = new Image();
                const file = this.convertBase64UrlToBlobFinFile(dataUrl, '签名.png');
                const reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = e => {
                    img.src = e.target.result;
                };
                const canvas = document.createElement('canvas');
                const context = canvas.getContext('2d');
                img.onload = async () => {
                    let base64;
                    let originWidth = img.width;
                    let originHeight = img.height;

                    // 目标尺寸
                    let targetWidth = originWidth;
                    let targetHeight = originHeight;
                    canvas.width = targetWidth;
                    canvas.height = targetHeight;
                    // 清除画布
                    context.clearRect(0, 0, targetWidth, targetHeight);
                    // 图片压缩
                    context.drawImage(img, 0, 0, targetWidth, targetHeight);

                    let width = targetWidth;
                    let height = targetHeight;

                    switch (window.orientation) {
                        case 90: //需要顺时针90度旋转
                        case -90: //需要逆时针90度旋转
                            canvas.width = height;
                            canvas.height = width;
                            context.rotate((90 * Math.PI) / 180);
                            context.drawImage(img, 0, 0, width, -height);
                            break;
                    }

                    base64 = canvas.toDataURL('image/png', 0.6);
                    resolve(base64);
                };
            });
            return promise;
        },
        async save() {
            if (this.signaturePad.isEmpty()) {
                this.$message('签名不能为空！');
                return;
            }
            let loading = this.$loading('正在上传，请稍候');
            let base64Str = await this.rotateImgDataURL(this.signaturePad.toDataURL('image', 0.6));
            let seal;
            try {
                seal = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'UPLOAD_PERSONAL_SEAL',
                        data: {
                            userId: this.$_qyssdk_userId,
                            base64Str,
                            contractId: this.contractId,
                        },
                    }),
                });

                this.$emit('changeSignature', seal.result);
                if (this.isModify) {
                    this.$emit('modifyStamper', this.selectSealOptions.modifyStamperId, {
                        id: seal.result,
                        type: 'PERSONAL',
                    });
                } else {
                    this.$emit('addStamper', { id: seal.result, type: 'PERSONAL' });
                }
                this.close();
            } catch (e) {
                console.log(e);
            } finally {
                loading.close();
            }
        },
        clear() {
            this.signaturePad.clear();
        },
        close() {
            this.$emit('componentClose');
        },
    },
};
</script>

<style scoped lang="less">
.sign-pad {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;

    display: flex;
    flex-direction: column;
    align-items: stretch;

    width: auto;
    height: auto;
    margin: 0;
    border: 1px solid #e8e8e8;
    border-radius: 4px;

    font-size: 10px;
    background-color: #fff;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.27), 0 0 40px rgba(0, 0, 0, 0.08) inset;

    &.in-vertical-direction {
        transform: rotate(90deg) translateY(-100%);
        transform-origin: left top;

        .sign-canvas-wrapper {
            transform: rotate(-90deg) translateX(-100%);
            transform-origin: left top;
        }
    }
}

.sign-content {
    width: 100%;
    height: 100%;
    position: relative;
    flex-grow: 1;
    overflow: hidden;

    .sign-canvas-wrapper {
        height: 100%;
        width: 100%;
        .sign-canvas {
            width: 100%;
            height: 100%;
            border: none;
            box-shadow: 0 0 5px rgba(0, 0, 0, 0.02) inset;
        }
    }
}
.sign-footer {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    padding: 11px 25px;
    border-top: 1px dashed #e5e7ea;
    justify-content: space-between;
    .circle {
        display: inline-block;
        width: 22px;
        height: 22px;
        border-radius: 100%;
        line-height: 50px;
        &:not(:last-child) {
            margin-right: 15px;
        }
        position: relative;
        &.outer-circle:before {
            position: absolute;
            top: -4px;
            right: -4px;
            content: '';
            opacity: 0.2;
            width: 30px;
            height: 30px;
            border-radius: 100%;
            display: inline-block;
        }
        &.black {
            background: #273039;
            &.outer-circle:before {
                background: #273039;
            }
        }
        &.blue {
            background: #00a0e9;
            &.outer-circle:before {
                background: #00a0e9;
            }
        }
        &.red {
            background: #f74555;
            &.outer-circle:before {
                background: #f74555;
            }
        }
    }
    i {
        font-size: 30px;
        line-height: 51px;
        margin-left: 10px;
        color: #c0c0c0;
        &:first-child {
            margin-left: 10px;
        }
        &.blue {
            color: #00a0e9;
        }
    }
    button {
        padding: 8px 15px;
        font-size: 16px;
    }
    .clear {
        background-color: #fff;
        border: 1px solid #00a0e9;
        color: #00a0e9;
        margin: 5px;
    }
    .save {
        background-color: #00a0e9;
        border: 1px solid #00a0e9;
        margin-left: 40px;
        color: #fff;
        margin: 5px;
    }
}
.sign-title {
    flex-shrink: 0;

    display: flex;
    align-items: center;
    padding: 7px 20px;
    text-align: center;
    text-align: center;
    font-size: 16px;
    background-color: #273039;
    color: #fff;
    justify-content: space-between;
    i {
        display: inline-block;
        font-size: 1.5em;
        float: right;
        margin-right: 10px;
        line-height: 2;
        color: #fff;
    }
}
</style>
