<template>
    <div class="face-fail">
        <div class="ios-compat-content">
            <div class="result-top">
                <q-icon class="exit-auth" icon="export" @click="showExitAuthModal"></q-icon>
                <img class="res-img" src="../assets/img/default-result-fail.png" />
                <span class="h2-title title">认证失败</span>
                <span v-if="authDetail.$get('logs', 0, 'reason')" class="tip-large">{{
                    authDetail.$get('logs', 0, 'reason')
                }}</span>
            </div>

            <div class="result-bottom">
                <span class="main-word title-color title">核对身份信息</span>
                <div class="info-row">
                    <span class="main-word regular-color">姓&nbsp;&nbsp;&nbsp;&nbsp;名</span
                    ><span class="main-word">{{ info.username }}</span>
                </div>
                <div class="info-row">
                    <span class="main-word regular-color">证件类型</span><span class="main-word">{{ paperType }}</span>
                </div>
                <div class="info-row">
                    <span class="main-word regular-color">证件号</span><span class="main-word">{{ info.cardNo }}</span>
                </div>
            </div>
            <div class="modify-info main-word body-color-dark" type="primary" @click="repeatAuth">
                修改信息重新认证
            </div>
            <div class="change-other main-word" type="primary" v-if="isShowOtherWays" @click="openAuthWaysModal">
                尝试其他认证方式
            </div>
        </div>
    </div>
</template>

<script>
import authWaysModal from '../auth-common/auth-ways.vue';

export default {
    name: 'FaceFail',
    props: {
        user: {
            type: Object,
            default: () => {
                return {};
            },
        },
        otherModes: {
            type: String,
            default: '',
        },
        channel: {
            type: String,
            default: '',
        },
        successPage: {
            type: String,
            default: '',
        },
        step: {
            type: String,
            default: '',
        },
    },
    data() {
        return {
            info: {
                username: '',
                paperType: '',
                cardNo: '',
                mobile: '',
            },
            authDetail: {},
            hasCardPic: false,
            hasHoldCardPic: false,
            hasManualed: false,
            otherAuthWays: [],
            faildLogs: [],
        };
    },
    computed: {
        isUseMail() {
            let result = this.$qUtils.validata({
                value: this.info.mobile,
                noValNotValid: false,
                rules: [{ rule: this.$qUtils.validator.isEmail }],
            });
            return !result.isInvalid;
        },
        paperType() {
            let result = '';
            let obj = {
                IDCARD: '身份证',
                PASSPORT: '护照',
                HKMP: '港澳通行证',
                MTPS: '台胞证',
            };
            return obj[this.info.paperType] || '身份证';
        },
        degradeToManual() {
            return this.otherAuthWays.includes('MANUAL');
        },
        baiduAiHasFailed() {
            return this.faildLogs.some(item => item.type === 'BAIDUAI');
        },
        isShowOtherWays() {
            let authWays = this.$qUtils.deepClone(this.otherAuthWays);

            if (this.$appointAuthWay === 'FACE' && !this.baiduAiHasFailed) {
                authWays.push('FACE');
            }

            let canManual = !this.hasManualed && this.degradeToManual && !this.hasHoldCardPic;
            if (canManual) {
                return true;
            }
            if (authWays.every(authWay => authWay === 'FACE')) {
                return false;
            }
            if (!canManual && authWays.every(item => item === 'MANUAL')) {
                return false;
            }
            return !!authWays.length;
        },
    },
    watch: {
        'pageKey.$pageRandom': {
            handler(val) {
                this.init();
            },
            deep: true,
            immedidate: true,
        },
    },
    created() {
        this.init();
    },
    methods: {
        async init() {
            await this.getAuthInfo();
            await this.judgeCardPictureType();
            await this.judgeManualed();
            await this.$getAppointAuthWay();
            this.otherAuthWays = await this.$getAuthWays();

            const { result } = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_LOG',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            this.faildLogs = result;
        },
        openAuthWaysModal() {
            const instance = new Vue({
                components: { authWaysModal },
                render: h =>
                    h(authWaysModal, {
                        props: {
                            root: this,
                            user: this.user,
                            channel: this.channel,
                            otherModes: this.otherModes,
                            step: this.step,
                            useOtherFrom: 'FACE',
                        },
                    }),
            }).$mount();
            document.body.appendChild(instance.$el);
        },
        async judgeCardPictureType() {
            try {
                const res = await this.postForm('', {
                    requestData: JSON.stringify({
                        type: 'GET_AUTH_IMAGE_EXIST',
                        data: {
                            userId: this.$_qyssdk_userId,
                            name: this.user.username,
                            cardNo: this.user.cardNo,
                        },
                    }),
                });
                this.hasCardPic = res.faceImage;
                this.hasHoldCardPic = res.handfrontImage;
            } catch (error) {
                console.log(error);
            }
        },
        async repeatAuth() {
            this.$emit('reVerify');
            const { result } = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_CURRENT_STEP',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            if (result === 'END') {
                this.dealStep('INVALID');
                return;
            }

            this.dealStep('INFO');
        },
        async goManual() {
            if (this.authDetail.mode === 'DEFAULT') {
                this.dealStep('MANUALINFO');
                return;
            }
            const { result } = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_CURRENT_STEP',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            if (result === 'END') {
                this.dealStep('INVALID');
                return;
            }

            this.dealStep('manualAuth');
        },
        async getAuthInfo() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_DETAIL',
                    data: {
                        userId: this.$_qyssdk_userId,
                    },
                }),
            });
            const { result } = this.decryptoData(res.result);
            this.info = result.authInfo;
            this.authDetail = result;
            this.user = Object.assign(this.user, result.authInfo);
        },
        async judgeManualed() {
            const res = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_PASS_BEFORE',
                    data: {
                        userId: this.$_qyssdk_userId,
                        name: this.user.username,
                        cardNo: this.user.cardNo,
                    },
                }),
            });
            this.hasManualed = res.result;
        },
        async showExitAuthModal() {
            try {
                await this.$mt.MessageBox.confirm('', {
                    message: '确定退出认证流程？',
                    confirmButtonClass: 'main-word link-color',
                });
                this.$giveUpAuth();
            } catch (e) {
                console.log(e);
            }
        },
    },
};
</script>
<style lang="less" scoped>
.face-fail {
    width: 100%;
    height: 100%;
    .ios-compat-content {
        display: flex;
        flex-direction: column;
        height: 100%;
        background: @body-background;
        .result-top {
            flex-shrink: 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 19px 20px 28px;
            background: white;
            margin-bottom: 10px;
            position: relative;
            .exit-auth {
                position: absolute;
                top: 16px;
                right: 16px;
                font-size: 18px;
                color: #c1c6cd;
            }
            .res-img {
                width: 138px;
            }
            .title {
                margin-top: -10px;
            }
        }
        .result-bottom {
            flex-shrink: 0;
            display: flex;
            flex-direction: column;
            padding: 16px 20px;
            background: white;
            .title {
                flex-shrink: 0;
                margin-bottom: 16px;
            }
            .info-row {
                flex-shrink: 0;
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-bottom: 12px;
                :first-child {
                    min-width: 80px;
                }
                > :last-child {
                    text-align: right;
                }
            }
        }

        .modify-info {
            padding: 10px 0px;
            background: @primary-color;
            border: 1px solid #2489f2;
            border-radius: 4px;
            text-align: center;
            margin: 16px;
        }
        .change-other {
            margin: 0 16px 16px 16px;
            padding: 10px 0px;
            border-radius: 4px;
            text-align: center;
            border: 1px solid #2489f2;
            color: #2489f2;
            margin-right: 11px;
            background: #fff;
        }
    }
}
</style>
