<template>
    <div class="three-elements">
        <auth-subheader :account="user.mobile"></auth-subheader>

        <div class="content">
            <div class="form-item" @click="activeForm('mobile')">
                <div class="input-wrap area-code-con" :class="`ui-input-${mobile.status}`">
                    <area-code
                        className="area-code-selecter"
                        v-show="isCurrentFormActive || !!mobile.value"
                        :code="code"
                        @toggleSelectCode="toggleSelectCode('mobile')"
                        @getCode="getAreaCode"
                    />
                    <ui-textbox
                        floating-label
                        id="mobile"
                        ref="mobile"
                        label="手机号"
                        v-model="mobile.value"
                        @blur="contactBlur('mobile')"
                        iconPosition="right"
                    >
                        <q-icon
                            slot="icon"
                            class="clear-icon"
                            icon="close-circle-fill"
                            v-if="!!mobile.value && $refs.$get('mobile', 'isActive')"
                            @touchstart="mobile.value = ''"
                        ></q-icon>
                    </ui-textbox>
                </div>
                <span v-if="mobile.status" class="ui-textbox-tips" :class="`${mobile.status}-tips`">{{
                    mobile.message
                }}</span>
            </div>

            <div class="code-wrap">
                <div class="form-item form-item-code">
                    <div class="input-wrap" :class="`ui-input-${pin.status}`">
                        <ui-textbox
                            floating-label
                            id="pin"
                            ref="pin"
                            label="验证码"
                            :enforceMaxlength="true"
                            :maxlength="6"
                            v-model="pin.value"
                        >
                        </ui-textbox>
                    </div>
                    <span v-if="pin.status" class="ui-textbox-tips" :class="`${pin.status}-tips`">{{
                        pin.message
                    }}</span>
                </div>
                <div
                    @click="countStatus && mobile.value ? sendCode() : ''"
                    class="get-code-btn main-word link-color"
                    :class="{ disabled: !countStatus || !mobile.value }"
                >
                    {{ isFirst ? '获取验证码' : countDown }}
                </div>
            </div>

            <auth-button @click="submit" class="submit-btn" type="primary" :disabled="isDisabled" :debounce="300"
                >提交</auth-button
            >
        </div>
        <div v-if="showOtherAuthWays" class="tips">
            <span class="tip-large">不记得手机号？</span>
            <span class="tip-large link-color" @click="openAuthWaysModal">尝试其他认证方式</span>
        </div>
    </div>
</template>

<script>
import authWaysModal from '../auth-common/auth-ways.vue';

export default {
    name: 'ThreeElements',
    props: {
        user: {
            type: Object,
            default: () => {
                return {};
            },
        },
        otherModes: {
            type: String,
            default: '',
        },
        channel: {
            type: String,
            default: '',
        },
        step: {
            type: String,
            default: '',
        },
        accountPrecheck: {
            type: Boolean,
            default: false,
        },
    },
    data() {
        return {
            interval: null,
            isFirst: true, //是否为初始状态
            countDown: '重新获取',
            countStatus: true, //计时是否完毕
            countNumber: 60, //短信计时
            mobile: {
                value: '',
                status: '',
                message: '',
            },
            pin: {
                value: '',
                status: '',
                message: '',
            },
            code: { label: '大陆', areaAbbr: 'ZH', code: '+86', value: '' },
            activeForm: null,
            isCurrentFormActive: false,
            authWays: [],
        };
    },
    computed: {
        showOtherAuthWays() {
            if (this.accountPrecheck) {
                return false;
            }
            // 除了当前正在操作的认证，还有其他认证则显示其他认证入口
            let authWays = this.$qUtils.deepClone(this.authWays);
            let index = authWays.findIndex(way => way === 'IVS');
            if (index > -1) {
                authWays.splice(index, 1);
            }
            return authWays.length !== 0;
        },
        isDisabled() {
            return (
                !this.mobile.value || this.mobile.status === 'error' || !this.pin.value || this.pin.status === 'error'
            );
        },
    },
    async created() {
        // 特殊浏览器由于不支持跳转支付宝，隐藏支付宝入口和弹窗
        this.activeForm = this.$qUtils.debounce(id => {
            this.isCurrentFormActive = this.$refs[id] && this.$refs[id].isActive;
        }, 200);
        this.authWays = await this.$getAuthWays();
    },
    mounted() {
        document.addEventListener('touchstart', this.bindEvent, false);
    },
    beforeDestroy() {
        document.removeEventListener('touchstart', this.bindEvent, false);
        this.interval && clearInterval(this.interval);
    },
    methods: {
        bindEvent() {
            this.activeForm('mobile');
        },
        openAuthWaysModal() {
            const instance = new Vue({
                components: { authWaysModal },
                render: h =>
                    h(authWaysModal, {
                        props: {
                            root: this,
                            user: this.user,
                            otherModes: this.otherModes,
                            channel: this.channel,
                            step: this.step,
                            useOtherFrom: 'IVS',
                        },
                    }),
            }).$mount();
            document.body.appendChild(instance.$el);
        },
        async validatePin() {
            const { result } = await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'GET_AUTH_PINVALIDATE',
                    data: {
                        userId: this.$_qyssdk_userId,
                        mobile: this.user.mobile || this.getContactWithCode(this.code.value, this.mobile.value),
                        pin: this.pin.value,
                    },
                }),
            });
            return result;
        },
        async submit() {
            let { username, cardNo, mobile } = this.user;
            let params = {
                username,
                cardNo,
                mobile: mobile || this.getContactWithCode(this.code.value, this.mobile.value),
                cellphone: this.getContactWithCode(this.code.value, this.mobile.value),
                pin: this.pin.value,
            };
            let loading = this.$loading('正在提交');
            try {
                if (this.accountPrecheck) {
                    let data = await this.validatePin();
                    await this.saveMobile();
                    if (data.result) {
                        this.dealStep('INFO');
                    } else {
                        this.$message('手机号验证失败');
                    }
                } else {
                    await this.postForm('', {
                        requestData: JSON.stringify({
                            type: 'UPLOAD_IVS_MATERIAL',
                            data: {
                                userId: this.$_qyssdk_userId,
                                material: params,
                                pin: params.pin,
                            },
                        }),
                    });
                    await this.verify();
                }
            } catch (error) {
                console.log(error);
            } finally {
                loading.close();
            }
        },
        async saveMobile() {
            await this.postForm('', {
                requestData: JSON.stringify({
                    type: 'SAVE_AUTH_PHONE',
                    data: {
                        userId: this.$_qyssdk_userId,
                        mobile: this.user.mobile || this.getContactWithCode(this.code.value, this.mobile.value),
                    },
                }),
            });
        },
        async verify() {
            try {
                const res = await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'PHONE_AUTH_VERIFY',
                            data: {
                                userId: this.$_qyssdk_userId,
                            },
                        }),
                    },
                    {
                        headers: {
                            noMes: 'true',
                        },
                    }
                );
                if (res.code === 0 && res.nextStep === 'END') {
                    this.dealStep('SUCCESS');
                } else {
                    this.dealStep(res.nextStep);
                }
            } catch (error) {
                console.log(error);
            }
        },
        // 获取验证码
        async sendCode() {
            if (!this.mobile.value) {
                this.$message('请输入手机号码');
                return false;
            }
            this.interval && clearInterval(this.interval);
            try {
                const res = await this.postForm(
                    '',
                    {
                        requestData: JSON.stringify({
                            type: 'GET_AUTH_PIN',
                            data: {
                                userId: this.$_qyssdk_userId,
                                mobile: this.getContactWithCode(this.code.value, this.mobile.value),
                            },
                        }),
                    },
                    {
                        headers: {
                            noMes: 'true',
                        },
                    }
                );
                if (res.code === 0) {
                    this.countNumber = 60;
                } else {
                    this.$message(res.message);
                    return false;
                }

                this.interval = setInterval(() => {
                    if (this.countNumber === 0) {
                        this.countStatus = true;
                        this.countDown = '重新获取';
                        clearInterval(this.interval);
                    } else {
                        if (this.isFirst) {
                            this.isFirst = false;
                        }
                        this.countStatus = false;
                        this.countDown = `${this.countNumber}s`;
                        this.countNumber--;
                    }
                }, 1000);
            } catch (e) {
                this.countStatus = false;
                this.countNumber = 0;
                console.log(e);
            }
        },
        contactBlur(id) {
            if (id === 'mobile') {
                this.activeForm('mobile');
                this.inputBlur(id);
            }
        },
        trimData(id) {
            let val = this.mobile.value;
            if (val) {
                val = val.replace(/\s/g, '');
                this.mobile.value = val;
            }
            return val;
        },
        inputBlur(id) {
            let target = this.mobile.value;
            let result = null;

            if (id === 'mobile') {
                target = this.trimData(id);
                let regKey = `is${this.code.areaAbbr}Phone`;
                result = this.$qUtils.validata({
                    value: target,
                    rules: [{ rule: this.$qUtils.validator[regKey], level: 'error', message: '手机号码格式不正确' }],
                });
            }

            if (result.isInvalid) {
                this.mobile.status = result.status;
                this.mobile.message = result.message;
            } else {
                this.mobile.status = '';
                this.mobile.message = '';
            }
        },
        // 获取手机号码区号
        getAreaCode(codeItem) {
            this.code = codeItem;
        },
        toggleSelectCode(id) {
            // 解决ios不可聚焦的问题
            this.$refs[id].focus();
        },
    },
};
</script>
<style lang="less" scoped>
.three-elements {
    width: 100%;
    height: 100%;
    .content {
        width: 100%;
        padding: 0 20px;
        .clear-icon {
            font-size: 14px;
            color: #e5e7ea;
            bottom: 8px;
        }
        .code-wrap {
            position: relative;
            .form-item-code {
                width: 100%;
                height: 68px;
                .certify-code-con {
                    .ui-textbox__input {
                        padding-right: 100px !important;
                    }
                }
                .tips-tel {
                    .icon-succ {
                        font-size: 14px;
                        color: @success-color;
                        margin-right: 5px;
                    }
                }
            }
            .get-code-btn {
                position: absolute;
                top: 24px;
                right: 0;
                padding-left: 12px;
                line-height: 16px;
                border-left: 1px solid #e5e7ea;
                min-width: 83px;
                text-align: center;
                &.disabled {
                    color: #c1c6cd;
                }
            }
        }

        .submit-btn {
            width: 100%;
            margin-top: 40px;
        }
    }
    .tips {
        width: 100%;
        position: fixed;
        bottom: 16px;
        text-align: center;
    }
}
</style>
